const state = {
    count: 5,
    // 登录页面当前活跃的tab
    activeTab: 0,
}

// 定义 getters
var getters = {
    cbd(state) {
        return state.count +'sdsd'
    }
}

const actions = {
    //修改登录页当前显示的tab
    changeActiveTabFun(context, index) {
        context.commit('changeActiveTab', index);
    },
    increment({ commit, state }) {
        // 此处提交的事件与下方 mutations 中的 types.INCREMENT 对应
        //与原来 commit('increment') 的原理相同，只是把类型名换成了常量
        //可以处理异步事务
        setTimeout(()=>{
            commit('add');
        },2000)
    },
    decrement({ commit, state }) {
        if (state.count > 10) {
            commit('minus')
        }
    }
}

const mutations = {
    // 修改当前显示的tab
    changeActiveTab(state, index) {
        state.activeTab = index;
    },
    // 此处的事件为上方 actions 中的 commit(types.INCREMENT)
    add(state) {

        state.count++
    },
    // 此处的事件为上方 actions 中的 commit(types.DECREMENT)
    minus(state) {
        state.count--
    }
}
// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
