
const state = {
    typeFlag:0,
    activeFlag:0,

}

const mutations = {
    getypeFlag(state,date) {
      state.typeFlag = date
    },
    getActiveFlag(state,data){
      state.activeFlag = data
    }
    
};

const actions = {
    changeTypeFlag( context,date){
      context.commit('getypeFlag',date)
    },
    changeActiveFlag(context,date){
      context.commit('getActiveFlag',date)
    }
   
};

const getters = {
    getTypeFlag(state,date){
      return state.typeFlag
    },
    getActiveFlag(state,date){
      return state.activeFlag
    }
   
}

// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
