<template>
  <div class="phoneDoor">
    <!-- 开门按钮 -->
    <img
      width="60"
      class="phoneDoorStyle"
      :src="isOpening?loadingImg:phoneImg"
      @click="doorPress()"
    />
    <!-- 远程开门弹窗 -->
    <div
      v-if="isPopShow"
      :class="{themeL:bindThemeL}"
      class="phonedoor-popup-container"
      style="background: rgba(0, 0, 0,0.5);"
    >
      <div class="door-popup-body">
        <img :src="closeImg" class="popcloseBtn" @click="closePopup()" />

        <van-image fit="fill" :src="backImg" />
        <div id="qrArea" v-if="isLLQrcode">
          <div class="failText">请对准门禁摄像头</div>
          <div class="QRCode">
            <canvas id="phoneDoorQrcode" class="phoneDoorQrcode"></canvas>
          </div>
          <!-- <div class="QRCodeRefresh hide" style="position: absolute;bottom: 0;width: 100%;">
          <img
            style="position:relative;top: 2px"
            src="img/phonedoor/pic_tips_refresh.png"
            onclick="refreshQRcode()"
          />
          </div>-->

          <div class="lineBoxStyle">
            <div class="lineStyle"></div>
          </div>
        </div>
        <div class="opemDoorBox" v-show="isRemote">
          <div class="failMsg">远程开门</div>
          <div class="share" id="shareScrollbar">
            <div
              class="leftImg"
              v-for="(x,index) in doorList"
              :key="index"
              @click="doorRemoteOpen(x)"
            >
              <button class="doorBtn" v-if="x.openType>3">{{x.deviceName}}</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getDoorListService,
  openLinglingRemoteDoorService,
  addRecordService,
  addPointService,
  getLeeLenDoorInfoService,
  openLeeLenRemoteDoorService
} from "../../services/phoneDoorService";
import config from "@/common/config";
import QRCode from "qrcode"; //引入生成二维码插件
import { store, storage } from "../../utils/storage";
import { mapState } from "vuex";
export default {
  name: "phoneDoor",
  data() {
    return {
      doorList: [], //门禁列表
      isLLQrcode: false, //是否支持令令二维码开门
      isLLBluetooth: false, //是否支持令令蓝牙开门
      isLeeLenBluetooth: false, //是否支持立林蓝牙开门
      isRemote: false, //是否支持远程开门
      llBluetoothKeys: "", //令令支持蓝牙设备key
      llQrcodeKeys: "", //令令支持二维码设备key
      linglingDoorInfos: [], //令令门禁信息
      LLingID: "", //令令设备ID
      qrcode: "", //令令二维码
      qrCanvas: null,
      isPopShow: false, //远程开门弹窗是否显示
      openStart: null, //手机开门开始时间，用于计算开门周期
      isOpening: false, //判断是否在处理手机开门
      popupTimer: null, //弹出框定时器
      btnTimer: null,
      isCanvas: true,
      closeImg: require("../../assets/images/home/property/icon_phoneDoor_popup_close.png"),
      phoneImg: require("../../assets/images/home/property/icon_phoneDoor_open.png"),
      backImg: require("../../assets/images/home/property/icon_phoneDoor_popup_bg.png"),
      loadingImg: require("../../assets/images/home/property/icon_phoneDoor_loading.gif"),
      userInfo: {},
      /*大字模式*/
      bindThemeL: this.$curTheme == 0 ? false : true
    };
  },
  computed: {
    //监控刷新手机开门state是否变化
    phoneDoorRefresh() {
      return this.$store.state.home.phoneDoorRefresh;
    }
  },
  watch: {
    //机开门state变化回调
    phoneDoorRefresh(val) {
      console.log("valvalvalvalvalvalvalvalval" + val);
      if (val) {
        this.$store.dispatch("setPhoneDoorRefreshFlag", false);
        this.doorList = [];
        this.isLLQrcode = false; //是否支持令令蓝牙开门
        this.isLLBluetooth = false; //是否支持令令蓝牙开门
        this.isLeeLenBluetooth = false; //是否支持立林蓝牙开门
        this.isRemote = false; //是否支持远程开门
        this.llBluetoothKeys = ""; //令令支持蓝牙设备key
        this.llQrcodeKeys = ""; //令令支持二维码设备key
        this.linglingDoorInfos = []; //令令门禁信息
        this.LLingID = ""; //令令设备ID
        this.qrcode = ""; //令令二维码
        this.isPopShow = false; //远程开门弹窗是否显示
        this.openStart = null; //手机开门开始时间，用于计算开门周期
        this.isOpening = false; //判断是否在处理手机开门
        if (this.popupTimer != null) clearTimeout(this.popupTimer);
        if (this.btnTimer != null) clearTimeout(this.btnTimer);
        this.getDoorList();
      }
    }
  },
  mounted() {
    this.getDoorList();
  },
  methods: {
    /* *************************************获取开门信息************************************* */
    /* 获取门禁列表 */
    getDoorList() {
      console.log("getDoorList");
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        buildCode: this.$userInfo.BUILD_CODE,
        roomCode: this.$userInfo.ROOM_CODE,
        ownerCode: this.$userInfo.OWNER_CODE,
        unitNo: this.$userInfo.UNIT_NO
      };
      getDoorListService(reqData)
        .then(result => {
          console.log("findOpenType---result");
          console.log(JSON.stringify(result));
          //请求成功
          if (result.resultCode == 0) {
            this.doorList = result.data;
            this.findOpenType();
            this.$store.state.home.hasOpenDoor = true;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 判断小区支持的开门方式 */
    findOpenType() {
      console.log("findOpenType");
      //循环门禁列表判断支持哪些开门方式，用于用户点击开门时判断使用哪种方式
      for (let i in this.doorList) {
        let openType = this.doorList[i].openType;
        //奇数，1、3、5、7,支持蓝牙开门
        if ((openType & 1) == 1) {
          console.log("蓝牙开门支持");
          if (this.doorList[i].deviceMfr == 2) {
            //deviceMfr=2 令令
            this.isLLBluetooth = true;
            //保存设备key
            this.llBluetoothKeys += this.doorList[i].sdkKey;
            this.llBluetoothKeys += ",";
          } else if (this.doorList[i].deviceMfr == 1) {
            //deviceMfr=1 立林
            this.isLeeLenBluetooth = true;
          }
        }

        //偶数，支持二维码开门
        if ((2 & openType) == 2) {
          if (this.doorList[i].deviceMfr == 2) {
            console.log("二维码开门支持");
            this.isLLQrcode = true;
            this.LLingID = this.doorList[i].linglingId;
            //保存设备key
            this.llQrcodeKeys += this.doorList[i].sdkKey + ",";
          }
        }
        //openType=4，支持远程开门
        if ((4 & openType) == 4) {
          console.log("远程开门支持");
          this.isRemote = true;
        }
      }

      //去掉字符串最后的逗号
      this.llBluetoothKeys = this.llBluetoothKeys.substring(
        0,
        this.llBluetoothKeys.length - 1
      );
      this.llQrcodeKeys = this.llQrcodeKeys.substring(
        0,
        this.llQrcodeKeys.length - 1
      );
    },
    /* *************************************开门处理************************************* */
    /* 开门按钮事件 */
    doorPress() {
      //无门禁权限
      if (this.doorList == null || this.doorList.length == 0) {
        this.$toast("该小区无门禁权限!");
        this.closePopup();
        return;
      }
      //设置开门状态
      if(this.isOpening){
        this.$toast("开门中，请稍后!");
        return;
      }else{
        this.isOpening = true;
      }
      // 首先获取定位，使用定位+远程开门方式
      this.getCurrentLocatioon();
      console.log("doorPress");
      this.bindThemeL = this.$curTheme == 0 ? false : true;
      //记录点击开门时间
      this.openStart = new Date();
      //开门按钮动图
      this.btnTimer = setTimeout(() => {
        this.isOpening = false;
      }, 8000);
      //设置两秒钟后显示远程开门弹窗
      setTimeout(() => {
        //如果有二维码或远程开门
        if (this.isLLQrcode || this.isRemote) {
          this.showRemotePopup();
        }
      }, 2000);
      //支持令令蓝牙
      if (this.isLLBluetooth) {
        this.openLLBlueTooth();
      }
      //支持立林蓝牙
      if (this.isLeeLenBluetooth) {
        this.openLeeLenBlueTooth();
      }
    },
    /* *************************************定位相关************************************* */
    /*
     * 获取用户定位，设置经纬度
     */
    getCurrentLocatioon() {
      //高德定位
      AMap.plugin("AMap.Geolocation", () => {
        //异步加载插件
        let geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 10000, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: false, //显示定位按钮，默认：true
          buttonPosition: "LB", //定位按钮停靠位置，默认：'LB'，左下角
          buttonOffset: new AMap.Pixel(10, 20), //定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
          showMarker: false, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: false, //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
        });
        geolocation.getCurrentPosition();
        AMap.event.addListener(geolocation, "complete", res => {
          console.log("complete" + JSON.stringify(res));
          //记录经纬度信息
          this.longitude = res.position.lng;
          this.latitude = res.position.lat;

          // 遍历所有门禁，根据距离排序
          // 判断10米范围内门禁设备，只要在范围内的设备都调用远程开门
          this.doorList.forEach(door => {
            //判断门禁是否配置经纬度
            if (door.deviceLatitude && door.deviceLongitude) {
              //计算距离
              var p1 = [this.longitude, this.latitude];
              var p2 = [door.deviceLongitude, door.deviceLatitude];
              let far = AMap.GeometryUtil.distance(p1, p2);
              console.log("far = "+far);
              //10米范围内，自动远程开门
              if (far < 10) {
                if (door.deviceMfr == 2) {
                  this.openLinglingRemoteDoor(door,true);
                } else {
                  this.openLeeLenRemoteDoor(door,true);
                }
              }
            }
          });
        }); //返回定位信息
        AMap.event.addListener(geolocation, "error", err => {
          console.log("error" + err);
        }); //返回定位出错信息
      });
    },
    /* *************************************蓝牙开门************************************* */
    /*
     * 令令蓝牙开门
     */
    openLLBlueTooth() {
      console.log("======this.llBluetoothKeys=====" + this.llBluetoothKeys);
      var lingling = cordova.plugins.linglingPlugin;
      lingling.btOpenDoorMethod(
        this.llBluetoothKeys,
        resultStatus => {
          console.log("======令令蓝牙成功");
          this.closePopup();

          this.$toast({
            message: "开门成功",
            onClose: () => {
              //  获取数据令令门数据 并记录用户开门数据
              for (var i = 0; i < this.doorList.length; i++) {
                console.log("进入for循环 resultStatus=" + resultStatus);
                // 循环找到设备
                if (this.doorList[i].deviceCode == resultStatus) {
                  console.log(
                    "匹配到 deviceCode=" + this.doorList[i].deviceCode
                  );
                  //  记录开门动作
                  this.addRecord(
                    this.doorList[i].deviceCode,
                    this.doorList[i].deviceName,
                    1,
                    2,
                    this.doorList[i].buildCode == "" ? 0 : 1,
                    "",
                    1,
                    "开门成功"
                  );

                  this.addPoint();
                  return;
                }
              }
            }
          });
        },
        message => {
          console.log("开门失败" + message);
          // 记录失败开门动作
          this.addRecord("无设备信息", "无设备信息", 1, 2, 3, "", 0, message);
          if (this.isLeeLenBluetooth) {
            this.openLeeLenBlueTooth();
            return;
          }
          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("开门失败，请重试");
            return;
          }
        }
      );
    },
    /* 立林蓝牙开门 */
    openLeeLenBlueTooth() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        buildCode: this.$userInfo.BUILD_CODE,
        roomCode: this.$userInfo.ROOM_CODE,
        ownerCode: this.$userInfo.OWNER_CODE,
        unitNo: this.$userInfo.UNIT_NO
      };
      getLeeLenDoorInfoService(reqData)
        .then(res => {
          if (res.resultCode == 0) {
            var llData = JSON.parse(JSON.stringify(res.data));
            var obj = {
              cardNo: llData.cardNo,
              useType: llData.userType,
              deviceList: llData.leeLenBluetoothDoorInfoVOS,
              isLLBluetooth: this.isLeeLenBluetooth
            };
            if (/android/.test(navigator.userAgent.toLowerCase())) {
              this.leelenAndroid(obj);
            } else {
              this.leelenIos(obj);
            }
          }else{
            this.addRecord("无设备信息", "无设备信息", 1, 1, 3, "", 0, res.msg);
          }
        })
        .catch(err => {
          console.log(JSON.stringify(err));
          this.$toast("网络异常，请稍后重试！");
        });
    },
    leelenIos(obj) {
      /* 发送蓝牙开门通知 */
      window.broadcaster.fireNativeEvent(
        "openLeelenDoor",
        { openCode: obj },
        function() {
          console.log("立林蓝牙开门发起成功");
        }
      );

      var leelenBlueFlag = 0;
      // 蓝牙开门结果返回监听;
      window.broadcaster.addEventListener("LeeLenResultCode", data2 => {
        if (data2.code == "0" && leelenBlueFlag == 0) {
          this.$toast({
            message: "开门成功",
            onClose: () => {
              let buildCode = "";
              let door = null;
              for (var i = 0; i < this.doorList.length; i++) {
                if (this.doorList[i].deviceNo == data2.DEVICE_ID) {
                  buildCode = this.doorList[i].buildCode;
                  door = this.doorList[i];
                }
              }
              //  记录开门动作
              this.addRecord(
                door.deviceCode,
                door.deviceName,
                1,
                1,
                buildCode == "" ? 0 : 1,
                "",
                1,
                "开门成功"
              );

              this.addPoint();
            }
          });

          this.closePopup();
        } else if (leelenBlueFlag == 0) {
          console.log("开门失败");
          //  记录失败开门动作
          var errMsg = this.getLeelenErrorMsg(data2.code);
          this.addRecord("无设备信息", "无设备信息", 1, 1, 3, "", 0, errMsg);

          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("开门失败，请重试");
            return;
          }
        }
        leelenBlueFlag = 1;
      });
    },
    leelenAndroid(obj) {
      var leelen = cordova.plugins.LeeLenPlugin;
      leelen.access(
        obj,
        resultStatus => {
          console.log("开门成功，line 335!!!!!_+_+_+_+_ " + resultStatus);
          this.$toast({
            message: "开门成功",
            onClose: () => {
              //判断门类型
              let buildCode = "";
              let door = null;
              for (var i = 0; i < this.doorList.length; i++) {
                if (this.doorList[i].deviceNo == resultStatus.deviceId) {
                  buildCode = this.doorList[i].buildCode;
                  door = this.doorList[i];
                }
              }
              this.addRecord(
                door.deviceCode,
                door.deviceName,
                1,
                1,
                buildCode == "" ? 0 : 1,
                "",
                1,
                "开门成功"
              );

              this.addPoint();
            }
          });

          this.closePopup();
        },
        message => {
          console.log("开门失败===countFlag2=== " + message);
          //  记录失败开门动作
          var errMsg = this.getLeelenErrorMsg(message);
          this.addRecord("无设备信息", "无设备信息", 1, 1, 3, "", 0, errMsg);
          if (!this.isRemote && !this.isLLQrcode) {
            this.closePopup();
            this.$toast("开门失败，请重试");
            return;
          }
        }
      );
    },
    getLeelenErrorMsg(message) {
      var errMsg = "";
      switch (message) {
        case "10001":
          errMsg = "蓝牙关闭(错误码:10001)";
          break;
        case "10002":
          errMsg = "连接超时(错误码:10002)";
          break;
        case "10003":
          errMsg = "未搜索到设备(错误码:10003)";
          break;
        case "10004":
          errMsg = "蓝牙连接失败(错误码:10004)";
          break;
        case "10005":
          errMsg = "蓝牙码错误(错误码:10005)";
          break;
        case "10006":
          errMsg = "设备没有开门权限(错误码:10006)";
          break;
        case "10007":
          errMsg = "设备版本不支持(错误码:10007)";
          break;
      }
      return errMsg;
    },
    /* *************************************远程开门************************************* */
    showRemotePopup() {
      this.isPopShow = true;
      //获取二维码
      this.$nextTick(() => {
        this.getQrCanvas();
      });

      this.popupTimer = setTimeout(() => {
        this.addPoint();
        this.closePopup();
      }, 15000);
    },
    getQrCanvas() {
      if (this.llQrcodeKeys != "" && this.llQrcodeKeys != null) {
        this.qrCanvas = document.getElementById("phoneDoorQrcode");
        console.log("canvas=" + this.qrCanvas);
        var lingling = cordova.plugins.linglingPlugin;
        lingling.getQrCode(
          this.LLingID,
          this.llQrcodeKeys,
          resultStatus => {
            console.log("二维码获取成功" + resultStatus);
            // 将获取到的数据（val）画到msg（canvas）上
            console.log("sdkKeys=" + this.llQrcodeKeys);
            QRCode.toCanvas(this.qrCanvas, resultStatus);
          },
          function(message) {
            console.log("二维码获取失败");
          }
        );
      }
    },
    closePopup() {
      clearTimeout(this.popupTimer);
      clearTimeout(this.btnTimer);
      this.isOpening = false;
      this.isPopShow = false;
    },
    /* 远程开门点击 */
    doorRemoteOpen(obj) {
      if (obj.deviceMfr == 2) {
        this.openLinglingRemoteDoor(obj);
      } else {
        this.openLeeLenRemoteDoor(obj);
      }
    },
    /* 令令远程开门 */
    openLinglingRemoteDoor(door,isGPS=false) {
      let reqData = {
        sdkKey: door.sdkKey
      };
      openLinglingRemoteDoorService(reqData)
        .then(result => {
          console.log(JSON.stringify(result));
          if (result.resultCode == 0) {
            //令令远程开门成功
            this.closePopup();
            this.$toast({
              message: "开门成功",
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  2,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  1,
                  isGPS?"开门成功(GPS)":"开门成功"
                );
                this.addPoint();
              }
            });
          } else {
            //令令远程开门失败
            this.closePopup();
            this.$toast({
              message: result.msg,
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  2,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    /* 立林远程开门 */
    openLeeLenRemoteDoor(door,isGPS=false) {
      let reqData = {
        areaNo: door.areaNo,
        deviceNo: door.deviceNo,
        deviceCode: door.deviceCode,
        readHeadNo: door.readHeadNo
      };
      openLeeLenRemoteDoorService(reqData)
        .then(result => {
          if (result.resultCode == 0) {
            //立林远程开门成功
            this.closePopup();
            console.log("远程开门成功");
            this.$toast({
              message: "开门成功",
              onClose: () => {
                console.log("toast close");
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  1,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  1,
                  isGPS?"开门成功(GPS)":"开门成功"
                );

                this.addPoint();
              }
            });
          } else {
            //立林远程开门失败
            this.closePopup();
            this.$toast({
              message: "远程开门失败，请重试",
              onClose: () => {
                this.addRecord(
                  door.deviceCode,
                  door.deviceName,
                  2,
                  1,
                  door.buildCode == "" ? 0 : 1,
                  "",
                  0,
                  result.msg
                );
              }
            });
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },

    /* *************************************开门后处理************************************* */
    /* 手机开门记录 */
    addRecord(
      deviceId,
      deviceName,
      openType,
      deviceMfr,
      doorType,
      duration,
      openSucceed,
      remarks
    ) {
      //返回开门结果
      var phoneModel = device.model;
      var phoneType = device.platform;

      //记录点击开门时间
      var openEnd = new Date();
      duration = openEnd.getTime() - this.openStart.getTime();
      let reqData = {
        areaCode: this.$userInfo.AREA_CODE, //小区代码
        areaName: this.$userInfo.AREA_NAME, //小区名称
        buildCode: this.$userInfo.BUILD_CODE, //楼座代码
        buildName: this.$userInfo.BUILD_NAME, //楼座名称
        unitNo: this.$userInfo.UNIT_NO, //单元号
        roomCode: this.$userInfo.ROOM_CODE, //房间代码
        ownerCode: this.$userInfo.OWNER_CODE, //业主代码
        ownerName: this.$userInfo.OWNER_NAME, //业主名称
        ownerNickName: this.$userInfo.OWNER_NICK_NAME, //业主昵称
        phone: this.$userInfo.PHONE, //手机号
        phoneModel: phoneModel, // 开门手型号
        phoneType: phoneType, // 开门手机类型
        deviceId: deviceId, // 设备编号
        deviceName: deviceName, // 设备名称
        openType: openType, // 开门方式：1蓝牙，2远程开门，3二维码，0其它
        deviceMan: deviceMfr, //设备厂家 0其它 1立林 2令令 3业主宝 4海康
        doorType: doorType, //门口类型 0大门 1单元门
        duration: duration, //开门话费时间
        openSucceed: openSucceed, //开门是否成功 0失败 1成功
        remarks: remarks //结果备注
      };
      if (this.$userInfo.AREA_NO == "" || this.$userInfo.AREA_NO == undefined) {
        //确认门禁类型 是否有AREA_NO
        reqData.DOOR_TYPE = "1";
      } else {
        reqData.DOOR_TYPE = "0"; //门禁类型：0立林开门,1普通开门;默认普通开门
      }
      reqData.DOOR_TYPE = "0";
      //蓝牙开门成功，传入经纬度  3.2.4.0新增功能，用于自动计算门禁经纬度
      if (openType == 1 && openSucceed == 1) {
        reqData.deviceLongitude = this.longitude;
        reqData.deviceLatitude = this.latitude;
      }
      addRecordService(reqData)
        .then(result => {
          //请求成功
          if (result.success) {
            //添加记录成功
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
        });
    },
    /* 增加用户积分 */
    addPoint() {
      var reqData = {
        areaCode: this.$userInfo.AREA_CODE,
        behaviourNo: "1002",
        ownerCode: this.$userInfo.OWNER_CODE,
        appVersion: config.version
      };
      addPointService(reqData)
        .then(result => {
          //请求成功
          if (result.code == 200) {
            this.$toast("恭喜手机开门获取" + result.data + "积分");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.phoneDoorDefaule = true; //是否显示default页
          this.$toast("网络异常，请稍后重试！");
        });
    }
    /* *************************************开门后处理************************************* */
  }
};
</script>

