<template>
  <div style="height:100%;background:#F9F9F9">
    <!--<van-nav-bar id="NaviBar" title="账号设置" fixed left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="账号设置" >
    </navigation-bar>
    <div :class="{themeL:bindThemeL}">
      <!-- <van-cell-group> -->
      <van-cell title="修改绑定手机"  is-link to="changePhone" />
      <van-cell title="修改密码" is-link to="resetpassWord" />
      <van-cell title="字号大小设置" is-link to="setTheme" />
      <!-- </van-cell-group> -->
    </div>
    <div class="exitBtnSpace" :class="{exitBtnSpaceThemeL:bindThemeL}" @click="logOut"
    >退出账号</div>
  </div>
</template>

<script>
// import {} from '../../services/MineService'
import { Dialog, Toast } from "vant";
import { storage } from "../../utils/storage";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "UserSetting",
  components:{
    NavigationBar
  },
  data(){
    return{
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  methods: {
    onClickLeft() {
      if (this.$curTheme==0) {
        this.$router.replace("/minePage");
      } else {
        this.$router.replace("/minePageThemeL");
      }
    },
    logOut() {
      Dialog.confirm({
        title: "确定退出？"
      })
        .then(() => {
          console.log("退出账号");
          this.clearTags();
          this.deleteAlias();
          this.$setUserInfo(null);
          this.$router.replace("/login");
        })
        .catch(() => {});
    },
    //移除极光推送 别名
    deleteAlias() {
      this.$plugin.usePlugin("deleteAlias")(
        Number(this.random()), //sequence
        () => {
          //success callback
          console.log("极光推送: 删除别名成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：删除别名失败.");
        }
      );
    },
    //移除极光推送 标签
    clearTags() {
      this.$plugin.usePlugin("cleanTags")(
        Number(this.random()), //sequence
        () => {
          //success callback
          storage.setStorage("jpushTags", null, 60);
          console.log("极光推送：移除标签成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：移除标签失败.");
        }
      );
    },
    //生成随机数
    random: function() {
      var myDate = new Date(); //获取系统当前时间
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber =
        year +
        month +
        date +
        hours +
        minutes +
        seconds +
        Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    }
  }
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.van-cell {
  color: #333333;
  font-size: 15px;
  font-family: PingFangSC-Medium;
  letter-spacing: -0.05px;
}
/deep/.themeL .van-cell {
  font-size: 18px;
}
/deep/.themeL .van-cell__title {
  height: 38px;
  line-height: 38px;
}
/deep/.themeL .van-cell__right-icon {
  line-height: 38px;
}
.exitBtnSpace {
  height:40px;
  background-color:#FFFFFF;
  text-align:center;
  border:0.5px solid #F2F2F2;
  color:#FB5768;
  line-height:40px;
  font-size:15px;
}
.exitBtnSpaceThemeL {
  height:58px;
  line-height: 58px;
  font-size:18px;
}
</style>