  margin-left: 13px;
  flex: 1;
}
.cellInDelete-l {
  display: flex;
  background-color: #fff;
  /* margin: 12px; */
  border-radius: 3px;
  height: 130px;
  align-items: center;
  margin-left: 13px;
  flex: 1;
}
.title {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.05px;
  margin-bottom: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  max-width: 200px;
  margin-right: 10px;
}
.title-l {
  font-size: 17px;
  color: #333333;
  letter-spacing: -0.05px;
  margin-bottom: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  max-width: 200px;
  margin-right: 10px;
}
.emptyView {
  padding: 2rem 0px;
  text-align: center;
  color: rgb(0, 210, 202);
}
.empDataContent {
  margin: 1rem auto;
  text-align: center;
  color: #ddd;
}
.emptyImg {
  display: block;
  width: 40%;
  margin: 0 auto;
}

.uncollectView {
  height: 100%;
  width: 45px;
  background-color: #ff4356;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.uncollectView-l {
  height: 100%;
  width: 48px;
  background-color: #ff4356;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.uncollectText {
  width: 35px;
  font-size: 13px;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
