  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.articleImgSpace {
  min-width: 120px;
  margin-right: 10px;
}
.articleImgSpace img {
  height: 90px;
  margin-top: 10px;
  margin-right: 10px;
  margin-left: 10px;
}
.articleImgSpace-l {
  min-width: 120px;
  margin-right: 10px;
}
.articleImgSpace-l img {
  height: 90px;
  margin-top: 10px;
