import { Dialog } from "vant";
import Axios from "axios";
import {
    getPointsRecord,
    getSignList,
    getWillExpirePoints,
    getCurrentPoints,
    getActivitys,
    checkSignConf,
    pointSign
} from "../../services/MineService";
import NavigationBar from '@/common/currency/NavigationBar.vue';

export const MyPoints=   {
    name: "MyPoints",
    components: {
        NavigationBar
    },
    data() {
        return {
            headerItemWidth:{
                width:'120PX'
            },
            showSignView: false,
            showActivity: false,
            getSignData: {},
            activityData: null,
            isSigned: false,
            totalPoints: "正在获取积分",
            willExpirePoint: "0",
            activeIndex: 0,
            list: [],
            points: [],
            footerLoading: false,
            finished: false,
            headerLoading: false,
            currentPage: 1,
            pageSize: 10,
            signDays: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
            signSuccessText: "",
            showDialog: false,
            isIosDevice:this.$devicePlatform == 'ios'
        };
    },
    methods: {
        onClickLeft() {
            this.$router.back(-1);
        },
        onClickRight() {
            this.$router.push("/activityIntro");
        },
        onLoad() {
            if (!this.headerLoading) {
                this.currentPage = Math.ceil(this.list.length / this.pageSize) + 1;
                this.initPointRecord();
            }
        },
        onRefresh() {
            if (!this.footerLoading) {
                this.currentPage = 1;
                this.initPointRecord();
                this.initData();
            }
        },
        initPointRecord() {
            let param = {
                ownerCode: this.$userInfo.OWNER_CODE,
                page: this.currentPage,
                rows: this.pageSize
            };
            getPointsRecord(param)
                .then(data => {
                    if (this.currentPage == 1) {
                        this.list = [];
                    }
                    this.list.push(...data.list);
                    this.headerLoading = false;
                    this.footerLoading = false;
                    this.finished = data.pages == data.pageNum;
                })
                .catch(error => {
                    console.log("Mypoints " + error);
                    // this.$toast("网络异常，请稍后重试！");
                    this.headerLoading = false;
                    this.footerLoading = false;
                    this.finished = true;
                });
        },
        initData() {
            let param = { ownerCode: this.$userInfo.OWNER_CODE };
            let activityParam = { areaCode: this.$userInfo.AREA_CODE };
            // 查询是否有签到活动
            checkSignConf()
                .then(data => {
                    this.showSignView = data;
                })
                .catch(error => {
                    console.log("Mypoints "+error);
                    // this.$toast("网络异常，请稍后重试！");
                });
            // 签到列表
            getSignList(param)
                .then(res => {
                    this.parasData(res);
                })
                .catch(error => {
                    console.log("Mypoints "+error);
                    // this.$toast("网络异常，请稍后重试！");
                });
            //当前积分
            getCurrentPoints(param)
                .then(data => {
                    this.totalPoints = data.pointBalance;
                })
                .catch(error => {
                    console.log("Mypoints "+error);
                    // this.$toast("网络异常，请稍后重试！！");
                });

            //即将过期积分
            getWillExpirePoints(param)
                .then(data => {
                    this.willExpirePoint = data.out_date_balance;
                })
                .catch(error => {
                    console.log("Mypoints "+error);
                    // this.$toast("网络异常，请稍后重试！");
                });

            if ( !(this.$userInfo.AREA_CODE == null || this.$userInfo.AREA_CODE == undefined || this.$userInfo.AREA_CODE == "") ) {
                getActivitys(activityParam)
                    .then(data => {
                        this.showActivity = data.delFlag != 1;
                        this.activityData = data;
                    })
                    .catch(error => {
                        console.log("Mypoints " + error);
                        // this.$toast("网络异常，请稍后重试！");
                    });
            }


            //小区活动
            // if ( this.$userInfo.AREA_CODE == null || this.$userInfo.AREA_CODE == undefined || this.$userInfo.AREA_CODE == "" ) {
            //   Axios.all([checkSignConf(),getSignList(param),getCurrentPoints(param),getWillExpirePoints(param) ])
            //   .then(
            //     Axios.spread(
            //       (signConfigData, signListData, currPointData, expirePointData) => {
            //         this.showSignView = signConfigData;
            //         this.parasData(signListData);
            //         this.totalPoints = currPointData.pointBalance;
            //         this.willExpirePoint = expirePointData.out_date_balance;
            //       }
            //     )
            //   )
            //   .catch(err => {});
            // }else{
            //   Axios.all([checkSignConf(),getSignList(param),getCurrentPoints(param),getWillExpirePoints(param),getActivitys(activityParam) ])
            //   .then(
            //     Axios.spread(
            //       (signConfigData, signListData, currPointData, expirePointData,activityData) => {
            //         this.showSignView = signConfigData;
            //         this.parasData(signListData);
            //         this.totalPoints = currPointData.pointBalance;
            //         this.willExpirePoint = expirePointData.out_date_balance;
            //         this.showActivity = activityData.delFlag != 1;
            //         this.activityData = activityData;
            //       }
            //     )
            //   )
            //   .catch(err => {});
            // }
        },
        parasData(res) {
            this.points = res.data.valueList;
            this.signDays = [];
            this.activeIndex = res.data.signDays;
            this.isSigned = res.data.signFlag == 1;
            this.getSignData = res.data;
            var weekDayList = [
                "周一",
                "周二",
                "周三",
                "周四",
                "周五",
                "周六",
                "周日"
            ];
            var arr = weekDayList.splice(0, res.data.firstSignWeek - 1);
            weekDayList = weekDayList.concat(arr);
            let startIndex = res.data.firstSignWeek - 1;
            this.signDays = weekDayList;
        },
        signAction() {
            let param = { ownerCode: this.$userInfo.OWNER_CODE };
            pointSign(param)
                .then(data => {
                    this.showDialog = true;
                    this.signSuccessText = "+" + data.pointValue;
                    this.initData();
                    this.onRefresh();
                })
                .catch(error => {
                    console.log("Mypoints " + error);
                    // this.$toast("网络异常，请稍后重试！");
                });
        },
        goPointsActivity() {
            this.$router.push({
                path: "/pointActivity",
                query: {
                    promotionId: this.activityData.promotionId,
                    title: this.activityData.promotionName,
                    pointConsumed: this.activityData.pointConsumed
                }
            });
        }
    },
    mounted() {
        this.initData();
    },
    beforeRouteLeave(to, from, next) {
        if (this.showDialog) {
            this.showDialog = false;
            next(false);
        } else {
            next();
        }
    }
};
