<template>
  <!-- <phone-door></phone-door> -->

  <!-- tabs view -->
  <van-tabbar
    class="tabbar-class"
    v-model="active"
    active-color="#FB5768"
    inactive-color="#444444"
    route
    safe-area-inset-bottom
  >
    <van-tabbar-item replace to="/homePage" :class="{selectItem:homeIcon.show}">
      <span>首页</span>
      <img slot="icon" slot-scope="props" :src="homeIcon.show ? homeIcon.active : homeIcon.normal" />
    </van-tabbar-item>

    <van-tabbar-item replace to="/productPage" :class="{selectItem:welfare.show}">
      <span>优品</span>
      <img slot="icon" slot-scope="props" :src="welfare.show ? welfare.active : welfare.normal" />
    </van-tabbar-item>

    <van-tabbar-item v-if="disOpenDoorEntry">
      <!-- 开门 -->
      <phone-door class="open-door"></phone-door>
      <!-- <img class="open-door" slot="icon" slot-scope="props" :src="props.active ? doorIcon.active : doorIcon.normal" /> -->
    </van-tabbar-item>

    <van-tabbar-item replace to="/findPage">
      <span>发现</span>
      <img slot="icon" slot-scope="props" :src="props.active ? findIcon.active : findIcon.normal" />
    </van-tabbar-item>

    <van-tabbar-item replace to="/minePage" :class="{selectItem:mineIcon.show}">
      <span>我家</span>
      <img slot="icon" slot-scope="props" :src="mineIcon.show ? mineIcon.active : mineIcon.normal" />
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
import Findpage from "../Find/Findpage";
import { storage } from "../../utils/storage";
import phoneDoor from "../property/phoneDoor";

import Vue from 'vue';
import { Tabbar, TabbarItem } from 'vant';
Vue.use(Tabbar).use(TabbarItem);

//tabBar首页默认图
import homeNormalImg from '@/assets/images/common/tabbar_icon_home_normal.png'
//tabBar首页选择图
import homeSelectImg from '@/assets/images/common/tabbar_icon_home_click.png'
//tabBar发现默认图
import findNormalImg from '@/assets/images/common/tabbar_icon_find_nromal.png'
//tabBar发现选择图
import findSelectImg from '@/assets/images/common/tabbar_icon_find_click.png'
//tabBar开门默认图
import doorNormalImg from '@/assets/images/common/tabbar_icon_openDoor.png'
//tabBar开门选择图
import doorSelectImg from '@/assets/images/common/tabbar_icon_openDoor_sel.png'
//tabBar优品默认图
import welfareNormalImg from '@/assets/images/common/tabbar_icon_welfare_nromal.png'
//tabBar优品选择图
import welfareSelectImg from '@/assets/images/common/tabbar_icon_welfare_click.png'
//tabBar我家默认图
import mineNormalImg from '@/assets/images/common/tabbar_icon_mine_normal.png'
//tabBar我家选择图
import mineSelectImg from '@/assets/images/common/tabbar_icon_mine_click.png'

export default {
  name: "TabBar",
  data() {
    return {
      tabBarShow: true,
      debug: false,
      active: 0,
      redNoticeActive: false,
      homeIcon: {
        normal: homeNormalImg,
        active: homeSelectImg,
        show: true
      },
      findIcon: {
        normal: findNormalImg,
        active: findSelectImg
      },
      doorIcon: {
        // normal: doorNormalImg,
        normal: doorSelectImg,
        active: doorSelectImg
      },
      welfare: {
        normal: welfareNormalImg,
        active: welfareSelectImg,
        show: false
      },
      mineIcon: {
        normal: mineNormalImg,
        active: mineSelectImg,
        show: false
      }
    };
  },

  computed: {
    //控制是否显示手机开门入口
    disOpenDoorEntry: function() {
      return this.$store.state.home.hasOpenDoor;
    }
  },

  components: {
    'phone-door': phoneDoor,
    'find-page': Findpage,
  },

  methods: {
  },

  watch: {
    active: function(newValue, oldValue) {
      console.log('当前active：' + newValue + '之前avtive' + oldValue);
      switch (newValue) {
        case 0:
          // this.$router.push('/homePage');
          break;
        case 1:
          // this.$router.push('/servicePage');
          break;
        case 2:
          this.active = oldValue;
          //点击开门按钮
          console.log('----- 点击开门按钮 -----');
          break;
        case 3:
          // this.$router.push('/productPage');
          break;
        case 4:
          // this.$router.push('/minePage');
          break;
        default:
          break;
      }
    },
    $route(to, from) {
      console.log("【路由跳转跟踪】curTheme: "+Vue.prototype.$curTheme+", to.path: " + to.path+", from.path: "+from.path);
      //判断当前选中项
      if (to.path == "/homePage" || to.path == "/homePageThemeL") {
        this.homeIcon.show = true;
        this.welfare.show = false;
        this.mineIcon.show = false;
      } else if (to.path == "/productPage" || to.path == "/productPageThemeL") {
        this.homeIcon.show = false;
        this.welfare.show = true;
        this.mineIcon.show = false;
      } else if (to.path == "/minePage" || to.path == "/minePageThemeL") {
        this.homeIcon.show = false;
        this.welfare.show = false;
        this.mineIcon.show = true;
      } else if (to.path == "/findPage") {
        this.homeIcon.show = false;
        this.welfare.show = false;
        this.mineIcon.show = false;
      }
    },
  }
};
</script>

<style scoped lang="scss">

.tabbar-class {
  box-shadow: 0 -2px 7px rgba(226,226,226,0.50);
}
/deep/.van-tabbar-item__icon {
  margin-bottom: 0px !important;
  img {
    width: 32px;
    height: 32px;
  }
  .open-door:active{
    opacity: 1;    //通过透明度来设置按压效果
  }
}
  .open-door {
    // width: 62px;
    // height: 62px;
    position: relative;
    top: -10px;
    color: #fff;
  }
.van-hairline--top-bottom::after,
.van-hairline-unset--top-bottom::after {
  border-width: 0;
}
.selectItem{

}
/deep/.selectItem .van-tabbar-item__text {
  color:#FB5768 !important;
}
</style>
