const state = {
    eventBackButtonFlag: false, //支付页物理返回键按钮状态
    paysuccessEventBackButtonFlag: false, //支付成功页物理返回键按钮状态

    orderDetailEventBackButtonFlag: false, //订单详情物理返回键按钮状态
    orderListEventBackButtonFlag: false, //订单列表物理返回键按钮状态

    goodsDetailEventBackButtonFlag:false,//商品详情物理返回按键状态
    flashSaleEventBackButtonFlag:false,//秒杀详情物理返回按键状态
    addressEventBackButtonFlag:false,//收货地址
    evaluateListEventBackButtonFlag:false,//商品评价列表
    myEvaluateEventBackButtonFlag:false,//商品评价详情myEvaluate
    shopPageEventBackButtonFlag:false,//店铺首页
};

// 定义 getters
let getters = {
    getShopPageEventBackButtonFlag(state) {
        return state.shopPageEventBackButtonFlag
    },
    getEventBackButtonFlag(state) {
        return state.eventBackButtonFlag
    },
    getPaysuccessEventBackButtonFlag(state) {
        return state.paysuccessEventBackButtonFlag
    },
    getOrderDetailEventBackButtonFlag(state) {
        return state.orderDetailEventBackButtonFlag
    },
    getOrderListEventBackButtonFlag(state) {
        return state.orderListEventBackButtonFlag
    },
    getGoodsDetailEventBackButtonFlag(state) {
        return state.goodsDetailEventBackButtonFlag
    },
    getFlashSaleEventBackButtonFlag(state){
        return state.flashSaleEventBackButtonFlag
    },
    getAddressEventBackButtonFlag(){
        return state.addressEventBackButtonFlag
    },
    getEvaluateListEventBackButtonFlag(){
        return state.evaluateListEventBackButtonFlag
    },
    getMyEvaluateEventBackButtonFlag(){
        return state.myEvaluateEventBackButtonFlag
    }
};

const actions = {
    //修改支付页，true:点击返回, false:重置
    setShopPageEventBackButtonFlag(context, flag) {
        context.commit('setShopPageEventBackButtonFlag', flag);
    },
    //修改支付页，true:点击返回, false:重置
    setEventBackButtonFlag(context, flag) {
        context.commit('setEventBackButtonFlag', flag);
    },
     //修改支付成功状态，true:点击返回, false:重置
     setPaysuccessEventBackButtonFlag(context, flag) {
        context.commit('setPaysuccessEventBackButtonFlag', flag);
    },
     //修改支付页，true:点击返回, false:重置
     setOrderDetailEventBackButtonFlag(context, flag) {
        context.commit('setOrderDetailEventBackButtonFlag', flag);
    },
     //修改支付成功状态，true:点击返回, false:重置
     setOrderListEventBackButtonFlag(context, flag) {
        context.commit('setOrderListEventBackButtonFlag', flag);
    },
    setGoodsDetailEventBackButtonFlag(context, flag) {
        context.commit('setGoodsDetailEventBackButtonFlag', flag);
    },
    setFlashSaleEventBackButtonFlag(context, flag) {
        context.commit('setFlashSaleEventBackButtonFlag', flag);
    },
    setAddressEventBackButtonFlag(context, flag) {
        context.commit('setAddressEventBackButtonFlag', flag);
    },
    setEvaluateListEventBackButtonFlag(context, flag) {
        context.commit('setEvaluateListEventBackButtonFlag', flag);
    },
    setMyEvaluateEventBackButtonFlag(context, flag) {
        context.commit('setMyEvaluateEventBackButtonFlag', flag);
    },
};

const mutations = {
    // 设置支付页，true:点击返回, false:重置
    setShopPageEventBackButtonFlag(state, flag) {
        state.shopPageEventBackButtonFlag = flag;
    },
    // 设置支付页，true:点击返回, false:重置
    setEventBackButtonFlag(state, flag) {
        state.eventBackButtonFlag = flag;
    },
     // 设置支付成功状态，true:点击返回, false:重置
     setPaysuccessEventBackButtonFlag(state, flag) {
        state.paysuccessEventBackButtonFlag = flag;
    },
     // 设置支付页，true:点击返回, false:重置
     setOrderDetailEventBackButtonFlag(state, flag) {
        state.orderDetailEventBackButtonFlag = flag;
    },
     // 设置支付成功状态，true:点击返回, false:重置
     setOrderListEventBackButtonFlag(state, flag) {
        state.orderListEventBackButtonFlag = flag;
    },
    //
    setGoodsDetailEventBackButtonFlag(state, flag) {
        state.goodsDetailEventBackButtonFlag = flag;
    },
    setFlashSaleEventBackButtonFlag(state, flag) {
        state.flashSaleEventBackButtonFlag = flag;
    },
    setAddressEventBackButtonFlag(state, flag) {
        state.addressEventBackButtonFlag = flag;
    },
    setEvaluateListEventBackButtonFlag(state, flag) {
        state.evaluateListEventBackButtonFlag = flag;
    },
    setMyEvaluateEventBackButtonFlag(state, flag) {
        state.myEvaluateEventBackButtonFlag = flag;
    },
};

// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
