<template>
  <div style="height:100%;background:#F9F9F9;" :class="{themeL:bindThemeL}">
    <!--<van-nav-bar id="NaviBar" title="换绑手机" fixed left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="换绑手机" >
    </navigation-bar>
    <div class="phone-number" :class="{themeL:bindThemeL}">
      <input
        type="tel"
        class="input-phone"
        :class="{newPhone:firstCheckResult }"
        :placeholder="firstCheckResult ? '请输入新手机号' : '请输入原手机号'"
        maxlength="11"
        v-model="userPhone"
        :readonly="!firstCheckResult"
        onkeyup="value=value.replace(/[^\d]/g,'')"
      />
      <van-field
        v-model="secondCondition"
        :placeholder="placeholderText"
        :type="checkByPhone ? 'tel' : 'password'"
        :onkeyup="checkByPhone ? 'value=value.replace(/[^\\d]/g,\'\')' : 'value'"
        :maxlength="checkByPhone ? '6' : '16'"
      >
        <div
          slot="right-icon"
          class="msgCodeBtn"
          :class="{countDown : msgBtnText != '获取验证码' }"
          v-if="showMsgBtn"
          @click="getMsgcode"
        >{{msgBtnText}}</div>
      </van-field>
    </div>
    <div style="padding:25px" :class="{themeL:bindThemeL}">
      <van-button
        size="large"
        :color="couldCommit ? '#FB5768' : '#999999' "
        :disabled="!couldCommit"
        round
        @click="verifyBtnAction"
      >验证后绑定新手机号</van-button>
    </div>
    <span
      style="font-size:12px;color:#999999;float: right;padding-right:25px;"
      :style="{fontSize:defFontSize}"
      @click="changeCheckType"
    >{{checkByPhone ? "账号密码验证" : "手机号验证"}}</span>
    <!-- 弹出框 -->
    <van-dialog
            v-model="isShowDialog"
            confirmButtonColor="#fb5768"
            :message="dialogMessage"
            show-cancel-button
            :beforeClose="beforeClose"/>
  </div>
</template>

<script>
import {
  getMsgCode,
  verifyUserInfo,
  loginByPsw,
  getUserExits,
  changePhone
} from "../../services/MineService";
import { Toast, Dialog } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "ChangePhone",
  components:{
    NavigationBar
  },
  data() {
    return {
      showMsgBtn: true,
      couldVerify: false,
      btnColor: "#999999",
      userPhone: "",
      secondCondition: "",
      checkByPhone: true,
      msgBtnText: "获取验证码",
      placeholderText: "请输入验证码",
      firstCheckResult: false,
      couldCommit: false,
      timer: null,
      //是否显示提示框
      isShowDialog: false,
      //提示框内容
      dialogMessage: '',
      /*大字模式*/
      bindThemeL: this.$curTheme==0 ? false : true,
      defFontSize: this.$curTheme==0 ? "12px" : "15px"
    };
  },
  methods: {
    onClickLeft() {
      // if (this.firstCheckResult) {
      //   this.firstCheckResult = false;
      //   return;
      // }
      this.$router.back(-1);
    },
    //切换换绑验证机制
    changeCheckType() {
      this.firstCheckResult = false;
      if (this.timer) {
        this.clearTimer();
      }
      this.userPhone = this.formatPhone();
      this.secondCondition = "";
      this.checkByPhone = !this.checkByPhone;
    },
    //验证按钮事件
    verifyBtnAction() {
      var phoneValit = /^1[0-9]{10}$/;
      let phoneNumber = this.userPhone;
      if (this.firstCheckResult) {
        if (0 == phoneNumber.length) {
          this.$toast("手机号不能为空");
          return;
        }
        if (!phoneValit.test(phoneNumber)) {
          this.$toast("请输入有效的手机号", "center");
          return;
        }
        if (this.$userInfo.PHONE == phoneNumber) {
          this.$toast("新手机号不能与原来的手机号一样");
          return;
        }
        Dialog.confirm({
          title: "修改手机号后，您的登录账号也将被修改，确认换绑手机号？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.changePhoneAction();
          })
          .catch(error => {});
      } else {
        this.firstVerify();
      }
    },

    //验证旧手机号 或者 账号密码验证
    firstVerify() {
      let param = {};
      if (this.checkByPhone) {
        param = { msgCode: this.secondCondition, phone: this.$userInfo.PHONE };
        verifyUserInfo(param)
          .then(data => {
            this.firstCheckResult = true;
          })
          .catch(error => {
            this.$toast(error);
          });
      } else {
        param = { password: this.secondCondition, phone: this.$userInfo.PHONE };
        loginByPsw(param)
          .then(data => {
            this.firstCheckResult = true;
          })
          .catch(error => {
            console.log("=====qiao===== : "+JSON.stringify(error));
            if (error === '您还尚未设置密码，是否前往设置？') {
              this.dialogMessage = error;
              this.isShowDialog = true;
            }else if(error.match("登陆失败次数过多")) {
              this.$toast("登陆失败次数过多，请稍后再试！");
            }
          });
      }
    },
    changePhoneAction() {
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        PHONE: this.userPhone,
        PHONE1: this.$userInfo.PHONE,
        VERIFICATION: this.secondCondition
      };
      changePhone(param)
        .then(data => {
          this.refreshLoaclInfo();
          this.$router.back(-1);
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    refreshLoaclInfo(){
      var info = this.$userInfo;
      info.PHONE = this.userPhone;
      this.$setUserInfo(info);
    },
    //获取验证码
    getMsgcode() {
      if (this.msgBtnText !== "获取验证码") return;
      if (this.firstCheckResult) {
        this.getPhoneValit();
      } else {
        this.getMsgByOldPhone();
      }
    },
    getMsgByOldPhone() {
      let phoneValitInfo = Math.floor(Math.random() * 900000) + 100000;
      let param = {
        PHONE: this.$userInfo.PHONE,
        VERIFICATION_CODE: phoneValitInfo
      };
      getMsgCode(param)
        .then(data => {
          this.$toast(data.msg);
          if (true == data.success) {
            this.startTimeCountDown();
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    /*判断手机号存不存在、合法及手机验证码*/
    getPhoneValit() {
      var phoneValit = /^1[0-9]{10}$/;
      let phoneNumber = this.userPhone;

      if (0 == phoneNumber.length) {
        this.$toast("手机号不能为空");
        return;
      }
      if (!phoneValit.test(phoneNumber)) {
        this.$toast("请输入有效的手机号", "center");
        return;
      }
      if (this.$userInfo.PHONE == phoneNumber) {
        this.$toast("新手机号不能与原来的手机号一样");
        return;
      }

      var param = {
        PHONE: phoneNumber
      };
      getUserExits(param)
        .then(data => {
          if ("0" == data.success) {
            this.sendMsgCode();
          } else {
            this.$toast(
              "新手机号已注册信我家账号，不能更换，如需修改请联系客服"
            );
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    sendMsgCode() {
      var phoneValitInfo = parseInt(
        Math.floor(Math.random() * 900000) + 100000
      );
      var phoneNumberValit = {
        PHONE: this.userPhone,
        VERIFICATION_CODE: phoneValitInfo
      };
      getMsgCode(phoneNumberValit)
        .then(data => {
          this.$toast(data.msg);
          if (true == data.success) {
            this.startTimeCountDown();
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    startTimeCountDown() {
      var timeNumber = 60;
      this.timer = window.setInterval(() => {
        timeNumber--;
        if (timeNumber > 0) {
          this.msgBtnText = timeNumber + "秒";
        } else {
          this.clearTimer();
        }
      }, 1000);
      this.timer;
    },
    clearTimer() {
      window.clearInterval(this.timer);
      this.msgBtnText = "获取验证码";
    },
    checkBtnState() {
      this.couldCommit =
        this.userPhone.length >= 11 && this.secondCondition.length >= 6;
    },
    formatPhone() {
      let phone = this.$userInfo.PHONE;
      return phone.substring(0, 3) + "****" + phone.substr(7, 4);
    },
    beforeClose: function(action, done) {
      if (action === "confirm") {
          //跳转设置密码
          console.log('跳转设置密码');
          this.$router.push({
            name: "resetPwd",
            params: {phoneNum: this.$userInfo.PHONE}
          });
      }
      done();
    },
  },
  watch: {
    userPhone: function(newValue) {
      this.checkBtnState();
    },
    secondCondition: function(newValue) {
      this.checkBtnState();
    },
    checkByPhone: function(newValue) {
      this.placeholderText = newValue ? "请输入验证码" : "请输入密码";
      this.showMsgBtn = newValue;
    },
    firstCheckResult: function(newValue) {
      if (newValue) {
        if (this.timer) {
          this.clearTimer();
        }
        this.userPhone = "";
        this.secondCondition = "";
        this.checkByPhone = true;
      } else {
        this.userPhone = this.formatPhone();
      }
    }
  },
  mounted() {
    this.userPhone = this.formatPhone();
  }
};
</script>
<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.van-cell {
  height: 50px;
}
/deep/.themeL .van-cell {
  height: 58px;
  line-height: 58px;
  font-size: 17px;
}
/deep/.van-field__control {
  line-height: 30px;
  color: #333333;
}
.phone-number {
  background-color: #fff;
}
.phone-number .input-phone {
  font-family: PingFangSC-Regular;
  font-size: 14px;
  width: 100%;
  color: #8a8a8a;
  letter-spacing: -0.06px;
  background-color: transparent;
  border: 0;
  padding: 16px;
  box-sizing: border-box;
  display: block;
  text-align: left;
}
.themeL .phone-number .input-phone {
  font-size: 17px;
  padding: 18px 16px;
}
.msgCodeBtn {
  color: #FB5768;
  padding: 0px 8px;
  font-size: 13px;
  height: 100%;
  line-height: 28px;
}
.themeL .msgCodeBtn {
  font-size: 16px;
}
.countDown{
  color:#BDBDBD;
}

.phone-number .newPhone{
  color: #333333;
}

/deep/.themeL .van-button {
  font-size: 18px;
}
</style>