import http from "../http";


export {
    getUserExitsService,
    getPhoneValitService,
    requestLoginService,
    getProtocolService,
    requestModifyOPwdService,
    changePassWord,
    getRsaPublicKey,
}

//获取用户是否存在服务
const getUserExitsService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/login/getUserExits.do', { data: JSON.stringify(reqData) }, true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

//获取手机验证码
const getPhoneValitService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/login/getMsgCode.do', { data: JSON.stringify(reqData) }, true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

// 请求登录服务 reqData：msgCode&phone
// loginType 0：短信验证码登录 1：RSA密码登录 2:密码登录 // qiao 2019.12.12
const requestLoginService = (reqData, loginType) => {
    let requestUrl = '';
    if (loginType === 0) {
        requestUrl = 'xwj-sys-user/login/loginByOwnerMsgCodeRSA';
    }
    else if (loginType === 1) {
        requestUrl = 'xwj-sys-user/login/loginByOwnerPasswordRSA';
    }else if (loginType === 2){
        requestUrl = 'xwj-sys-user/login/loginByOwnerPassword';
    }
    return new Promise((resolve, reject) => {
        http.get(requestUrl, reqData, 'baseUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

