<template>
  <div style="height:100%;overflow:auto;background:#f9f9f9;">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list
        :style="{'min-height':list.length == 0 ? '0px' : (listMinHeight+'px'),'background-color':'#f9f9f9'}"
        v-model="loading"
        :finished="finished"
        @load="onLoad"
      >
        <van-checkbox-group v-model="selectItems">
          <div
            style="padding-top:13px;padding-right:13px;"
            v-for="(item,index) in list"
            :key="index"
          >
            <van-swipe-cell>
              <template slot="right">
                <div
                  :class="themeCurrent == '1' ? 'uncollectView-l' : 'uncollectView'"
                  @click="uncollectItem(item)"
                >
                  <span :class="themeCurrent == '1' ? 'uncollectText-l' : 'uncollectText'">取消收藏</span>
                </div>
              </template>
              <van-checkbox
                style="flex-shrink:0;margin-left:10px; "
                checked-color="#FB5768"
                v-if="isDeleteState"
                :name="item.collectCode"
              ></van-checkbox>
              <div
                v-bind:class="!isDeleteState ? 'cellItem' : 'cellInDelete'"
                @click="goToGoodsDetail(item)"
                v-if="themeCurrent == '0'"
              >
                <div class="goodsImg" @click="goToGoodsDetail(item)">
                  <img v-lazy="item.spuPicUrl" />
                  <div class="showActivitySpu" v-show="item.activitySpuId">秒杀</div>
                  <div
                    class="outSellBox"
                    v-if="item.goodsStatus == '02' || (item.storeNumber == 0&&!item.activitySpuId)"
                  >{{item.goodsStatus == '02' ? '已下架' : '补货中'}}</div>
                </div>

                <div class="goodsInfoBox" @click="goToGoodsDetail(item)">
                  <div>
                    <span class="name">{{item.spuName}}</span>
                    <span class="content">{{item.spuSalePoint}}</span>
                  </div>
                  <span class="price">￥{{item.skuPrice}}</span>
                </div>
              </div>
              <div
                v-bind:class="!isDeleteState ? 'cellItem-l' : 'cellInDelete-l'"
                @click="goToGoodsDetail(item)"
                v-if="themeCurrent == '1'"
              >
                <div class="goodsImg-l" @click="goToGoodsDetail(item)">
                  <img v-lazy="item.spuPicUrl" />
                  <div class="showActivitySpu" v-show="item.activitySpuId">秒杀</div>
                  <div
                    class="outSellBox-l"
                    v-if="item.goodsStatus == '02' || (item.storeNumber == 0&&!item.activitySpuId)"
                  >{{item.goodsStatus == '02' ? '已下架' : '补货中'}}</div>
                </div>

                <div class="goodsInfoBox-l" @click="goToGoodsDetail(item)">
                  <div>
                    <span class="name-l">{{item.spuName}}</span>
                    <span class="content-l">{{item.spuSalePoint}}</span>
                  </div>
                  <span class="price-l">￥{{item.skuPrice}}</span>
                </div>
              </div>
            </van-swipe-cell>
          </div>
        </van-checkbox-group>
      </van-list>
      <div class="emptyView" v-show="list.length==0">
        <div class="empDataContent">
          <img class="emptyImg" src="../../assets/images/default/no_shoucang_img.png" />
          <p style="margin-top: 1rem;font-size: 16px;color:#9a9a9a;">暂无收藏</p>
        </div>
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getCollectList, deleteCollect } from "../../services/MineService";
import { Toast, Dialog } from "vant";
export default {
  name: "GoodsCollect",
  data() {
    return {
      listMinHeight: document.body.clientHeight - this.$stbHeight - 55 - 44,
      list: [],
      loading: false,
      finished: false,
      isLoading: false,
      currentPage: 1,
      pageSize: 10,
      isDeleteState: false,
      selectItems: [],
      themeCurrent: this.$curTheme
    };
  },
  activated() {
    this.themeCurrent = this.$curTheme;
  },
  methods: {
    onLoad() {
      // 异步更新数据
      this.currentPage = Math.ceil(this.list.length / this.pageSize) + 1;
      if (this.finished) {
        return
      }
      this.initArticleList();
    },
    onRefresh() {
      this.currentPage = 1;
      this.initArticleList();
    },
    initArticleList() {
      let param = {
        collectType: "01",
        page: this.currentPage,
        rows: this.pageSize,
        userCode: this.$userInfo.OWNER_CODE,
        areaCode: this.$userInfo.AREA_CODE
      };
      console.log(this.currentPage);
      getCollectList(param)
        .then(data => {
          if (this.currentPage == 1) {
            this.list = [];
            if(data.list.length==0){
              this.$emit("resetDeleteState");
            }
          }
          console.log(data.list);
          this.list.push(...data.list);
          this.loading = false;
          this.isLoading = false;
          console.log(this.currentPage, data.pages);
          this.finished = this.currentPage >= data.pages;
        })
        .catch(error => {
          console.log(error);
          // Toast(error);
          this.loading = false;
          this.finished = true;
          this.isLoading = false;
        });
    },
    uncollectItem(item) {
      console.log(item);
      let param = { collectCodes: [item.collectCode] };
      console.log(param);
      deleteCollect(param)
        .then(data => {
          // this.list.splice(this.list.indexOf(item), 1);
          this.onRefresh();
        })
        .catch(error => {
          // Toast(error);
        });
    },
    uncollectMultiItem() {
      let codes = [];
      if (this.selectItems.length == 0) {
        this.$toast("请选择要取消收藏的商品");
        return;
      }
      // for (let item of this.selectItems) {
      //   codes.push(item.collectCode);
      // }
      codes = this.selectItems;
      if (codes.length == 0) {
        return;
      }
      console.log(codes);
      let param = { collectCodes: codes };
      deleteCollect(param)
        .then(data => {
          // let array = [];
          // array.push(...this.list);
          // for (let collectCode of this.selectItems) {
          //   for (const item of array) {
          //     if (item.collectCode == collectCode) {
          //       this.list.splice(this.list.indexOf(item), 1);
          //     }
          //   }
          // }
          // this.selectItems = [];
          // this.onLoad();
          this.onRefresh();
        })
        .catch(error => {
          // Toast(error);
        });
    },
    goToGoodsDetail(item) {
      if (item.goodsStatus == "02" || (item.storeNumber == 0&&!item.activitySpuId)) {
        Dialog.alert({
          message: item.goodsStatus == "02" ? "商品已下架" : "商品补货中",
          confirmButtonColor: "#FFF"
        }).then(() => {});
        return;
      }
      if (item.activitySpuId) {
        this.$router.push({
          name: "flashSaleGoods",
          query: {
            spuCode: item.spuCode,
            activitySpuId: item.activitySpuId,
            goodsUrl: item.spuPicUrl
          }
        });
      } else {
        this.$router.push({
          name: "goodsDetail",
          params: {
            spuCode: item.spuCode,
            goodsUrl: item.spuPicUrl
          }
        });
      }
    },
    selectAll(isSelectAll) {
      if (isSelectAll) {
        for (const item of this.list) {
          if (this.selectItems.indexOf(item.collectCode) == -1) {
            this.selectItems.push(item.collectCode);
          }
        }
      } else {
        this.selectItems = [];
      }
    }
  },
  watch: {
    isDeleteState: function(newValue) {
      this.selectItems = [];
    },
    selectItems: function(newValue) {
      console.log(newValue);
      this.$emit("select-changed", {
        isAllSelect:
          newValue.length == this.list.length && this.selectItems.length != 0,
        name: "goods"
      });
    },
    list(newValue){
      let array = []
      for (const item of newValue) {
        let collectCode = item.collectCode;
        if (this.selectItems.indexOf(collectCode) != -1) {
          array.push(collectCode)
        }
      }
      this.selectItems = array
    }
  }
};
</script>

