import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

//关于我们
import AboutUs from './page/Mine/AboutUs'
//语音助手
import VoiceAssistant from './page/VoiceAssistant/MessageChat'
//我的收藏
import MyCollect from './page/Mine/MyCollect'
//家庭成员
import MyFamily from './page/Mine/MyFamily'
//家庭成员--改版
import MyFamilyNew from './page/Mine/MyFamilyNew'
//家庭成员--人员申请--改版新增页面
import MyFamilyApply from './page/Mine/MyFamilyApply'
//房屋管理
import MyHouses from './page/Mine/MyHouses'
//房屋管理--绑定授权--改版新增页面
import MyHousesBindAuth from './page/Mine/MyHousesBindAuth'
//我的积分
import MyPoints from './page/Mine/MyPoints.vue'
//积分转盘
import PointActivity from './page/Mine/PointActivity'
//个人设置
import UserProfile from './page/Mine/UserProfile'
//账号设置
import UserSetting from './page/Mine/UserSetting'
//中奖记录
import PrizeRecord from './page/Mine/PrizeRecord'
//选择小区
//import SelectArea from './page/Mine/SelectArea'
//选择房产
import SelectHouse from './page/Mine/SelectHouse'
//提交房产申请
import RoomApply from './page/Mine/RoomApply'
//选择城市
import SelectCity from './page/Mine/SelectCity'
//搜索房间信息
import SearchRoom from './page/Mine/SearchRoom'
//换绑手机
import ChangePhone from './page/Mine/ChangePhone'
//重置密码
import ResetPassWord from './page/Mine/ResetPassword'
//验证码重置密码
import RecoverPassWord from './page/Mine/RecoverPassword'
//积分说明
import ActivityIntro from './page/Mine/ActivityIntro'
//字号大小设置
import SetTheme from './page/Mine/SetTheme'

const mineRouter = [{
  name: 'mine',
  path: '/mine',
  component: () => import('./page/Mine/MineHome.vue'),
},
  {
    name: 'aboutUs',
    path: '/aboutUs',
    component: AboutUs
  },
  {
    name: 'voiceAssistant',
    path: '/voiceAssistant',
    component: VoiceAssistant,
    meta: {
        title: '语音助手',
        keepAlive: true,
        statusBgc: true
      }
  },
  {
    name: 'myCollect',
    path: '/myCollect',
    component: MyCollect,
    meta: {
      title: '我的收藏',
      keepAlive: true
    }
  },
  {
    name: 'myFamily',
    path: '/myFamily',
    component: MyFamily
  },
  {
    name: 'myFamilyNew',
    path: '/myFamilyNew',
    component: MyFamilyNew
  },
  {
    name: 'myFamilyApply',
    path: '/myFamilyApply',
    component: MyFamilyApply
  },
  {
    name: 'myHouses',
    path: '/myHouses',
    component: MyHouses
  },
  {
    name: 'myHousesBindAuth',
    path: '/myHousesBindAuth',
    component: MyHousesBindAuth
  },
  {
    name: 'myPoints',
    path: '/myPoints',
    component: MyPoints
  },
  {
    name: 'pointActivity',
    path: '/pointActivity',
    component: PointActivity
  },
  {
    name: 'userProfile',
    path: '/userProfile',
    component: UserProfile
  },
  {
    name: 'userSetting',
    path: '/userSetting',
    component: UserSetting
  },
  {
    name: 'prizeRecord',
    path: '/prizeRecord',
    component: PrizeRecord
  },
  {
    name: 'selectArea',
    path: '/selectArea',
    //component: SelectArea
    component: () => import('@/page/Mine/SelectArea.vue')
  },
  {
    name: 'selectHouse',
    path: '/selectHouse',
    component: SelectHouse
  },
  {
    name: 'roomApply',
    path: '/roomApply',
    component: RoomApply
  },
  {
    name: 'selectCity',
    path: '/selectCity',
    component: SelectCity
  },
  {
    name: 'searchRoom',
    path: '/searchRoom',
    component: SearchRoom
  },
  {
    name: 'changePhone',
    path: '/changePhone',
    component: ChangePhone
  },
  {
    name: 'resetPassWord',
    path: '/resetPassWord',
    component: ResetPassWord
  },
  {
    name: 'recoverPassWord',
    path: '/recoverPassWord',
    component: RecoverPassWord
  },
  {
    name: 'activityIntro',
    path: '/activityIntro',
    component: ActivityIntro
  },
  {
    name: 'setTheme',
    path: '/setTheme',
    component: SetTheme
  }
];


export default{
    mineRouter
};
