<template>
  <div style="height:100%;background:#F9F9F9;display: flex;flex-direction: column;">
    <!--<van-nav-bar id="NaviBar" title="家庭成员" left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="家庭成员" >
    </navigation-bar>
    <van-tabs
      style="flex: 1;overflow: auto;display: flex;flex-direction: column;"
      v-model="active"
      sticky
      @change="onClick"
      v-if="isHouseOwner"
      :class="{themeL:bindThemeL}"
    >
      <van-tab>
        <div slot="title">家庭成员</div>
        <member-list ref="members" isOwner="true" />
      </van-tab>
      <van-tab>
        <div slot="title">人员申请</div>
        <member-apply ref="applyMembers" @reload-data="initMemberList" :list="list" />
      </van-tab>
    </van-tabs>
    <member-list style="overflow:auto;" ref="members" v-if="!isHouseOwner" />
  </div>
</template>

<script>
import MemberList from "./MemberList";
import MemberApply from "./MemberApply";
import { getMemberList, getMemberListNew, getLoginStatus } from "../../services/MineService";
import { Toast } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "MyFamily",
  components: {
    MemberList,
    MemberApply,
    NavigationBar
  },
  data() {
    return {
      active: 0,
      list: [],
      isHouseOwner: false,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    initMyFlag() {
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getLoginStatus(param)
        .then(data => {
          this.isHouseOwner = data.RELATE_FLAG == 1;
        })
        .catch(error => {
          // Toast(error);
        });
    },
    initMemberList() {
      //调用新接口
      if (this.$openVirArea) {
        //暂时在开发联调环境下调用新接口
        this.getMemberListNew();
        return;
      }
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        ROOM_CODE: this.$userInfo.ROOM_CODE
      };
      getMemberList(param)
        .then(data => {
          this.list = data;
          this.$refs.members.list = this.members;
        })
        .catch(error => {
          console.log(error);
          // Toast(error);
        });
    },
    //获取家庭成员列表（新接口）
    getMemberListNew() {
      let param = {
        ownerCode: this.$userInfo.OWNER_CODE,
        roomCode: this.$userInfo.ROOM_CODE
      };
      getMemberListNew(param)
        .then(result => {
          if (result.resultCode == 0) {
            //新老接口字段映射
            result.data.forEach((item, index) => {
              item.APPROVAL_RESULT = item.approvalResult;
              item.APPROVAL_STATUS = item.approvalStatus;
              item.OWNER_CODE = item.ownerCode;
              item.OWNER_NICK_NAME = item.ownerNickName;
              item.PHONE = item.phone;
              item.PIC_URL = item.picUrl;
              item.RELATE_FLAG = item.relateFlag;
              item.ROOM_CODE = item.roomCode;
              item.ROOM_NAME = item.roomName;
              item.UNIT_NO = item.unitNo;
              item.UUID = item.uuid;
            })

            this.list = result.data;
            this.$refs.members.list = this.members;
          } else {
            console.log("【Error】getMemberListNew: "+result.msg);
          }
        })
        .catch(error => {
          console.log(error);
          // Toast(error);
        });
    },
    onClick(name, title) {}
  },
  mounted() {
    this.initMyFlag();
    this.initMemberList();
  },
  computed: {
    members: function() {
      let arr = [];
      for (let member of this.list) {
        if (member.APPROVAL_STATUS == 1 && member.APPROVAL_RESULT == 1) {
          arr.push(member);
        }
      }
      return arr;
    }
  }
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.van-tabs__line {
  width: 25px !important;
  height: 3px;
  border-radius: 1.5px;
  background-color: #fb5768;
}
/deep/.van-tabs {
  /* height: 100%; */
  /* overflow: auto; */
}
/deep/.van-tabs__content{
  flex: 1;
  overflow: auto;
}
/*大字模式*/
/deep/ .themeL .van-tab {
  font-size: 17px;
}
</style>