<template>
  <div style="height:100%;background:#F9F9F9">
    <div style="height:100%">
      <!--<van-nav-bar id="NaviBar" title="密码找回" fixed left-arrow @click-left="onClickLeft" />-->
      <navigation-bar  @onLeftClick="onClickLeft" pageName="密码找回" >
      </navigation-bar>
      <div :style="padingtopNum" :class="{themeL:bindThemeL}">
        <van-field v-model="formatPhone" disabled />
        <van-field
          v-model="verifyCode"
          type="tel"
          onkeyup="value=value.replace(/[^\d]/g,'')"
          maxlength="6"
          clearable
          placeholder="请输入验证码"
        >
          <div
            slot="right-icon"
            class="msgCodeBtn"
            :class="{countDown : verifyCodeStr != '获取验证码'}"
            @click="getPhoneAuthCode"
          >{{verifyCodeStr}}</div>
        </van-field>
        <van-field
          v-model="password"
          type="password"
          maxlength="16"
          clearable
          placeholder="请输入新密码（6-16位字符）"
        />
      </div>
      <div style="padding:25px" :class="{themeL:bindThemeL}">
        <van-button
          :color="okBtnEnable ? '#FB5768' : '#999999'"
          size="large"
          round
          :disabled="!okBtnEnable"
          @click="clickedokBtn"
        >确 认</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getPhoneValitService,
  requestModifyOPwdService,
  getRsaPublicKey
} from "../../services/loginService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from "vue";
export default {
  name: "RecoverPassword",
  components:{
    NavigationBar
  },
  mounted() {
    // 获取传参
    // let routerParams = this.$route.params;
    // if (routerParams != undefined && routerParams.phoneNum != undefined) {
    //   this.phoneNum = routerParams.phoneNum;
    // }
    this.getPubKey();
  },
  data() {
    return {
      title: "重置密码",
      verifyCodeStr: "获取验证码",
      phoneNum: this.$userInfo.PHONE,
      verifyCode: "",
      password: "",
      //确认按钮是否可点击
      okBtnEnable: false,
      //是否显示提示框
      isShowDialog: false,
      //提示框内容
      dialogMessage: "",
      padingtopNum: {
        //"padding-top": this.$stbHeight + 44 + "px",
        "margin-bottom": +10 + "px"
      },
      publicKey: "",
      /*大字模式*/
      bindThemeL: this.$curTheme==0 ? false : true
    };
  },
  watch: {
    //监听验证码输入
    verifyCode: function() {
      this.changngeLoginBtnStatus();
    },
    //监听密码输入
    password: function() {
      this.changngeLoginBtnStatus();
    }
  },
  computed: {
    formatPhone() {
      let phone = this.$userInfo.PHONE;
      return phone.substring(0, 3) + "****" + phone.substr(7, 4);
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    //修改登录按钮状态
    changngeLoginBtnStatus() {
      this.okBtnEnable =
        this.verifyCode.length >= 6 && this.password.length >= 6;
    },
    clickedokBtn: function() {
      console.log("点击确认按钮");
      if (!this.okBtnEnable) {
        return;
      }
      // let reqData = {
      //   PHONE: this.phoneNum,
      //   OWNER_PWD: this.password,
      //   VERIFICATION: this.verifyCode
      // };
      let basePassWord = btoa(this.password);
      let rsaInfo = this.phoneNum + "," + this.verifyCode + "," + basePassWord;
      let rsaPassWord = Vue.prototype.$getRSASign(rsaInfo, this.publicKey);
      let reqData = {
        modifyInfo: rsaPassWord
      };
      requestModifyOPwdService(reqData)
        .then(noteData => {
          this.showMsg(noteData.msg);
          if (noteData.resultCode == "0") {
            this.onBack();
          }
        })
        .catch(error => {
          // this.showMsg(error);
        });
    },

    //获取验证码
    getPhoneAuthCode: function() {
      if (this.verifyCodeStr !== "获取验证码")  return;
      let reqData = {
        PHONE: this.phoneNum
      };
      getPhoneValitService(reqData)
        .then(noteData => {
          if (noteData.success == false) {
            //失败
            this.showMsg(noteData.msg);
          } else {
            //成功
            this.showMsg("验证码已发送");
            let totalTime = 60;
            this.verifyCodeStr = totalTime + "秒";
            let clock = window.setInterval(() => {
              totalTime--;
              if (totalTime > 0) {
                this.verifyCodeStr = totalTime + "秒";
              } else {
                window.clearInterval(clock);
                //修改颜色
                this.verifyCodeStr = "获取验证码";
              }
            }, 1000);
          }
        })
        .catch(error => {
          // this.showMsg(error);
        });
    },
    // 退出登录弹框回调
    beforeClose: function(action, done) {
      if (action === "confirm") {
        this.$store.dispatch("changeActiveTabFun", 0);
        //跳转验证码登录
        this.onBack();
      }
      done();
    },
    onBack() {
      this.$router.replace("/userSetting");
    },
    showMsg: function(msg) {
      this.$toast(msg);
    },
    getPubKey() {
      getRsaPublicKey()
        .then(res => {
          this.publicKey = res.data.publicKey;
        })
        .catch(error => {
          this.isLoading = false;
        });
    },
    random: function() {
      var myDate = new Date(); //获取系统当前时间
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber =
        year +
        month +
        date +
        hours +
        minutes +
        seconds +
        Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    }
  }
};
</script>

<style scoped>
/deep/.van-field__control {
  color: #333333;
}
/deep/.themeL .van-cell__value {
  height: 38px;
  line-height: 38px;
  font-size: 17px;
}
.msgCodeBtn {
  color: #fb5768;
  padding: 0px 8px;
  font-size: 13px;
  height: 100%;
  line-height: 28px;
}
.themeL .msgCodeBtn {
  font-size: 16px;
}
/deep/.themeL .van-button {
  font-size: 18px;
}
.countDown{
  color: #8D8D8D;
}
</style>