 import { storage } from './storage';
import Vue from 'vue'
Vue.prototype.isDeleteLocalImgFile = null;
//是否删除本地缓存图片
function isDeleteLocalImg (){
    let localImgDataLength = 0;
    let localImgData = storage.getStorage('localImgData');
    console.log("-------------------")
    console.log(localImgData)
    Object.keys(localImgData).forEach(function(key){
        // console.log(key,localImgData[key]);  //key--月份  localImgData[key]--月份缓存的图片数组
        localImgDataLength+=localImgData[key].length;
    }); 
    if (localImgDataLength>4000){
        let yearMonth= Object.keys(localImgData)[0];
        delete  localImgData[Object.keys(localImgData)[0]]
        storage.setStorage('localImgData',localImgData,180);
        // deleteLacalImg(yearMonth);  //删除本地文件夹
        Vue.prototype.isDeleteLocalImgFile = yearMonth;
    }
}
isDeleteLocalImg();
//删除本地文件夹
Vue.prototype.$deleteLacalImg = function(yearMonth){
    if (Vue.prototype.$devicePlatform == "pc") {
        return;
    }
    window.requestFileSystem(LocalFileSystem.PERSISTENT, 0, function (fs) {
        //删除文件夹
        fs.root.getDirectory('goodsImgFile', { create: true }, function (fileEntry) {
            fileEntry.getDirectory(yearMonth, { create: true }, function (sonFileEntry) {
                sonFileEntry.removeRecursively(function () {
                    console.log('delete success');
                }, function (err) {
                    console.log("--------+++++++++")
                    console.error(err);
                }, function () {
                    console.log('file not exist');
                });
            }, onErrorGetDir);
        }, onErrorGetDir);
    }, onErrorLoadFs)
}

//判断本地是否有图片
Vue.prototype.$isHasLocalImage = function (imageUrl,callBack) {
    if (this.$devicePlatform == 'ios') {
        callBack(imageUrl)
        return;
    }
    

    console.log("-------------"+imageUrl);
    let hasImgUrl = false; //是否有缓存图片
    let hasYearMonth = null; //缓存的月份文件夹
    if (Vue.prototype.$devicePlatform == "pc") {
        callBack(imageUrl)
        return ;
    }
    let localImgData = null;
    if(storage.getStorage('localImgData').$s_code_$exp == 100||storage.getStorage('localImgData').$s_code_$exp == 101){
        console.log("缓存过期设置对象")
        callBack(imageUrl);
        setTimeout(()=>{
            Vue.prototype.$setLocalImage(imageUrl)
        },1000)
        return;
    }else{
        console.log("获取localImgData对象")
        localImgData = storage.getStorage('localImgData'); //获取已缓存的图片数据
    }
    let filename = imageUrl;
    if (imageUrl.indexOf("/") > 0){//如果包含有"/"号 从最后一个"/"号+1的位置开始截取字符串
        filename = imageUrl.substring(imageUrl.lastIndexOf("/") + 1, imageUrl.length);
        console.log("-------------"+filename)
    }
    Object.keys(localImgData).forEach(function(key){
        console.log(key,localImgData[key]);  //key--月份  localImgData[key]--月份缓存的图片数组
        localImgData[key].map((item)=>{
            if(item == filename){
                hasImgUrl = true; // 有缓存的图片
                hasYearMonth = key; // 缓存图片的月份文件夹
                return;
            }
        })
    });
    if(hasImgUrl){
        console.log("有缓存的图片")
        //有缓存的图片
        Vue.prototype.$getLocalFileUrl(imageUrl,filename,hasYearMonth,callBack)
    }else{
        console.log("没有缓存的图片")
        //没有缓存的图片
        callBack(imageUrl);
        setTimeout(()=>{
            Vue.prototype.$setLocalImage(imageUrl)
        },1000)
    }

};

//存储本地图片
Vue.prototype.$setLocalImage = function (imageUrl) {
    if (Vue.prototype.$devicePlatform == "pc") {
        return;
    }
    let now = new Date();
    let year = now.getFullYear(); //得到年份
    let month = now.getMonth()+1<10?('0'+(now.getMonth()+1)):(now.getMonth()+1);//得到月份
    let yearMonth = year+month; //得到月份文件名
    window.requestFileSystem(LocalFileSystem.PERSISTENT, 0, function (fs) {
        //创建文件夹
        fs.root.getDirectory('goodsImgFile', { create: true }, function (fileEntry) {
            fileEntry.getDirectory(yearMonth, { create: true }, function (sonFileEntry) {
                createFile(sonFileEntry, imageUrl);
            }, onErrorGetDir);
        }, onErrorGetDir);
    }, onErrorLoadFs)
}

//在文件夹里面创建图片
function createFile(fileEntry,imageUrl) {
    var xhr = new XMLHttpRequest();
    xhr.open('GET', imageUrl, true);
    xhr.responseType = 'blob';
    // var ImgObj = new Image();
    // ImgObj.src = pathImg;
    // if(ImgObj.fileSize > 0 || (ImgObj.width > 0 && ImgObj.height > 0)) {
    //        return true;
    //      } else {
    //        return false;
    //        }
    xhr.onload = function () {
        if (this.status == 200) {
            var blob = new Blob([this.response], { type: 'image/*' });
            var filename;
            if (imageUrl.indexOf("/") > 0)//如果包含有"/"号 从最后一个"/"号+1的位置开始截取字符串
            {
                filename = imageUrl.substring(imageUrl.lastIndexOf("/") + 1, imageUrl.length);
            }
            else {
                filename = imageUrl;
            }
            // saveFile(fs.root, blob, filename);

            fileEntry.getFile(filename, { create: true, exclusive: false }, function (fileEntry) {
                // writeFile(fileEntry, blob);
                //创建一个写入对象
                fileEntry.createWriter(function (fileWriter) {

                    //文件写入成功
                    fileWriter.onwriteend = function () {
                        console.log("Successful file write...---------"+fileEntry.toURL());
                        console.log(fileEntry)
                        setLocalStorage(filename);

                    };

                    //文件写入失败
                    fileWriter.onerror = function (e) {
                        fileEntry.remove(function () {
                            console.log('delete success');
                        }, function (err) {
                            console.error(err);
                        }, function () {
                            console.log('file not exist');
                        });
                        console.log("Failed file write: " + e.toString());
                    };

                    //写入文件
                    fileWriter.write(blob);
                });
            }, function (fileEntry) {
                fileEntry.remove(function () {
                    console.log('delete success');
                }, function (err) {
                    console.error(err);
                }, function () {
                    console.log('file not exist');
                });
                console.log("文件创建失败！")
            });
        }
    };
    xhr.send();
};

// 更新本地storage里面的图片
function setLocalStorage(locaImgUrl){
    let now = new Date();
    let year = now.getFullYear(); //得到年份
    let month = now.getMonth()+1<10?('0'+(now.getMonth()+1)):(now.getMonth()+1);//得到月份
    let yearMonth = year+month;
    if(storage.getStorage('localImgData').$s_code_$exp == 100||storage.getStorage('localImgData').$s_code_$exp == 101){
        storage.setStorage('localImgData',{},180);
    }
    let localImgData = storage.getStorage('localImgData');
    if(localImgData.hasOwnProperty(yearMonth)){
        localImgData[yearMonth].push(locaImgUrl)
    }else{
        localImgData[yearMonth] = [];
        localImgData[yearMonth].push(locaImgUrl)
    }
    storage.setStorage('localImgData',localImgData,180);
};
// //读取本地图片路径
Vue.prototype.$getLocalFileUrl = function () {
    if (Vue.prototype.$devicePlatform == "pc") {
        return;
    }
    window.requestFileSystem(LocalFileSystem.PERSISTENT, 0, function (fs) {
        fs.root.getDirectory('goodsImgFile', { create: true }, function (fileEntry) {
            return fileEntry.nativeURL
        }, onErrorGetDir);
    });
};
