/*大字主题页面路由表*/
import MyPoints from "./page/Mine/MyPoints";

const routerThemeL = [
    {
        name: 'homePageThemeL',
        path: '/homePageThemeL',
        component: () => import('./theme/large/Home/HomePage.vue'),
        meta: {
            title: '首页',
            keepAlive: true
        }
    },
    {
        name: 'servicePageThemeL',
        path: '/servicePageThemeL',
        component: () => import('./theme/large/Home/ServicePage.vue'),
        meta: {
            title: '服务',
            keepAlive: false
        }
    },
    {
        name: 'mineThemeL',
        path: '/mineThemeL',
        component: () => import('./theme/large/Mine/MineHome.vue'),
    },
    {
        name: 'minePageThemeL',
        path: '/minePageThemeL',
        component: () => import('./theme/large/Home/MinePage.vue'),
        meta: {
            title: '我家',
            keepAlive: true
        }
    },
    // 新增投诉报修
    {
        name: 'complaintReportingAddThemeL',
        path: '/complaintReportingAddThemeL',
        component: () => import('@/theme/large/Services/ComplaintReporting/ComplaintReportingAdd.vue')
    },
    //选择小区
    {
        name: 'selectAreaThemeL',
        path:
            '/selectAreaThemeL',
        component:
            () => import('@/theme/large/Mine/SelectArea.vue')
    },
    {
        name: 'productPageThemeL', //优品首页
        path: '/productPageThemeL',
        component: () => import('./theme/large/superiorProduct/productPage.vue'),
        meta: {
            title: '优品',
            keepAlive: true
        }
    },
    {
        name: 'moreClassifyThemeL', //更多分类
        path: '/moreClassifyThemeL',
        component: () => import('./theme/large/superiorProduct/moreClassify.vue'),
        meta: {
            title: '更多分类',
            keepAlive: true
        }
    },
    {
        name: 'shopPageThemeL', //店铺首页
        path: '/shopPageThemeL',
        component: () => import('./theme/large/superiorProduct/shopPage.vue'),
        meta: {
            title: '店铺首页',
            keepAlive: true
        }
    },
    // 商品详情页
    {
        name: 'goodsDetailThemeL',
        path: '/goodsDetailThemeL',
        component: () => import('./theme/large/product/goodsDetail.vue'),
        meta: {
            keepAlive: true,
        }
    },
    //秒杀商品详情页
    {
        name: 'flashSaleGoodsThemeL',
        path: '/flashSaleGoodsThemeL',
        component: () => import('./theme/large/product/flashSaleGoods.vue'),
        meta: {
            keepAlive:true,
          }
    },
    {
        name: 'myPointsThemeL',
        path: '/myPointsThemeL',
        component: () => import('./theme/large/Mine/MyPoints.vue')
    },
];

export default {
    routerThemeL
};
