<template>
  <div style="height:100%;display:flex;overflow:hidden;flex-direction:column;">
    <!-- <van-nav-bar
      :style="naviStyle"
      title="我的收藏"
      :right-text="isDeleteState ? '完成' : '管理' "
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />-->
    <navigation-bar @onLeftClick="onClickLeft">
      <slot slot="nav-center" name="center">
        <div v-if="themeCurrent == '0'" slot="nav-center" class="collect-title">我的收藏</div>
        <div v-else-if="themeCurrent == '1'" slot="nav-center" class="collect-title-l">我的收藏</div>
      </slot>
      <slot slot="nav-right" name="right">
        <div
          v-if="themeCurrent == '0'"
          slot="right"
          @click="onClickRight"
          :style="naviStyle"
          class="right-nav"
        >{{isDeleteState ? '完成' : '编辑'}}</div>
        <div
          v-else-if="themeCurrent == '1'"
          slot="right"
          @click="onClickRight"
          :style="naviStyle"
          class="right-nav-l"
        >{{isDeleteState ? '完成' : '编辑'}}</div>
      </slot>
    </navigation-bar>
    <van-tabs
      style="flex:1;overflow:hidden;"
      v-model="currentTabRefName"
      @change="tabChanged"
      animated
    >
      <van-tab name="goods">
        <div slot="title" :class="themeCurrent == '1' ? 'tab-text-style-l' : 'tab-text-style'">商品</div>
        <goods-collect ref="goods" @select-changed="selectChangeAction" @resetDeleteState="resetDeleteState"/>
      </van-tab>
      <van-tab name="shop">
        <div slot="title" :class="themeCurrent == '1' ? 'tab-text-style-l' : 'tab-text-style'">店铺</div>
        <shop-collect ref="shop" @select-changed="selectChangeAction" @resetDeleteState="resetDeleteState"/>
      </van-tab>
      <van-tab name="article">
        <div slot="title" :class="themeCurrent == '1' ? 'tab-text-style-l' : 'tab-text-style'">文章</div>
        <article-collect ref="article" @select-changed="selectChangeAction" @resetDeleteState="resetDeleteState"/>
      </van-tab>
    </van-tabs>
    <div
      class="bottomView"
      :class="{showActive: isDeleteState}"
      :style="{paddingBottom:this.$footBarHeight+'px'}"
      v-show="isDeleteState"
    >
      <!-- 全选 -->
      <div class="bottom-view-left" :class="{noDataToggleStyle:isEmptyData}" @click="toggleAll">
        <van-checkbox checked-color="#FB5768" v-model="selectAll" @change="selectAllChangeAction"></van-checkbox>
        <span
          :class="themeCurrent == '1' ? 'bottom-view-left-text-style-l' : 'bottom-view-left-text-style'"
        >全选</span>
      </div>
      <!-- 取消收藏 -->
      <div class="bottom-view-right" :class="{noDataStyle:isEmptyData}" @click="deleteCollect">
        <span
          :class="themeCurrent == '1' ? 'bottom-view-right-text-style-l' : 'bottom-view-right-text-style'"
        >取消收藏</span>
      </div>
    </div>
  </div>
</template>

<script>
import ArticleCollect from "./ArticleCollect";
import ShopCollect from "./ShopCollect";
import GoodsCollect from "./GoodsCollect";
import NavigationBar from "@/common/currency/NavigationBar.vue";

export default {
  name: "MyCollect",
  data() {
    return {
      currentTabRefName: "goods",
      isDeleteState: false,
      selectAll: false,
      naviStyle: {
        "padding-top": this.$stbHeight + "px"
      },
      isEmptyData: false,
      themeCurrent: 0
    };
  },
  components: {
    ArticleCollect,
    ShopCollect,
    GoodsCollect,
    NavigationBar
  },
  methods: {
    onClickLeft() {
      if (this.isDeleteState) {
        this.onClickRight();
      } else {
        this.$router.back(-1);
      }
    },
    onClickRight() {
      if (this.$refs[this.currentTabRefName].list.length == 0 && !this.isDeleteState) {
        this.$toast("当前收藏列表无内容，不可编辑哦");
        return;
      }

      this.isDeleteState = !this.isDeleteState;
      this.$refs[this.currentTabRefName].isDeleteState = this.isDeleteState;
      this.selectAll = false;
      this.isEmptyData = this.$refs[this.currentTabRefName].list.length == 0;
    },
    resetDeleteState() {
      this.isDeleteState = false;
    },
    resetSelectState() {
      if (this.$refs.goods) {
        this.$refs.goods.isDeleteState = false;
      }
      if (this.$refs.shop) {
        this.$refs.shop.isDeleteState = false;
      }
      if (this.$refs.article) {
        this.$refs.article.isDeleteState = false;
      }
      console.log(this.currentTabRefName);
      if (this.$refs[this.currentTabRefName]) {
        this.$refs[this.currentTabRefName].isDeleteState = false;
        this.$refs[this.currentTabRefName].onRefresh();
      }
    },
    tabChanged() {
      this.isDeleteState = false;
      this.resetSelectState();
    },
    selectAllChangeAction() {
      if (this.$refs[this.currentTabRefName].list.length == 0) {
        if (this.selectAll) {
          
        }
        this.selectAll = false;
      }
      // console.log(selectAll);
      // this.$refs[this.currentTabRefName].selectAll = selectAll;
    },
    toggleAll() {
      this.selectAll = !this.selectAll;
      // debugger
      console.log(this.$refs[this.currentTabRefName]);
      this.$refs[this.currentTabRefName].selectAll(this.selectAll);
    },
    deleteCollect() {
      if (this.$refs[this.currentTabRefName].list.length == 0) {
        
        return;
      }
      this.$refs[this.currentTabRefName].uncollectMultiItem();
    },
    selectChangeAction(obj) {
      console.log(obj);
      this.selectAll = obj.isAllSelect;
    }
  },
  activated() {
    this.themeCurrent = this.$curTheme;
    this.tabChanged();
    console.log("22222222222222222");
    if (this.$refs[this.currentTabRefName]) {
      this.$refs[this.currentTabRefName].onRefresh();
    }
  }
};
</script>
<style  scoped>
/deep/.navBar .center .title {
  display: none;
}
.collect-title {
  margin: 0 auto;
  font-size: 20PX !important;
  font-family: PingFangSC-Medium;
  letter-spacing: -0.06PX !important;
  max-width: 6.66667rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.collect-title-l {
  margin: 0 auto;
  font-size: 20PX !important;
  font-family: PingFangSC-Medium;
  letter-spacing: -0.06PX !important;
  max-width: 6.66667rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.right-nav {
  font-size: 15px;
  padding-right: 13px;
  margin-left: -20px;
  display: flex;
  align-items: center;
  padding-top: 0px !important;
}
.right-nav-l {
  font-size: 17px;
  padding-right: 13px;
  margin-left: -20px;
  display: flex;
  align-items: center;
  padding-top: 0px !important;
}
.tab-text-style {
}
.tab-text-style-l {
  font-size: 17px;
}
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
.bottom-view-left {
  width: 50%;
  height: 50px;
  background: #ffffff;
  box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);

  display: flex;
  align-items: center;
  padding-left: 12px;
}

.bottom-view-right {
  width: 50%;
  height: 50px;
  background-image: linear-gradient(154deg, #ff9ba5 0%, #fb5364 57%);
  box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);
  text-align: center;
}
/deep/.van-tabs__line {
  width: 25px !important;
  height: 3px;
  border-radius: 1.5px;
  background-color: #fb5768;
}
/deep/.van-tabs {
  display: flex;
  flex-direction: column;
}
/deep/.van-ellipsis {
  font-size: 16px;
}
/deep/.van-tabs__nav--line {
  padding-bottom: 3px;
}
/deep/.van-tabs__content {
  flex: 1;
  /* overflow-y: auto; */
}
/deep/.van-hairline--top-bottom {
  border: 0 !important;
}
.bottomView {
  display: flex;
}
.noDataStyle {
  background: #999999;
} 
.noDataToggleStyle{

}

/deep/ .van-hairline--top-bottom::after {
  border-width: 0;
}
/deep/.van-tab__pane {
  height: 100%;
}
.bottom-view-left-text-style {
  font-size: 14px;
  color: #333333;
  margin-left: 10px;
}
.bottom-view-left-text-style-l {
  font-size: 17px;
  color: #333333;
  margin-left: 10px;
}
.bottom-view-right-text-style {
  font-size: 14px;
  color: #fff;
  line-height: 50px;
  height: 50px;
}
.bottom-view-right-text-style-l {
  font-size: 17px;
  color: #fff;
  line-height: 50px;
  height: 50px;
}
</style>