import { getQualityDetailService, getQualityGoodsService, getQualityListService, saveCollectService, getCollectService, deleteCollectService, getShareArticleUrlService, getReadInfoService } from '../../services/qualityService';
const state = {
    pageNumber: 1,
    scrollPosition: 0,
    newCommitType: false,
    articleFinished: false,
    qualityDetalPopupFlag: false,
    defaultQualityList: [],
}

// 定义 getters demo
var getters = {
    // refreshPage(state) {
    //     return state.commitType;
    // }
}

const actions = {
    getQualityGoodsAction(context, payload) {
        return new Promise((resolve, reject) => {
            getQualityGoodsService(payload.areaCode, payload.spuCodeList).then(article => {
                article.resultCode == 0 ? resolve(article.data) : reject(article.msg);
            }).catch(error => {
                reject(error);
            });
        });
    },
    //品质生活详情
    getQualityDetailAction(context, articleNo) {
        return new Promise((resolve, reject) => {
            getQualityDetailService(articleNo).then(article => {
                if (article.resultCode == '10001') {
                    let content = article.data.content;
                    // let b = article.data.content;
                    let goodsIdArr = content.match(/GOOD_ID=[a-z0-9]+/ig);
                    if (goodsIdArr && goodsIdArr.length > 0) {
                        let IdArr = [];
                        goodsIdArr.forEach((goods, index) => {
                            IdArr.push(goods.replace(/GOOD_ID=/, ''))
                        });
                        article.data.goodsIdArr = IdArr;
                        }
                    else {
                        article.data.goodsIdArr = [];
                    }
                    // let splitArr = b.split(/<a href="GOOD_ID=[a-z0-9]+"><\/a>/);\
                    // let splitArr = b.split(/&lt;a&nbsp;href=&quot;GOOD_ID=[a-z0-9]+&quot;&gt;&lt;\/a&gt;/)
                    // let splitArr = b.split(/(GOOD_ID=[a-z0-9]+)/)
                    // console.log(goodsIdArr)
                    // console.log(splitArr)
                    // if (goodsIdArr && goodsIdArr.length) {
                    //     let matcher = [];
                    //     goodsIdArr.forEach((goods, index) => {
                    //         // console.log(index)
                    //         matcher.push({
                    //             goodsId: goods.replace(/GOOD_ID=/, ''),
                    //             htmlStr: index == 0 ? splitArr[0] : splitArr[index * 2],
                    //         })
                    //     });
                    //     if (splitArr.length > goodsIdArr.length) {
                    //         matcher.push({
                    //             htmlStr: splitArr[goodsIdArr.length * 2],
                    //         })
                    //     }
                    //     article.data.goodsIdArr = matcher;
                    // } else {
                    //     article.data.goodsIdArr = [{
                    //         htmlStr: article.data.content
                    //     }]
                    // }
                    // console.log(article.data)
                    resolve(article.data);
                } else {
                    reject(article.msg);
                }

            }, fail => {
                reject(fail);
            })
        })
    },

    //品质生活列表数据
    getQualityListAction(context, page) {
        // if (isRefresh) {
        //     context.commit('qualityNumChange', true);
        // }
        return new Promise((resolve, reject) => {
            getQualityListService(page).then(list => {
                if (page > list.data.pages) {
                    context.state.articleFinished = true;
                    return;
                } else {
                    context.state.articleFinished = false;
                }
                if (list.resultCode == '10001') {
                    let newList = list.data.list.map(item => {
                        // let randomValue = parseInt(Math.random() * (200 - 20 + 1) + 20, 10);
                        // let rate = Math.random() * (2 - 1 + 1) + 1;
                        let tmpItem =  {
                            src: item.picUrl,
                            title: item.title,
                            articleNo: item.articleNo,
                            sorts: convertContent(item.content),
                            width: 100,
                            height: 100,
                        };
                        if (item.picWidth) {
                            tmpItem.width = item.picWidth;
                            tmpItem.height = item.picLength;
                        }
                        return tmpItem;
                    });
                    list.data.articleList = newList;
                    // list.data.
                    // context.commit('qualityNumChange');
                    resolve(list.data)
                } else {
                    reject(list.msg);
                }
            }).catch(error => {
                reject(error);
            });
        });

        function convertContent(html) {
            //匹配html标签的正则表达式，"g"是搜索匹配多个符合的内容
            let re1 = new RegExp("<.+?>", "g");
            //执行替换成空字符
            let msg = html.replace(re1, '');
            msg = msg.replace(/GOOD_ID=[a-z0-9]+/ig, '').replace(/&nbsp;/ig, '')
            return msg;
        };
    },

    saveCollectAction(context, payload) {
        return new Promise((resolve, reject) => {
            let { collectType, contentCode, userCode } = payload;
            saveCollectService(collectType, contentCode, userCode).then(result => {
                result.resultCode == 0 ? resolve(result.data) : reject(result.msg);

            }).catch(error => {
                reject(error);
            });
        })
    },
    getCollectAction(context, payload) {
        return new Promise((resolve, reject) => {
            let { collectType, contentCode, userCode } = payload;
            getCollectService(collectType, contentCode, userCode).then(result => {
                resolve(result.data.list);
            }).catch(error => {
                reject(error);
            });
        })
    },

    deleteCollectAction(context, collcetions) {
        return new Promise((resolve, reject) => {
            deleteCollectService([collcetions]).then(result => {
                result.resultCode == 0 ? resolve(result.data) : reject(result.msg);
            }).catch(error => {
                reject(error);
            });
        })
    },

    getShareArticleUrlAction(context, data) {
        return new Promise((resolve, reject) => {
            getShareArticleUrlService(data).then(result => {
                resolve(result)
            }).catch(error => {
                reject(error);
            });
        })
    },

    getReadInfoAction(context, data) {
        return new Promise((resolve, reject) => {
            getReadInfoService(data).then(result => {
                resolve(result)
            }).catch(error => {
                reject(error);
            });
        })
    },

    setQualityDetalPopupFlag(context, flag) {
        context.commit('setQualityDetalPopupFlag', flag);
    },

    setDefaultQualityList(context, arr) {
        context.commit('setDefaultQualityList', arr);
    },
}

const mutations = {
    //品质生活当前数据分页数
    // qualityNumChange(state, isRefresh) {
    //     if (isRefresh) {
    //         state.pageNumber = 1;
    //     } else {
    //         state.pageNumber += 1;
    //     }

    // },
    //记录findpage页面滚动位置
    saveFindScollPosition(state, position) {
        state.scrollPosition = position;
    },

    setQualityDetalPopupFlag(state, flag) {
        state.qualityDetalPopupFlag = flag;
    },
    setDefaultQualityList(state, arr) {
        state.defaultQualityList = arr;
    }
}
// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}