<template>
  <div :class="{themeL:bindThemeL}">
    <div class="cellItem" v-for="(item,index) in list" :key="index">
      <!-- 成员信息 -->
      <div class="info">
        <div style="display:flex;">
          <div style="display:flex;align-items:center;margin-right:10px;">
            <img
              style="width:38px;height:38px;border-radius:29px;"
              :src="item.PIC_URL.length == 0 ? require('../../assets/images/default/user_default_avata.png') : item.PIC_URL"
            />
          </div>
          <div style="display:flex;flex-direction:column;max-width:70%">
            <span class="memberName">{{item.OWNER_NICK_NAME}}</span>
            <span class="phoneNum">{{item.PHONE}}</span>
          </div>
        </div>
        <div style="display:flex;align-items:center;min-width: 30%" v-if="item.APPROVAL_STATUS == 0">
          <img
            style="width:50px;height:34px;margin-right: 5px;"
            src="../../assets/images/personal_center/user_member_icon_decline.png"
            @click="rejectClick(item)"
          />
          <img
            style="width:50px;height:35px;margin-right: 5px;"
            src="../../assets/images/personal_center/user_member_icon_pass.png"
            @click="passClick(item)"
          />
        </div>
        <div
          style="height:25px;font-size:14px;color:#888888;min-width: 20%"
          :style="{fontSize:stateSize}"
          v-if="item.APPROVAL_STATUS == 1"
        >{{item.APPROVAL_RESULT == 2 ? "已拒绝" : "已通过" }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { approvalMember } from "../../services/MineService";
import { Toast, Dialog } from "vant";
export default {
  name: "MemberApply",
  data() {
    return {
      // list: [],
      isDeald: false,
      isPass: true,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      stateSize: this.$curTheme==0?"14px":"16px"
    };
  },
  props: {
    list: {
      default: [],
      type: Array
    }
  },
  methods: {
    rejectClick(item) {
      Dialog.confirm({
        title: "确定审核？",
        confirmButtonColor: "#FB5768"
      })
        .then(() => {
          this.rejectAction(item);
        })
        .catch(() => {});
    },
    rejectAction(item) {
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        APPROVAL_RESULT: "2",
        UUID: item.UUID
      };
      approvalMember(param)
        .then(data => {
          this.$emit("reload-data");
        })
        .catch(error => {
          // Toast(error);
        });
    },
    passClick(item) {
      Dialog.confirm({
        title: "确定审核？",
        confirmButtonColor: "#FB5768"
      })
        .then(() => {
          this.confirmAction(item);
        })
        .catch(() => {});
    },
    confirmAction(item) {
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        APPROVAL_RESULT: "1",
        UUID: item.UUID
      };
      approvalMember(param)
        .then(data => {
          this.$emit("reload-data");
        })
        .catch(error => {
          // Toast(error);
        });
    }
  }
};
</script>

<style scoped>
.cellItem {
  background-color: #ffffff;
  margin: 15px;
  border-radius: 7px;
}
.info {
  height: 64px;
  display: flex;
  padding-right: 12px;
  padding-left: 12px;
  justify-content: space-between;
  align-items: center;
}

.memberName {
  font-weight: bold;
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
}
.themeL .memberName {
  font-size: 17px;
  height: 30px;
  line-height: 30px;
}

.phoneNum {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #888888;
  letter-spacing: -0.04px;
}
.themeL .phoneNum {
  font-size: 15px;
}

.applyResultText {
  font-size: 12px;
  color: #888888;
  letter-spacing: -0.04px;
}
</style>