import axios from "axios";
import { Dialog } from "vant";
import qs, { stringify } from "qs";
import config from "./common/config";
import { store, storage } from "./utils/storage";

let isFormHeader = false;

//重试队列，每一项将是一个待执行的函数形式
let retryRequests = [];
//是否正在刷新标记
let isRefreshing = false;

let loading; //定义loading变量
let needLoadingRequestCount = 0;
let accountShowLoading = true;
let http = axios.create({
    baseURL: config.baseUrl,
    withCredentials: false,
    timeout: 15000,//默认15s超时
    headers: {
        "Content-Type": "application/json;charset=utf-8",
         "Authorization":getLocalToken(),
    },
    /*transformRequest: [function (data) {
        if (isFormHeader) {
            return qs.stringify(data);
        } else {
            return JSON.stringify(data);
        }
    }]*/
});
function startLoading() {

    loading = store.get("showLoadingView");
    if (loading) {
        loading(true);
    }
    // loading = Toast.loading({
    //     duration: 0,
    //     mask: false,
    //     message: ''
    // });
}

function endLoading() {

    if (loading) {
        loading(false);
    }

    // loading.clear();
}

function showFullScreenLoading() {
    if (needLoadingRequestCount === 0 && accountShowLoading) {
        startLoading();
    }
    needLoadingRequestCount++;
}

function tryHideFullScreenLoading() {
    if (needLoadingRequestCount <= 0) return;
    needLoadingRequestCount--;
    if (needLoadingRequestCount === 0) {
        endLoading();
    }
}
function logOut(msg) {
    let dialogMsg = msg;
    Dialog.alert({
        message: dialogMsg
    }).then(() => {
        window.logOut();
    });
}
function getLocalToken() {
    const token = storage.getStorage("accessToken", false);
    return token;
}
http.interceptors.request.use(
    config => {
        showFullScreenLoading();
        /*if (isFormHeader) {
            config.headers["Content-Type"] = "application/x-www-form-urlencoded;charset=utf-8";
        }*/
        if (config.method == "get") {
            delete config.headers["Content-Type"];
        }
        return config;
    },
    error => {
        console.log("http请求拦截1: error = "+error);
    }
);

http.interceptors.response.use(
    response => {
        tryHideFullScreenLoading();
        accountShowLoading = true;
        return response;
    },
    error => {
        accountShowLoading = true;
        let resultCode = (error.response && error.response.data) ? error.response.data.resultCode : -1;
        if (resultCode == 30130){
                const config = error.response.config;
                if (!isRefreshing) {
                    isRefreshing = true;
                    let refreshToken = storage.getStorage("refreshToken", false);
                    return apiAxios("POST", "xwj-sys-auth/oauth/token", { refreshToken }, "baseUrl", false)
                        .then(function(res) {
                            let resStatus = res.resultCode;
                            if (resStatus == '0') {
                                const token = res.data.accessToken
                                storage.setStorage("accessToken", res.data.accessToken);
                                storage.setStorage("refreshToken", res.data.refreshToken,30);
                                config.headers["Authorization"] = token;
                                retryRequests.forEach(cb => cb(token));
                                retryRequests = [];
                                return http(config);
                            } else {
                                logOut("当前账号登录状态已过期，请重新登录");
                            }
                            isRefreshing = false;
                        }
                        )
                        .catch(function(err) {
                        logOut("当前账号登录状态已过期，请重新登录");
                        })
                        .finally(function() {
                            isRefreshing = false
                        });
                } else{
                    return new Promise((resolve) => {
                        retryRequests.push((token) => {
                            const newToken = storage.getStorage("accessToken", false);
                            config.headers["Authorization"] = newToken;
                            resolve(http(config));
                        });
                    }).catch(function(reason) {
                      });
                }
        }else if(resultCode == 30131){
            logOut("账号在别的设备上登录," +' \n '+" 当前设备下线");
        }else if(resultCode == 30132||resultCode == 30135||resultCode == 30136||resultCode == 30137){
            logOut("当前账号登录状态已过期，" +' \n '+"请重新登录");
        }
        return Promise.reject(error);
    }
);

function apiAxios(method, url, params, serviceType = "baseUrl", isFormHeader = false) {
    // serviceType = serviceType === null ? baseUrl : serviceType;
    //iOS权限判断
    if (window.showNetDialog) {
        window.showUnNetPrivacy()
    }
    if(serviceType === null){
        serviceType = "baseUrl";
    }
    return new Promise((resolve, reject) => {
        //判断当前是否有网络
        if (!window.navigator.onLine) {
            reject('网络异常，请稍后重试！');
            return;
        }
        let reqUrl;
        if (/http|https/.test(url)) {
            reqUrl = url;
        } else {
            let { baseUrl, homeUrl } = store.get('custUrl');
            reqUrl = serviceType === 'baseUrl' ? baseUrl + url : homeUrl + url;
        }
        http({
            method: method,
            url: reqUrl,
            //data: method === "POST" || method === "PUT" ? params : null,
            data: method === "POST" || method === "PUT" ? (isFormHeader ? qs.stringify(params):JSON.stringify(params)) : null,
            params: method === "GET" || method === "DELETE" ? params : null,
            headers: {
                "Content-Type": isFormHeader ? "application/x-www-form-urlencoded;charset=utf-8" : "application/json;charset=utf-8",
                "Authorization":getLocalToken()
            }
        })
            .then(function (res) {
                tryHideFullScreenLoading();
                if (res !== undefined){
                    if (res.status === 200) { //http 响应状态是200为正常         //  && res.data.status 这个不一定有
                        resolve(res.data);
                    } else if (res.status === 200 && !res.data.status) {
                        resolve(res.data);
                    } else {
                        reject(res.message);
                    }
                }else {
                    reject(res);
                }
            })
            .catch(function (err) {
                tryHideFullScreenLoading();
                // 服务连接失败err.message为"Network Error", 连接超时（就是超过设定的timeout的时间）err.message为timeout of 3000ms exceeded
                if ("Network Error" == err.message || "timeout of 15000ms exceeded" == err.message) {
                    reject("当前网络状况不佳");
                } else {
                    reject(err.message || err);
                }
            });
    });
}

export default {
    get: function (url, params, servceType,accountLoading = true) {
        //isFormHeader = true;
        accountShowLoading = accountLoading;
        return apiAxios("GET", url, params, servceType, false);
    },
    post: function (url, params, isFormData, servceType,accountLoading = true) {
        //isFormHeader = isFormData;
        accountShowLoading = accountLoading;
        return apiAxios("POST", url, params, servceType, isFormData);
    },
    put: function (url, params, servceType) {
        return apiAxios("PUT", url, params, servceType, false);
    },
    delete: function (url, params, servceType) {
        return apiAxios("DELETE", url, params, servceType, false);
    }
};
