import store from "../../../store";
import { Toast } from "vant";
import { Base64 } from "js-base64";
//状态 0 未开始，1，正在观看
export default {
  //进入直播
  join() {
    if (store.state.store_live.live.status > 0) {
      Toast("正在观看中");
      return;
    }
    const live = {
      status: 1,
    };
    store.commit("updateLive", live);
    this.registerListener();
    this.watch();
  },
  //结束直播
  quit() {
    if (store.state.store_live.live.status == 1) {
      this.unregisterListener();
      this.reset();
      this.stopWatch();
    }
  },
  //重置直播状态
  reset() {
    const live = {
      status: 0,
      roomId: "",
    };
    store.commit("updateLive", live);
  },
  //开始观看直播
  watch() {
    var param = {
      serverUrl: "https://meeting-test.baijiayun.com",
      appKey: "Bmwqzi6O7hWaiPsmM6",
      roomId: store.state.store_live.live.roomId,
      userId: store.state.store_live.ownerCode,
      userName: Base64.encode(store.state.store_live.userName),
      roomPassword: "123456",
    };
    // eslint-disable-next-line no-undef
    HisenseLive.watch(
      JSON.stringify(param),
      function(info) {
        Toast("watch success:" + info);
      },
      function(error) {
        Toast("watch error:" + error);
      }
    );
  },
  //结束观看退出房间
  stopWatch() {
    this.unregisterListener();
    // eslint-disable-next-line no-undef
    HisenseLive.stopWatch(
      function(info) {
        Toast("stopWatch success:" + info);
      },
      function(error) {
        Toast("stopWatch error:" + error);
      }
    );
  },
  //监听操作
  liveListener(info) {
    var infoObj = JSON.parse(info);
    if (infoObj.type == "ROOM_NEW_DANMU") {
      var danmu = JSON.parse(infoObj.content);
      store.commit("addDanmu", {
        userId: danmu.userId,
        userName: danmu.userName,
        msg: danmu.msg,
      });
    } else {
      // 收到状态回调
      var now = new Date();
      var logMsg =
        now.getHours() +
        ":" +
        now.getMinutes() +
        ":" +
        now.getMinutes() +
        " - " +
        info;
      store.commit("addLog", logMsg);
    }
  },
  //注册监听
  registerListener() {
    // eslint-disable-next-line no-undef
    HisenseLive.registerListener(this.liveListener, function(error) {
      Toast("registerListener error:" + error);
    });
  },
  //解除监听
  unregisterListener() {
    // eslint-disable-next-line no-undef
    HisenseLive.unregisterListener(this.liveListener, function(error) {
      Toast("registerListener error:" + error);
    });
  },
  //设置小窗显示
  setDisplayMode(mode) {
    // eslint-disable-next-line no-undef
    HisenseLive.setDisplayMode(
      mode,
      function(info) {
        Toast("小窗显示成功:" + info);
      },
      function(error) {
        Toast("小窗显示失败:" + error);
      }
    );
  },
};
