export default {
  state: {
    ownerCode: "2020050518232992560", // 物业
    userName: "测试用户1",
    areaCode: "A2018012300015",
    live: {
      status: 0, //状态 0 未开始，1，正在观看
      roomId: "",
      danmuList: [],
    },
    logs: [],
  },
  mutations: {
    updateLive(state, paylod) {
      state.live = {
        ...state.live,
        ...paylod,
      };
    },
    //更新选中区域
    updateAreaCode(state, areaCode) {
      state.areaCode = areaCode;
    },
    addLog(state, log) {
      state.logs.push(log);
    },
    addDanmu(state, danmu) {
      state.live.danmuList.push(danmu);
    },
  },
  actions: {},
  modules: {},
};
