<template>
  <div style="height:100%;display: flex;flex-direction: column;" class="find-box" :class="themeCurrent == '1' ? 'life-large' : ''">
    <!-- <navigation-bar pageName="发现" :isShowBack="false">
      <slot slot="nav-right" name="right">
        <div style="display: flex; justify-content: center; align-items: center;" @click="addNote">
          <img class="nav-bar-custom-icon" src="../../assets/images/find/camera.png" alt />
        </div>
      </slot>
    </navigation-bar>-->
    <div class="topPlaceHolder"> </div>
    <van-tabs
      v-model="tabIndex"
      @change="tabChange"
      swipeable
      :border="false"
      line-height="3"
      line-width="25"
      color="#FB5768"
    >
      <slot slot="nav-left">
        <div style="display: flex; justify-content: flex-start; align-items: center; width: 70px; padding-left: 15px;box-sizing: border-box" @click="goMyNote">
          <img class="top-tabsolt-icon " src="../../assets/images/find/find_meIcon.png" alt v-show="tabIndex == '0'"/>
        </div>
      </slot>
      <slot slot="nav-right">
        <div style="display: flex; justify-content: flex-end; align-items: center; width: 70px; padding-right: 15px;box-sizing: border-box" @click="addNote">
          <img class="top-tabsolt-icon " src="../../assets/images/find/camera.png" alt v-show="tabIndex == '0'"/>
        </div>
      </slot>
      <van-tab title="邻里互动" name="0">
        <div class="message-box" v-if="mineMessageCount">
          <div :class="themeCurrent == '0' ? 'message-notify' : 'message-notify-l'" @click="goNotifyList">
            <span>您有{{mineMessageCount}}条新消息</span>
            <van-icon name="arrow" color="#fff" />
          </div>
        </div>
        <div id="refreshWrapper" ref="refreshWrapper" @click="replyInputShow = false">
          <van-pull-refresh v-model="isLoading" @refresh="refresh">
            <div class="wrapper" v-show="!networkError && themeCurrent == '0'">
              <van-list
                v-model="loading"
                :finished="finished"
                @load="loadMoreNote"
                :offset="1000"
                :immediate-check="false"
              >
                <!--header banner-->
                <!-- <div class="banner" :style="background">
                  <div class="user-msg" @click="goMyNote">
                    <p>{{userInfo.OWNER_NICK_NAME}}</p>
                    <img v-if="userInfo.PIC_URL" class="user-avatar" :src="userInfo.PIC_URL" alt />
                    <img v-else class="user-avatar" src="../../assets/images/find/photo.png" alt />
                  </div>
                </div> -->
                <div class="findTopLine"></div>
                <!--content-->
                <div class="no-list" v-show="!noteData.length && !loading">
                  <img src="../../assets/images/find/img_find_default.png" alt />
                  <div>暂时没有内容哦</div>
                </div>
                <!-- 加载的内容-->
                <section
                  class="friend-message"
                  v-for="(item,m) in noteData"
                  v-bind:key="m"
                  :ref="'noteId' + m"
                >
                  <div v-show="m != 0" style="background-color: #E5E5E5; height: 1px; width: 100%; margin-bottom:15px"></div>
                  <div style="padding: 0 15px;"> 
                    <div class="friend-msg" v-if="item">
                      <div>
                        <img v-if="item.headPic" :src="item.headPic" alt class="head" />
                        <img v-else src="../../assets/images/find/photo.png" alt class="head" />
                        <p>{{item.nickName}}</p>
                        <img
                          v-if="item.noteType == 1"
                          src="../../assets/images/find/property.png"
                          class="property"
                        />
                      </div>
                      <van-icon size="0.53333rem" name="ellipsis" @click="reportMenu(item)" />
                    </div>
                    <div class="message-container" v-if="item">
                      <p>{{item.content}}</p>
                      <van-row
                        class="message-img"
                        v-bind:class="{'big-image': item.notePic && item.notePic.length == 1}"
                        v-if="item.notePic"
                      >
                        <van-col
                          span="6"
                          v-for="(imgurl,index) in item.notePic"
                          v-bind:key="index"
                          v-show="index < 4"
                        >
                          <img @click="preview(item.notePic,index)" alt :src="imgurl" :key="imgurl" />
                        </van-col>
                      </van-row>
                    </div>
                    <div class="handler-container" v-if="item">
                      <van-row span="8" type="flex" justify="space-between">
                        <van-col class="desc">
                          <span>{{item.createdTime}}</span>
                          <span
                            v-if="item.userCode === userInfo.OWNER_CODE"
                            @click="deleteNote(item.noteCode)"
                          >删除</span>
                        </van-col>
                        <van-col span="8" class="handle-type" @click.stop>
                          <span
                            @click="isFirst ? setLike(item.noteCode,m,item.likerList) : ''"
                            style="padding-left: 15px"
                          >
                            <img
                              src="../../assets/images/find/unlove_icon.png"
                              alt
                              v-if="checkLike(item.likerList)"
                            />
                            <img src="../../assets/images/find/love_icon.png" alt v-else />
                            <span v-if="item.likerList">{{item.likerList.length}}</span>
                          </span>
                          <span @click="isFirst ? udClick(null,item.noteCode,m) : ''">
                            <img src="../../assets/images/find/com_icon.png" alt />
                            {{item.commentList ? item.commentList.length : 0}}
                          </span>
                        </van-col>
                      </van-row>
                    </div>
                    <div
                      class="reply-container"
                      v-if="item && (item.likerList && item.likerList.length > 0 || item.commentList && item.commentList.length > 0)"
                    >
                      <div class="goods-group" v-if="item.likerList && item.likerList.length > 0">
                        <img src="../../assets/images/find/praise_icon.png" alt />
                        <span v-for="(ele,a) in item.likerList" v-bind:key="a">
                          {{ele.nickName}}
                          <i v-if="a != item.likerList.length-1">,</i>
                        </span>
                      </div>
                      <div v-if="item.commentList.length > 0" class="reply-group-box">
                        <div
                          type="flex"
                          class="reply-group"
                          v-for="(eva,d) in item.commentList"
                          v-bind:key="d"
                        >
                          <div class="reply-item" @click.stop>
                            <div
                              class="reply-desc"
                              @click="isFirst ? udClick(eva,item.noteCode,m,false) : ''"
                            >
                              <span class="reply-name">{{eva.nickName}}</span>
                              <img
                                v-if="eva.isProperty == 1"
                                src="../../assets/images/find/property.png"
                              />
                              <span class="result" v-if="eva.returner">回复</span>
                              <span class="reply-name">{{eva.returner}}</span>
                              <img
                                v-if="eva.returnerType == 1"
                                src="../../assets/images/find/property.png"
                              />:
                              <span>{{eva.content}}</span>
                            </div>
                            <div class="eval-pic-box" v-show="eva.evalPic && eva.evalPic.length > 0">
                              <div v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                                <img
                                  @click="preview(eva.evalPic,c)"
                                  :src="ind"
                                  :key="ind"
                                  class="small-reply-img"
                                  alt
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <van-row></van-row>
                    </div>
                  </div>
                  
                </section>
                <div style="height:600px;width: 100%; background: #ffffff;" v-show="replyInputShow"></div>
                <!-- <div class="bottom-box" v-show="finished && noteData.length > 0">
              <img src="@/assets/images/mall/mall_logo.png" alt="">
                </div>-->
              </van-list>

              <!-- 页面底部logo -->
              <div class="finishedBox" v-show="finished && noteData.length > 0">
                <img src="../../assets/images/mall/mall_logo.png" alt />
              </div>
            </div>
            <div class="wrapper-l" v-show="!networkError && themeCurrent == '1'">
              <van-list
                v-model="loading"
                :finished="finished"
                @load="loadMoreNote"
                :offset="1000"
                :immediate-check="false"
              >
                <!--header banner-->
                <!-- <div class="banner-l" :style="background">
                  <div class="user-msg-l" @click="goMyNote">
                    <p>{{userInfo.OWNER_NICK_NAME}}</p>
                    <img v-if="userInfo.PIC_URL" class="user-avatar" :src="userInfo.PIC_URL" alt />
                    <img v-else class="user-avatar-l" src="../../assets/images/find/photo.png" alt />
                  </div>
                </div> -->
                <div class="findTopLine"></div>
                <!--content-->
                <div class="no-list-l" v-show="!noteData.length && !loading">
                  <img src="../../assets/images/find/img_find_default.png" alt />
                  <div>暂时没有内容哦</div>
                </div>
                <!-- 加载的内容-->
                <section
                  class="friend-message-l"
                  v-for="(item,m) in noteData"
                  v-bind:key="m"
                  :ref="'noteId' + m"
                >
                  <div style="padding: 0 15px;">
                     <div v-show="m != 0" style="background-color: #E5E5E5; height: 1px; width: 100%; margin-bottom:15px"></div>
                    <div class="friend-msg-l" v-if="item">
                      <div>
                        <img v-if="item.headPic" :src="item.headPic" alt class="head" />
                        <img v-else src="../../assets/images/find/photo.png" alt class="head" />
                        <p>{{item.nickName}}</p>
                        <img
                          v-if="item.noteType == 1"
                          src="../../assets/images/find/property.png"
                          class="property-l"
                        />
                      </div>
                      <van-icon size="0.53333rem" name="ellipsis" @click="reportMenu(item)" />
                    </div>
                    <div class="message-container-l" v-if="item">
                      <p>{{item.content}}</p>
                      <van-row
                        class="message-img-l"
                        v-bind:class="{'big-image-l': item.notePic && item.notePic.length == 1}"
                        v-if="item.notePic"
                      >
                        <van-col
                          span="6"
                          v-for="(imgurl,index) in item.notePic"
                          v-bind:key="index"
                          v-show="index < 4"
                        >
                          <img @click="preview(item.notePic,index)" alt :src="imgurl" :key="imgurl" />
                        </van-col>
                      </van-row>
                    </div>
                    <div class="handler-container-l" v-if="item">
                      <van-row span="8" type="flex" justify="space-between">
                        <van-col class="desc">
                          <span>{{item.createdTime}}</span>
                          <span
                            v-if="item.userCode === userInfo.OWNER_CODE"
                            @click="deleteNote(item.noteCode)"
                          >删除</span>
                        </van-col>
                        <van-col span="8" class="handle-type" @click.stop>
                          <span
                            @click="isFirst ? setLike(item.noteCode,m,item.likerList) : ''"
                            style="padding-left: 15px"
                          >
                            <img
                              src="../../assets/images/find/unlove_icon.png"
                              alt
                              v-if="checkLike(item.likerList)"
                            />
                            <img src="../../assets/images/find/love_icon.png" alt v-else />
                            <span v-if="item.likerList">{{item.likerList.length}}</span>
                          </span>
                          <span @click="isFirst ? udClick(null,item.noteCode,m) : ''">
                            <img src="../../assets/images/find/com_icon.png" alt />
                            {{item.commentList ? item.commentList.length : 0}}
                          </span>
                        </van-col>
                      </van-row>
                    </div>
                    <div
                      class="reply-container-l"
                      v-if="item && (item.likerList && item.likerList.length > 0 || item.commentList && item.commentList.length > 0)"
                    >
                      <div class="goods-group-l" v-if="item.likerList && item.likerList.length > 0">
                        <img src="../../assets/images/find/praise_icon.png" alt />
                        <span v-for="(ele,a) in item.likerList" v-bind:key="a">
                          {{ele.nickName}}
                          <i v-if="a != item.likerList.length-1">,</i>
                        </span>
                      </div>
                      <div v-if="item.commentList.length > 0" class="reply-group-box-l">
                        <div
                          type="flex"
                          class="reply-group-l"
                          v-for="(eva,d) in item.commentList"
                          v-bind:key="d"
                        >
                          <div class="reply-item-l" @click.stop>
                            <div
                              class="reply-desc-l"
                              @click="isFirst ? udClick(eva,item.noteCode,m,false) : ''"
                            >
                              <span class="reply-name-l">{{eva.nickName}}</span>
                              <img
                                v-if="eva.isProperty == 1"
                                src="../../assets/images/find/property.png"
                              />
                              <span class="result-l" v-if="eva.returner">回复</span>
                              <span class="reply-name-l">{{eva.returner}}</span>
                              <img
                                v-if="eva.returnerType == 1"
                                src="../../assets/images/find/property.png"
                              />:
                              <span>{{eva.content}}</span>
                            </div>
                            <div
                              class="eval-pic-box-l"
                              v-show="eva.evalPic && eva.evalPic.length > 0"
                            >
                              <div v-for="(ind,c) in eva.evalPic" v-bind:key="c">
                                <img
                                  @click="preview(eva.evalPic,c)"
                                  :src="ind"
                                  :key="ind"
                                  class="small-reply-img-l"
                                  alt
                                />
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                      <van-row></van-row>
                    </div>
                  </div>
                </section>
                <div style="height:600px;width: 100%; background: #ffffff;" v-show="replyInputShow"></div>
                <!-- <div class="bottom-box" v-show="finished && noteData.length > 0">
              <img src="@/assets/images/mall/mall_logo.png" alt="">
                </div>-->
              </van-list>

              <!-- 页面底部logo -->
              <div class="finishedBox" v-show="finished && noteData.length > 0">
                <img src="../../assets/images/mall/mall_logo.png" alt />
              </div>
            </div>
            <div style="height:300px;padding-top: 50px;" v-show="networkError">
              <NetworkError></NetworkError>
            </div>
          </van-pull-refresh>
        </div>
      </van-tab>
      <van-tab title="品质生活" name="1">
        <div id="qualityLife">
          <van-pull-refresh v-model="refreshing" @refresh="onRefreshLife">
            <div class="lifeTopLine"></div>
             <!-- && themeCurrent == '0' -->
            <div class="wrapper" v-show="!networkError">
              <van-list
                ref="waterFallList"
                v-model="lifeLoading"
                :finished="lifeFinished"
                finished-text
                @load="addQualityLifeData"
                style="width:100%;"
                :offset="1000"
                :immediate-check="false"
              >
                <waterfall
                  line="v"
                  :line-gap="lifeItemW"
                  :watch="lifeItems"
                  @reflowed="reflowed"
                  ref="waterfall"
                  :style="waterfallStyle"
                  :fixed-height="true"
                >
                  <waterfall-slot
                    v-for="(item, index) in lifeItems"
                    :width="lifeItemW"
                    :height="waterfallSlotHeight(item)"
                    :order="index"
                    :key="item.articleNo + index"
                    move-class="item-move"
                    :style="waterFallSoltStyle"
                  >
                    <div class="life-item-box" @click="clickedLifeItem(item)">
                      <div class="life-item-pic">
                        <!-- <img v-lazy="item.src" alt="" :key="item.src"> -->
                        <img :src="item.src" alt />
                      </div>
                      <div class="life-title" :style="lifeTitleStyle">{{item.title}}</div>
                    </div>
                  </waterfall-slot>
                </waterfall>
              </van-list>
              <!-- 页面底部logo -->
              <div class="finishedBox" v-show="lifeFinished && lifeItems.length != 0">
                <img src="@/assets/images/mall/mall_logo.png" alt />
              </div>
            </div>
            <div style="height:300px;padding-top: 50px;" v-show="networkError">
              <NetworkError></NetworkError>
            </div>
          </van-pull-refresh>
        </div>
      </van-tab>
    </van-tabs>

    <div class="reply-wrapper" v-show="replyInputShow" ref="myReply">
      <div class="reply-box">
        <div class="box">
          <input
            type="text"
            :placeholder="commitType == 'reply' && replys ? '回复 ' + replys.nickName + ':' : '写下你此刻的想法'"
            autofocus
            ref="myInput"
            v-model="replyValue"
            maxlength="200"
          />
          <van-button
            slot="button"
            size="small"
            type="primary"
            @click="submitInput"
            color="#fb5768"
          >发送</van-button>
        </div>
      </div>
    </div>
    <van-action-sheet
      v-model="reportShow"
      :actions="reportActions"
      cancel-text="取消"
      round
      close-on-click-action
      safe-area-inset-bottom
      @select="reportAction"
      :style="{ color: '#FB5768' }"
    />
    <van-action-sheet
      v-model="deleteShow"
      :actions="deleteActions"
      cancel-text="取消"
      round
      close-on-click-action
      safe-area-inset-bottom
      @select="delCommentAction"
      :style="{ color: '#FB5768' }"
    />
    <Preview :images="images" ref="myPreview" @indexChange="indexChange" v-if="isShowPreview"></Preview>
  </div>
</template>

<script>
import { store } from "../../utils/store";
import { mapActions, mapState, mapGetters } from "vuex";
import typeJudge from "../../utils/typeJudge";
import Preview from "@/components/preview/Preview.vue";
import NetworkError from "../../common/networkError/NetworkError";

//瀑布流组件
import Waterfall from "../../components/vue-waterfall/waterfall";
import WaterfallSlot from "../../components/vue-waterfall/waterfall-slot";

import Vue from "vue";
import { ImagePreview, Toast, Dialog, Tab, Tabs } from "vant";
Vue.use(Tab)
  .use(Tabs)
  .use(Dialog)
  .use(ImagePreview)
  .use(Toast);

/***
 * 计算字符串size使用
 */
let canvas = document.createElement("canvas");
canvas.style.positon = "ablsolute";
let ctx = canvas.getContext("2d");

export default {
  name: "Findpage",
  created() {
    //品质生活
    this.lifeItemW = (window.innerWidth - (this.waterfallLF*2)) * 0.5;
  },
  updated () {
    //android顶部刘海屏适配
    this.$nextTick(()=>{
      if (this.$devicePlatform == 'android' || this.$devicePlatform == 'pc') {
        let el = document.querySelector(".topPlaceHolder");
        if (this.$stbHeight > 0) {        
          if (el) {
            el.style.paddingTop = this.$stbHeight + 'px';
          }
        } else {
          //清除高度
          if (el) {
            el.style.height = 0;
          }
        }
      }
    });
  },
  mounted() {
    // this.userInfo = Object.assign({}, this.$userInfo);
    this.watcherScroll();
  },
  activated() {
    this.isShowPreview = false;
    this.themeCurrent = this.$curTheme;
    this.networkError = false;
    this.isFirst = true;
    this.clickNum = 1;
    this.isLoading = false;
    this.loading = false;
    let { scrollPosition } = this.$store.state.find;
    if (this.userInfo.AREA_CODE != this.$userInfo.AREA_CODE) {
      scrollPosition = 0;
      this.$store.commit("saveFindScollPosition", 0);
    }
    this.userInfo = Object.assign({}, this.$userInfo);

    //判断是否已经加载过帖子，列表为空则显示加载框，不为空时则不显示加载框 add for ZHSQYYPT-9271
    if (this.noteData && this.noteData.length == 0) {
      this.isShowLoading = true;
    } else {
      this.isShowLoading = false;
    }

    //通过路由监听到vuex中存储的变量值
    if (scrollPosition === 0) {
      this.loadNotes(1);
    }

    //赋值上一次的scrollTop
    this.setPreScrollTop(scrollPosition);
    
    this.getMineMessageCount();
    window.addEventListener("native.keyboardshow", this.keyboardshow);
    window.addEventListener("native.keyboardhide", this.keyboardhide);
    document.addEventListener("visibilitychange", () => {
      if (document.hidden) {
        // 页面被挂起
        this.keyboardhide();
      }
    });

    //品质生活
    this.leavePage = false;
    if (this.lifeAwaitDrawing) {
      this.lifeAwaitDrawing = false;
      this.updateLifeItemData(this.lifeTmpData.isRefresh, this.lifeTmpData.tmpData);
    }
  },
  deactivated() {
    //品质生活
    this.leavePage = true;
    this.mineMessageCount = 0;

    if (this.$store.state.preview.previewId) {
      this.$store.dispatch("setPreviewId", "");
    }
    window.removeEventListener("native.keyboardshow", this.keyboardshow);
    window.removeEventListener("native.keyboardhide", this.keyboardhide);
  },
  beforeRouteLeave(to, from, next) {
    this.saveScrollTopValue();
    next();
  },
  props: {},
  data() {
    return {
      selections: [],
      deleteActions: [{ name: "删除" }],
      reportActions: [{ name: "举报" }],
      replyValue: "",
      reportShow: false,
      replyInputShow: false,
      deleteShow: false,
      userInfo: "",
      //社区
      loading: false,
      finished: false,
      commitType: "",
      replys: null,
      commentNoteCode: "",
      isLoading: false,
      background: {
        backgroundImage:
          "url(" + require("../../assets/images/find/find_bg.png") + ") ",
        backgroundPosition: "center center",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover"
      },
      noteData: [],
      isShowLoading: false, //是否显示Loading加载框，首次无内容时显示，已经有内容时不显示 add for ZHSQYYPT-9271
      mineMessageCount: 0,
      isShowPreview: false,
      // 键盘弹出时
      noteIndex: 0,

      // 图片预览属性
      images: [],
      networkError: false,

      // 是否是第一次点击评论，回复，点赞
      isFirst: true,
      clickNum: 1,
      keyboardTime: 0,
      themeCurrent: 0,

      tabIndex: "0",
      /// 品质生活相关
      refreshing: false,
      lifeItemW: 0,
      // vue-waterfall-左右margin值
      waterfallLF: 10.5,
      // vue-waterfall-slot padding值
      wfSoltPd: 4.5,
      //标题左右距离
      lifeTitleLF: 5,
      //标题顶部距离
      lifeTitleTop: 5,
      //标题底部距离
      lifeTitleBottom: 10,
      //单行高度
      lifeTitleSingleLineH: 20,
      lifeItems: [],
      //品质数据页数
      lifePage: 1,
      //数据总页数
      lifePageNum: 0,
      lifeLoading: false, //停止加载
      lifeFinished: false, //没有更多
      leavePage: false, //当前已离开页面
      lifeAwaitDrawing: false, //品质生活等待渲染
      lifeTmpData: {
        isRefresh: false,
        tmpData: []
      }, //品质生活等待渲染数据
      isBusy: false, //品质生活正在请求数据渲染
      lifeScrollTop: 0,
    };
  },
  watch: {
    //每次变化，input获取焦点
    replyInputShow(val, oldVal) {
      if (val == true) {
        this.$nextTick(() => {
          this.$refs.myInput.focus();
        });
      } else {
        this.$nextTick(() => {
          this.$refs.myInput.blur();
        });
      }
    },
    /*记录当前页面有弹窗*/
    reportShow(val) {
      //console.log("【举报】Findpage: 监控到reportShow状态变化： "+val)
      this.$store.dispatch("setFindPopWinFlag", val);
    },
    /*监控到关闭弹窗状态*/
    closePopWin(val) {
      //console.log("【举报】Findpage: 监控到closePopWin状态变化： "+val)
      if (val) {
        this.reportShow = false;
        this.$store.dispatch("setFindClosePopWinFlag", false);
      }
    },
    previewId(val, oldVal) {
      if (val == "") {
        this.isShowPreview = false;
      }
    }
  },
  computed: {
    ...mapState({
      pageNumber: state => state.find.pageNumber,
      closePopWin: state => state.find.closePopWin,
      previewId: state => state.preview.previewId
    }),

    //瀑布流样式
    waterfallStyle: function() {
      return {
        marginLeft: this.waterfallLF + "px",
        marginRight: this.waterfallLF + "px"
      };
    },
    //瀑布流内容样式
    waterFallSoltStyle: function() {
      return {
        padding: this.wfSoltPd + "px!important"
      };
    },
    //品质生活标题样式
    lifeTitleStyle: function() {
      return {
        paddingRight: this.lifeTitleLF + "px",
        paddingLeft: this.lifeTitleLF + "px",
        paddingTop: this.lifeTitleTop + "px",
        marginBottom: this.lifeTitleBottom + "px"
      };
    },
    //瀑布流内容宽度
    waterfallSlotHeight: function() {
      return function(item) {
        let picHeight =
          ((this.lifeItemW - this.wfSoltPd * 4) / item.width) * item.height;
        let size = this.getCharSizeByCanvas(item.title, {
          fontFamily: "PingFangSC-Medium",
          fontSize: "14px"
        });
        let titleMaxWidth =
          this.lifeItemW - this.lifeTitleLF * 2 - this.wfSoltPd * 2;
        let titleH =
          size.width > titleMaxWidth
            ? 2 * this.lifeTitleSingleLineH
            : this.lifeTitleSingleLineH;
        // console.log(item.title + ' 宽度： ' + size.width + ' 高度： ' + titleH + 'titleMaxWidth: ' + titleMaxWidth);
        return (
          picHeight +
          this.wfSoltPd * 2 +
          this.lifeTitleTop +
          titleH +
          this.lifeTitleBottom
        );
      };
    }
  },
  components: {
    Preview: Preview,
    NetworkError: NetworkError,
    Waterfall,
    WaterfallSlot
  },
  methods: {
    ...mapActions([
      "getNotesAction",
      "saveLikeAction",
      "delNoteDataAction",
      "delCommentInforAction",
      "addCommentAction",
      "saveReplyAction",
      "getMineMessageCountAction",
      "getNoteDetailAction",
      "getQualityListAction"
    ]),
    //设置上一次的scrollTop值
    setPreScrollTop(scrollPosition) {
      let dom = document.querySelector("#refreshWrapper");
      let domL = document.querySelector("#qualityLife");
      let noneDom = null;
      if (dom) {
        if (dom.parentElement.style.display === 'none') {
          noneDom = dom;
          noneDom.parentElement.style.display = 'block';
        }
        dom ? (dom.scrollTop = scrollPosition) : "";
      }
      
      if (domL) {
        if (domL.parentElement.style.display === 'none') {
          noneDom = domL;
          noneDom.parentElement.style.display = 'block';
        }
        domL ? (domL.scrollTop = this.lifeScrollTop) : "";
      }
      if (noneDom) {
        noneDom.parentElement.style.display = 'none';
      }
    },
    //保存scrollTop值
    saveScrollTopValue(isTabChange = false) {
      let judgeIndex =  this.tabIndex;
      //tab切换时显示的tabIndex是上一个，进到别的页面时，tabIndex时当前的
      if (!isTabChange) {
        judgeIndex = this.tabIndex === '0' ? '1' : '0';
      } 
      let refWapDom = document.querySelector("#refreshWrapper");
      let qLifeDom = document.querySelector("#qualityLife");
      if (refWapDom && judgeIndex != '0') {
        let scrllTop = refWapDom.scrollTop;
        this.$store.commit("saveFindScollPosition", scrllTop || 0);
      }
      else if (qLifeDom && judgeIndex != '1') {
        this.lifeScrollTop = qLifeDom.scrollTop;
      }
    },
    getNowTime() {
      var now = Date.now();
      // console.log("get now time " + (now - this.keyboardTime));
      if (now - this.keyboardTime <= 150) {
        this.keyboardTime = now;
        // ignore
        return false;
      } else {
        this.keyboardTime = now;
        return true;
      }
    },

    /**
     * 键盘弹出事件
     */
    keyboardshow(e) {
      if (!this.getNowTime()) {
        console.log("keyborad handle less than 150, ignore!!!");
        return;
      }
      // 安卓的处理不正确，有问题
      this.$refs.myReply.style.bottom = e.keyboardHeight + "px";
      // 如果滚动会影响输入框的位置
      // var dom = this.$refs["noteId" + this.noteIndex][0];
      // dom && dom.scrollIntoView();

      this.$nextTick(() => {
        console.log("scrollTop");
        var actualTop = this.$refs["noteId" + this.noteIndex][0].offsetTop;
        this.$refs.refreshWrapper.scrollTop = actualTop;
      });
      this.replyInputShow = true;
    },

    /**
     * 键盘隐藏事件
     */
    keyboardhide(e) {
      if (!this.getNowTime()) {
        console.log("keyborad handle less than 150, ignore+!!!");
        return;
      }
      this.replyInputShow = false;
      this.$refs.myReply.style.bottom = "1.33333rem";
    },

    /**
     * 页面滚动检测，发生滚动后隐藏输入框
     **/
    watcherScroll() {
      document.querySelector(".find-box").addEventListener(
        "touchmove",
        e => {
          this.replyInputShow = false;
        },
        { passive: false }
      );
    },

    refresh() {
      this.getMineMessageCount();
      this.loadNotes(1);
    },

    /**
     * 加载朋友圈信息
     * @param
     * pageNumber  Number      当前页码
     * isRefresh   Boolean     本次调用是否为从头刷新
     **/
    loadNotes(pageNumber, isRefresh = true) {
      if (pageNumber == 1) {
        this.resetData();
      }
      if (!this.$userInfo.ROOM_CODE) {
        Dialog.confirm({
          title: "您未绑定房间，是否前往绑定？",
          confirmButtonText: "前往"
        })
          .then(() => {
            //跳转到绑定房产页面
            this.$router.push("selectArea");
          })
          .catch(() => {
            // 不选择房产，清空列表
            this.noteData = [];
            this.loading = false;
            this.finished = true;
            this.isLoading = false;
          });
        return;
      }
      this.loading = true;
      this.isShowLoading = isRefresh ? this.isShowLoading : false; //加载更多时，不显示loading
      this.getNotesAction(
        {
          areaCode: this.$userInfo.AREA_CODE,
          page: pageNumber,
          rows: 10
        },
        this.isShowLoading
      )
        .then(noteData => {
          this.networkError = false;
          var indexFlag = 0;
          if (noteData.data.list && noteData.data.list.length > 0) {
            for (var i = 0; i < noteData.data.list.length; i++) {
              if (
                noteData.data.list[i] &&
                noteData.data.list[i].notePic &&
                noteData.data.list[i].notePic.length > 0
              ) {
                for (var j = 0; j < noteData.data.list[i].notePic.length; j++) {
                  if (indexFlag % 2 == 0) {
                    noteData.data.list[i].notePic[j] = noteData.data.list[
                      i
                    ].notePic[j].replace(
                      "xwjfile.juhaolian.cn",
                      "xwjfilea.juhaolian.cn"
                    );
                  }

                  //本地化处理
                  (function() {
                    let listIndex = i;
                    let picIndex = j;
                    Vue.prototype.$isHasLocalImage(
                      noteData.data.list[i].notePic[j],
                      function(localUrl) {
                        noteData.data.list[listIndex].notePic[
                          picIndex
                        ] = localUrl;
                        console.log(
                          "发现： 帖子[" +
                            listIndex +
                            "] -> 图片(" +
                            picIndex +
                            ") return url = " +
                            noteData.data.list[listIndex].notePic[picIndex]
                        );
                      }
                    );
                  })();

                  indexFlag++;
                }
              }
            }
          }
          if (noteData.data.list.length == 0 || noteData.data.isLastPage) {
            this.finished = true;
          }
          this.loading = false;
          this.isLoading = false;
          if (isRefresh || pageNumber == 1) {
            this.noteData = noteData.data.list;
            this.$store.commit("pageNumChange", {
              custPageNum: true,
              pageNumber: 2
            });
          } else {
            noteData.data.list.forEach(element => {
              this.noteData.push(element);
            });
          }
        })
        .catch(error => {
          if (pageNumber == 1) {
            this.noteData = [];
            this.networkError = true;
          }
          this.finished = true;
          this.loading = false;
          this.isLoading = false;
          //this.$toast(error);
          console.log("【Error】getNotesAction：" + error);
        });
    },

    /**
     * 重置数据
     */
    resetData() {
      this.replyValue = "";
      this.reportShow = false;
      this.replyInputShow = false;
      this.deleteShow = false;
      this.loading = false;
      this.finished = false;
      this.commentNoteCode = "";
      this.isLoading = false;
    },

    /**
     * 获取当前用户相关的动态提醒数
     **/
    getMineMessageCount() {
      this.getMineMessageCountAction(
        {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode: this.$userInfo.AREA_CODE
        },
        this.isShowLoading
      )
        .then(res => {
          this.mineMessageCount = res.data;
        })
        .catch(error => {
          this.mineMessageCount = 0;
          //this.$toast(error);
          console.log("【Error】getMineMessageCount：" + error);
        });
    },

    /**
     * 跳转到提示的消息列表
     **/
    goNotifyList() {
      store.set("reloadFindNotifyList", true);
      this.$router.push({
        name: "noteNotify",
        params: {}
      });
    },

    /**
     * 跳转至新增动态页面
     *
     **/
    addNote() {
      //没绑定房间或者不在社区内容页
      if (!this.$userInfo.ROOM_CODE || this.tabIndex !== '0') {
        return;
      }
      this.$store.commit("setAddNotePageParams", {
        pageType: "publish"
      });
      this.$router.push({ name: "addNote" });
    },

    /**
     * 跳转到我的相册页面
     */
    goMyNote() {
      if (!this.$userInfo.ROOM_CODE || this.tabIndex !== '0') {
        return;
      }
      this.$router.push({ name: "myNote" });
    },

    /**
     * 删除评论操作
     * @param
     **/
    delCommentAction() {
      Dialog.confirm({
        title: "提示",
        message: "确定删除此条评论？"
      })
        .then(() => {
          let { noteCode, evalCode } = this.delActionObj;
          this.delCommentInforAction({ noteCode, evalCode })
            .then(() => {
              let evalIndex = 0;
              this.noteData[this.noteIndex].commentList.forEach(
                (note, index) => {
                  if (note.evalCode == evalCode) {
                    evalIndex = index;
                  }
                }
              );
              console.log(evalIndex);
              this.noteData[this.noteIndex].commentList.splice(evalIndex, 1);
              // return this.getNoteDetailAction({ noteCode: noteCode });
            })
            // .then(noteData => {
            //   // this.$set(this.noteData, this.replyNoteIndex, noteData.data);
            // })
            .catch(error => {
              this.$toast(error);
            });
        })
        .catch(() => {});
    },

    /**
     * 评论或回复点击行为
     * @param
     * evaMap          Object<any>     当前回复对象数据
     * noteCode        Object<any>     当前消息对象数据
     * noteIndex?      Number          当前note索引
     * commentType?    Boolean         当前选择的回复类型
     **/
    udClick(evaMap, noteCode, noteIndex, commentType = true) {
      if (this.replyInputShow) {
        this.replyInputShow = false;
        return;
      }
      this.comitNoteCode = noteCode;
      if (commentType) {
        this.commitType = "comment";
        this.replyInputShow = true;
        this.noteIndex = noteIndex;
      } else {
        this.commitType = "reply";
        if (this.$userInfo.OWNER_CODE === evaMap.userCode) {
          this.noteIndex = noteIndex;
          this.deleteShow = true;
          this.replyNoteIndex = noteIndex;
          this.delActionObj = {
            noteCode: noteCode,
            evalCode: evaMap.evalCode
          };
        } else {
          this.replyInputShow = true;
          this.replys = {
            nickName: evaMap.nickName,
            evalCode: evaMap.evalCode,
            type: evaMap.isProperty
          };
          this.noteIndex = noteIndex;
        }
      }
    },

    /**
     * 输入框内容提交
     *
     **/
    submitInput() {
      if (this.replyValue.length == 0) {
        return;
      }
      if (this.commitType == "reply") {
        // 回复
        var obj = {
          userCode: this.$userInfo.OWNER_CODE,
          nickName: this.$userInfo.OWNER_NICK_NAME,
          content: this.replyValue,
          isProperty: 0, // 业主
          returner: this.replys.nickName,
          returnerType: this.replys.type
        };
        this.noteData[this.noteIndex].commentList.push(obj);
        this.replyAction();
      } else {
        // 评论
        var obj = {
          userCode: this.$userInfo.OWNER_CODE,
          nickName: this.$userInfo.OWNER_NICK_NAME,
          content: this.replyValue,
          isProperty: 0, // 业主
          returner: null,
          returnerType: null
        };
        this.noteData[this.noteIndex].commentList.push(obj);
        this.addComments();
      }
      this.replyInputShow = false;
    },

    /**
     * 回复好友评论
     *
     **/
    replyAction() {
      this.isFirst = false;
      let reqData = {
        content: this.replyValue,
        evalCode: this.replys.evalCode,
        isProperty: 0,
        userCode: this.$userInfo.OWNER_CODE
      };
      this.saveReplyAction(reqData)
        .then(() => {
          this.isFirst = true;
          this.clickNum = 1;
          this.replyValue = "";
          this.updateAssignNote(this.comitNoteCode);
        })
        .catch(error => {
          if (!this.isFirst && this.clickNum == 1) {
            // 如果是第一次再执行一遍
            this.clickNum = this.clickNum + 1;
            this.replyAction();
          } else {
            this.isFirst = true;
            this.clickNum = 1;
            this.noteData[this.noteIndex].commentList.splice(
              this.noteData[this.noteIndex].commentList.length - 1,
              1
            );
            this.$toast(error);
          }
        });
    },

    /**
     * 增加评论
     * @param
     * noteCode   String   当前选中note编码
     * noteIndex  Number   当前操作的信息的索引
     **/
    addComments() {
      this.isFirst = false;
      this.addCommentAction({
        content: this.replyValue,
        isProperty: 0,
        noteCode: this.comitNoteCode,
        // "picUrl": [],
        userCode: this.$userInfo.OWNER_CODE
      })
        .then(res => {
          this.isFirst = true;
          this.clickNum = 1;
          this.replyValue = "";
          this.$toast(res.msg);
          this.updateAssignNote(this.comitNoteCode);
        })
        .catch(error => {
          if (!this.isFirst && this.clickNum == 1) {
            // 如果是第一次再执行一遍
            this.clickNum = this.clickNum + 1;
            this.addComments();
          } else {
            this.isFirst = true;
            this.clickNum = 1;
            this.noteData[this.noteIndex].commentList.splice(
              this.noteData[this.noteIndex].commentList.length - 1,
              1
            );
            this.$toast(error);
          }
        });
    },

    /**
     *更新指定一条note信息
     * @param
     * noteCode    String    指定note信息编码，通过此编码更新信息
     **/
    updateAssignNote(noteCode) {
      this.getNoteDetailAction({ noteCode: noteCode })
        .then(noteData => {
          let noteIndex = 0;
          this.noteData.forEach((note, index) => {
            if (note.noteCode == noteCode) {
              noteIndex = index;
            }
          });
          this.noteData[noteIndex].likerList = noteData.data.likerList;
          this.noteData[noteIndex].commentList = noteData.data.commentList;
          // this.$set(this.noteData, noteIndex, noteData.data);
        })
        .catch(error => {
          this.$toast(error);
        });
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      console.log(src);
      this.images = src;
      this.indexChange(index);
      this.isShowPreview = true;
      this.$nextTick(() => {
        this.$refs.myPreview.setIndex(index);
        this.$refs.myPreview.setIsShowPreview(true);
        this.$store.dispatch("setPreviewId", this.$refs.myPreview);
      });
    },

    /**
     * index切换
     **/
    indexChange(index) {
      var src = this.images[index];
      if (src.lastIndexOf("@") == -1) {
        return;
      }
      var index1 = src.lastIndexOf(".");
      var index2 = src.lastIndexOf("@");
      var newSrc = src.substring(0, index2) + src.substring(index1, src.length);
      var img = new Image();
      img.onload = () => {
        this.$set(this.images, index, newSrc);
      };
      img.src = newSrc;
    },

    /**
     * 点赞行为(增加与取消)
     * @param
     * noteCode  String   当前点选节点code
     * noteIndex Number   当前点选节点索引
     * likeMap   Array<Object> 当前节点所有点赞集合
     **/

    setLike(noteCode, noteIndex, likeMap) {
      if (this.replyInputShow) {
        this.replyInputShow = false;
        return;
      }
      let reqData = {
        concemflag: 1,
        isProperty: 0,
        noteCode: noteCode,
        userCode: this.$userInfo.OWNER_CODE
      };
      if (likeMap) {
        reqData.concemflag = likeMap.some(
          val => val.userCode == reqData.userCode
        )
          ? 0
          : 1;
      } else {
        reqData.concemflag = 1;
      }
      if (reqData.concemflag) {
        // 点赞
        this.noteData[noteIndex].likerList.push({
          userCode: this.$userInfo.OWNER_CODE,
          nickName: this.$userInfo.OWNER_NICK_NAME
        });
      } else {
        // 取消点赞
        var ind = 0;
        this.noteData[noteIndex].likerList.forEach((item, index) => {
          if (item.userCode == this.$userInfo.OWNER_CODE) {
            ind = index;
          }
        });
        if (ind >= 0) {
          this.noteData[noteIndex].likerList.splice(ind, 1);
        }
      }
      this.saveLike(reqData, noteIndex);
    },

    saveLike(reqData, noteIndex) {
      this.isFirst = false;
      this.saveLikeAction(reqData)
        .then(res => {
          this.isFirst = true;
          this.clickNum = 1;
        })
        .catch(error => {
          if (!this.isFirst && this.clickNum == 1) {
            // 如果是第一次再执行一遍
            this.clickNum = this.clickNum + 1;
            this.saveLike(reqData, noteIndex);
          } else {
            this.isFirst = true;
            this.clickNum = 1;
            if (reqData.concemflag) {
              // 点赞
              var ind = -1;
              console.log(this.noteData[noteIndex]);
              this.noteData[noteIndex].likerList.forEach((item, index) => {
                if (item.userCode == this.$userInfo.OWNER_CODE) {
                  ind = index;
                }
              });
              if (ind >= 0) {
                this.noteData[noteIndex].likerList.splice(ind, 1);
              }
            } else {
              // 取消点赞
              this.noteData[noteIndex].likerList.push({
                userCode: this.$userInfo.OWNER_CODE,
                nickName: this.$userInfo.OWNER_NICK_NAME
              });
            }
            this.$toast(error);
          }
        });
    },

    /**
     * 删除当前所选的note
     * @param
     *  noteCode  String  当前所选的noteCode
     **/
    deleteNote(noteCode) {
      Dialog.confirm({
        title: "提示",
        message: "确定删除此贴？"
      }).then(() => {
        this.delNoteDataAction(noteCode)
          .then(result => {
            this.loadNotes(1);
          })
          .catch(error => {
            this.$toast(error);
          });
      });
    },

    /**
     * 上拉加载更多数据
     * @param
     **/
    loadMoreNote() {
      this.loadNotes(this.pageNumber, false);
    },

    /**
     * 校验当前的点赞列表中是否包含当前登录用户，以此来更改点赞图标
     * @param
     *  likeMap  Array<Object>  当前的点赞集合数据
     * @return
     *  Boolean  当前登录账户是否已经点赞过
     **/
    checkLike(likeMap) {
      if (!likeMap) {
        return false;
      }
      return likeMap.some(val => val.userCode == this.$userInfo.OWNER_CODE);
    },

    /**
     * 点击吊起举报菜单时保存当前的选中的note对象
     * @param
     * noteMap  Object  当前点击的note对象
     **/
    reportMenu(noteMap) {
      if (this.replyInputShow) {
        this.replyInputShow = false;
        return;
      }
      this.reportShow = !this.reportShow;
      this.checkedReport = noteMap;
    },

    /**
     * 举报操作
     * @param
     **/
    reportAction() {
      this.$store.commit("setAddNotePageParams", {
        pageType: "report",
        noteCode: this.checkedReport.noteCode
      });
      this.$router.push({
        name: "addNote"
      });
    },
    tabChange() {
      // console.log("当前选择页面index: " + this.tabIndex);
      if (this.tabIndex === "1" && this.lifePageNum == 0) {
        this.onRefreshLife();
      }
      this.saveScrollTopValue(true);
    },

    ///  品质生活
    //计算字符串的宽度size
    getCharSizeByCanvas: function(char, style = {}) {
      let { fontSize = 14, fontFamily = "Arial" } = style;
      document.body.appendChild(canvas);
      ctx.font = `${fontSize}px ${fontFamily}`;
      document.body.removeChild(canvas);
      let text = ctx.measureText(char); // TextMetrics object
      ctx.fillText(char, 50, 50);
      let result = {
        height: fontSize,
        width: text.width
      };
      return result;
    },
    onRefreshLife() {
      this.getQualityLifeData(true);
    },
    //获取品质生活数据
    getQualityLifeData(isFirst = false) {
      if (isFirst) {
        this.lifePage = 1;
      }
      this.getQualityListAction(this.lifePage)
        .then(list => {
          if (isFirst) {
            this.refreshing = false;
          }
          this.lifeLoading = false;

          if (this.lifePage <= list.pages) {
            this.lifePage += 1;
            this.lifePageNum = list.pages;
            this.lifeFinished = false;
          } else {
            this.lifeFinished = true;
            return;
          }
          list.articleList.forEach((item, index) => {
            if (index % 2 == 0) {
              item.src = item.src.replace(
                "xwjfile.juhaolian.cn",
                "xwjfilea.juhaolian.cn"
              );
            }
            // console.log('品质生活图片宽*高： ' + item.width + ' * ' + item.height);

            //本地化处理
            // console.log("首页-品质生活： url["+i+"] = "+ item.picUrl);
            Vue.prototype.$isHasLocalImage(item.src, function(localUrl) {
              // console.log('当前图片url： ' + localUrl);
              item.src = localUrl;
            });
          });

          // console.log('====>>>> 品质生活添加数据')
          this.updateLifeItemData(this.lifePage == 2, list.articleList);
        })
        .catch(error => {
          if (isFirst) {
            this.refreshing = false;
          }
          this.lifeLoading = false;
          console.log("品质生活加载数据失败" + error);
        });
    },

    //更新品质生活数据
    updateLifeItemData(isRefresh = false, lifeItems) {
      if (this.leavePage) {
        this.lifeAwaitDrawing = true;
        this.lifeTmpData.isRefresh = isRefresh;
        this.lifeTmpData.tmpData = lifeItems;
      } else {
        if (isRefresh) {
          this.lifeItems = lifeItems;
        } else {
          this.lifeItems = this.lifeItems.concat(lifeItems);
        }
      }
    },
    //添加品质生活数据
    addQualityLifeData() {
      if (!this.isBusy && this.lifePage <= this.lifePageNum) {
        this.isBusy = true;
        this.getQualityLifeData();
      } else {
        this.lifeLoading = false;
        this.lifeFinished = this.lifePage > this.lifePageNum;
      }
    },
    reflowed() {
      this.isBusy = false;
      // console.log('=====>>>> 品质生活渲染完毕了');
    },
    //点击跳转品质生活文章
    clickedLifeItem: function(item) {
      let articleNo = item.articleNo;
      let picUrl = item.src;
      this.$router.push({
        name: "qualityDetail",
        params: {
          articleNo: articleNo,
          articleImg: picUrl
        }
      });
    },
  }
};
</script>

