<template>
  <div style="height:100%;background:#F9F9F9;display: flex;flex-direction: column;" :class="{themeL:bindThemeL}">
    <navigation-bar  @onLeftClick="onClickLeft" pageName="人员申请">
    </navigation-bar>
    <div class="no-networking" v-show="networkError">
      <img :src="noNetworkImg">
      <h3>网络请求失败</h3>
      <p>请检查您的网络</p>
      <div @click="getRoomAndMemberList">重新加载</div>
    </div>
    <div class="noList" v-if="list.length == 0 && showEmpty" >
      <img src="../../assets/images/default/no_home_img.png" />
      <div>暂无新成员申请</div>
    </div>
    <!--家庭成员列表-->
    <div style="flex:1;overflow:auto;">
      <div v-for="(member,memberIndex) in list" :key="memberIndex">

        <div class="memberItem" :class="{red:member.approvalStatus == 0}">
          <div class="leftContent">
            <div style="display: flex;align-items: center;padding-right: 10px;" :class="{themeL:bindThemeL}">
              <img class="photoImg"
                   :src="member.picUrl.length == 0 ? require('../../assets/images/default/user_default_avata.png') : member.picUrl"
              />
            </div>
            <div class="textContent">
              <div class="firstLine">
                <span class="memberName" :class="{areaNameThemeL:bindThemeL}">{{member.ownerNickName}}</span>
              </div>
              <span
                  class="phoneNum"
                  :class="{themeL:bindThemeL}"
              >{{member.phoneMask}}</span>
              <span
                  class="phoneNum"
                  :class="{themeL:bindThemeL}"
              >来源：{{member.srcTag}}</span>
            </div>
          </div>

          <!--申请加入-->
          <div class="rightContent" v-if="member.approvalStatus == 0">
            <img
                class="btnStyle"
                src="../../assets/images/personal_center/user_member_icon_decline.png"
                @click="rejectClick(member)"
            />
            <img
                class="btnStyle"
                src="../../assets/images/personal_center/user_member_icon_pass.png"
                @click="passClick(member)"
            />
          </div>
          <!--已拒绝、已同意状态-->
          <div
              class="statusStyle"
              v-if="member.approvalStatus == 1"
          >{{member.approvalResult == 2 ? "已拒绝" : "已同意" }}</div>

        </div>

        <van-divider style="height: 1px;margin: 0 10px;background:#ECECEC"></van-divider>
      </div>
    </div>

  </div>

</template>

<script>
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import { getApplyMember, deleteRoomNew, removeBindFamily, approvalMember } from "../../services/MineService";
  import { Dialog } from "vant";
  import noNetworkImg from '@/assets/images/home/notice/img_net@2x.png'
  import defOwnerImg from '@/assets/images/default/user_default_avata.png'
  import {phoneMask} from "../../utils/common";

  export default {
    name: "MyFamilyApply",
    components: {
      NavigationBar
    },
    data() {
      return {
        headerItemWidth:{
          width:'110px'
        },
        areaCode: "",//传参
        showEmpty: false,
        list: [],
        networkError: false,
        noNetworkImg: noNetworkImg,
        minePhone: this.$userInfo.PHONE,
        /*大字模式*/
        bindThemeL: this.$curTheme==0?false:true,
        btnStyle: {
          fontSize:this.$curTheme==0?"12px":"15px",
          width:this.$curTheme==0?"90px":"100px"
        }
      }
    },
    mounted() {
      this.areaCode = this.$route.query.areaCode;
      console.log("myFamilyNew: this.areaCode ="+this.areaCode)
      this.getRoomAndMemberList();
    },
    methods: {
      onClickLeft() {
        this.$router.back(-1);
      },
      //获取房间列表以及每个房间下的成员列表
      getRoomAndMemberList() {
        let areaCode = this.$userInfo.AREA_CODE;
        if (this.areaCode != undefined && this.areaCode != "") {
          areaCode = this.areaCode;
        }
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode: areaCode
        };
        getApplyMember(param)
          .then(result => {
            if (result.resultCode == 0) {
              this.networkError = false;

              this.list = [];

              result.data.forEach((item, index) => {
                item.srcTag = item.showAllName;
                item.phoneMask = phoneMask(item.phone)
              })
              this.list = result.data;

              console.log("============ length "+this.list.length)
              this.showEmpty = true;
            } else {
              console.log("【Error】getRoomAndMemberList: "+result.msg);
            }
          })
          .catch(error => {
            console.log(error);
            // Toast(error);
          });
      },

      //拒绝加入房间
      rejectClick(member) {
        Dialog.confirm({
          title: "确认拒绝申请？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.rejectAction(member);
          })
          .catch(() => {});
      },
      rejectAction(member) {
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          approvalResult: "2",
          uuid: member.uuid
        };
        approvalMember(param)
          .then(data => {
            //重新获取家庭成员列表
            this.getRoomAndMemberList();
          })
          .catch(error => {
            // Toast(error);
          });
      },
      //同意加入房间
      passClick(member) {
        Dialog.confirm({
          title: "确认通过申请？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.confirmAction(member);
          })
          .catch(() => {});
      },
      confirmAction(member) {
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          approvalResult: "1",
          uuid: member.uuid
        };
        approvalMember(param)
          .then(data => {
            //重新获取家庭成员列表
            this.getRoomAndMemberList();
          })
          .catch(error => {
            // Toast(error);
          });
      }
    }
  }
</script>

<style scoped lang="scss">
  /*空列表*/
  .noList img {
    margin: 125px 100px 20px 100px;
    width: 180px;
    display: inline-block;
  }
  .noList div {
    font-family: PingFangSC-Regular;
    letter-spacing: 0;
    text-align: center;
    width: 100%;
    font-size: 16px;
    color: #9A9A9A;
    line-height: 25px;
  }

  .leftContent {
    display: flex;
    align-items: center;
  }
  .leftContent img {
    width: 45px;
    height: 45px;
  }
  .themeL .leftContent img {
    width: 50px !important;
    height: 50px !important;
  }

  .textContent {
    display: flex;
    flex-direction: column;
  }

  .areaName {
    font-weight: bold;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
  }
  .areaNameThemeL {
    font-size: 17px !important;
    line-height: 30px;
  }

  .houseNum {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #666666;
    letter-spacing: 0;
  }
  .houseNumThemeL {
    font-size: 15px !important;
    /*line-height: 30px;*/
  }

  /*新增房主、成员标签*/
  .firstLine{
    display: flex;
    flex-wrap: wrap;
    margin-bottom:4px;
    align-items: center;
  }
  .themeL .firstLine {
    margin-bottom: 0;
  }
  .rightContent {
    display: flex;
    align-items: flex-end;
    min-width: 30%;
  }
  .arrowImg {
    width: 14px;
    height: 14px;
    line-height: 14px;
    margin-left: 10px;
  }

  /*家庭成员列表*/
  .memberItem {
    min-height: 78px;
    background-color: #f9f9f9;
    margin: 0;
    display: flex;
    padding-right: 16px;
    padding-left: 16px;
    padding-top: 5px;
    padding-bottom: 5px;
    justify-content: space-between;
    align-items: center;
  }
  .themeL .memberItem {
    min-height: 90px;
  }
  .red {
    background-color: rgba(251,87,104,0.10);
  }
  .photoImg {
    border-radius: 50%;
    width: 38px !important;
    height: 38px !important;
  }
  .themeL .leftContent .photoImg {
    width: 43px !important;
    height: 43px !important;
  }
  .info {
    height: 64px;
    display: flex;
    padding-right: 12px;
    padding-left: 12px;
    justify-content: space-between;
    align-items: center;
  }
  .memberName {
    font-weight: bold;
    font-size: 14px;
    color: #3a3a3a;
    letter-spacing: 0;
  }
  .themeL .memberName {
    font-size: 17px;
    /*height: 30px;*/
    line-height: 20px;
  }
  .contact {
    display:flex;
    align-items:center;
    justify-content: center;
    width: 55px;
    height: 23px;
    border: 1px solid #FB5768;
    border-radius: 22px;
    margin-left: 8px;
  }
  .phoneImg {
    width:13px;
    height:12px;
    margin-right: 3px;
  }
  .phoneNum {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #666666;
    letter-spacing: 0;
  }
  .themeL .phoneNum {
    font-size: 15px;
  }

  /*拒绝，通过按钮*/
  .btnStyle {
    width:50px;
    height:34px;
    margin-right: 5px;
  }
  .statusStyle {
    height:25px;
    font-size:14px;
    color:#888888;
    min-width: 20%;
    text-align: right;
    padding-right: 10px;
  }
  .themeL .statusStyle {
    font-size: 16px;
  }

  /*无网络样式*/
  .no-networking {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 90px;
    font-family: PingFangSC-Regular;
    text-align: center;
    img {
      width: 66px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 16px;
      color: #888888;
      margin: 10px 0px;
    }
    p {
      font-size: 14px;
      color: #C4C4C4;
      margin: 0 0 3px;
    }
    div,
    div:active {
      background-color: #ffffff;
      color: rgb(251, 87, 104);
      position: relative;
      margin: 10px;
      border-radius: 20px;
      border: 1px solid rgb(251, 87, 104);
      font-size: 16px;
      height: 40px;
      line-height: 40px;
      width: 125px;
    }
    div:active {
      opacity: 0.5;
    }
  }
</style>