<template>
  <div style="height:100%">
    <van-nav-bar left-arrow fixed @click-left="onClickLeft">
      <div class="backIcon" slot="left">
        <img src="@/assets/images/common/nav_return_black.png" alt="" srcset="">
      </div>
      <div class="searchInput" :class="{themeLInput:bindThemeL}" slot="title">
        <div class="left">
          <form @submit.prevent="formSubmit" action="javascript:return true" style="display: flex; flex-direction: column;align-items: center;">
            <input ref="searchField" type="search" class="search-input" placeholder="请输入房间号搜索" @keydown.13="onSearch" v-model="searchText" />
          </form>
        </div>
        <div
          class="searchActionBtn"
          :class="{searchBtn :!searchNull, themeLCancel:bindThemeL}"
          @click="searchBtnAction"
        >{{searchNull ? '取消' : '搜索'}}</div>
      </div>
    </van-nav-bar>
    <div :style="padingtopNum" style="overflow:auto;height:100%">
      <van-cell
        :title="item.roomDisplayMessage"
        v-for="(item,index) in list"
        :key="index"
        @click="selectRoom(item)"
        :class="{themeLResult:bindThemeL}"
      />
      <div :style="bottomNum"></div>
    </div>
  </div>
</template>

<script>
import { searchRooms } from "../../services/MineService";
import { Toast } from "vant";
export default {
  neme: "SearchRoom",
  data() {
    return {
      searchText: "",
      areaCode: "",
      list: [],
      searchNull: true,
      selectedRoom: {},
      padingtopNum: { "margin-top": this.$stbHeight + 50 + "px" },
      bottomNum: { height: this.$stbHeight + 55 + "px" },
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  watch: {
    searchText() {
      if (this.searchText !== "") {
        this.searchNull = false;
      } else {
        this.searchNull = true;
      }
    }
  },
  methods: {
    searchBtnAction(){
      if (!this.searchNull) {
        this.onSearch()
      }else{
        this.onCancel()
      }
    },
    onClickLeft() {
      this.selectedRoom = {};
      this.$router.back(-1);
    },
    onSearch() {
      this.list = [];
      this.initRooms();
    },
    onCancel() {
      this.selectedRoom = {};
      this.$router.back(-1);
    },
    initRooms() {
      if (this.searchText == "") {
        this.$toast("房间号不能为空");
      } else {
        let param = { areaCode: this.areaCode, matchedName: this.searchText };
        searchRooms(param)
          .then(data => {
            if (data.roomList.length === 0) {
              console.log("打印toast");
              this.$toast("没有查询到相关的房间，请重新输入");
            } else {
              this.list = data.roomList;
            }
            console.log(this.list);
          })
          .catch(error => {
            // Toast(error);
          });
      }
    },
    selectRoom(item) {
      this.selectedRoom = item;
      this.$router.back(-1);
    }
  },
  mounted() {
    this.areaCode = this.$route.query.areaCode;
    this.$refs.searchField.focus()
    // console.log(this.$refs.searchBar);
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "selectHouse") {
      if (Object.keys(this.selectedRoom).length == 0) {
        to.query.selectBack = false;
      } else {
        to.query.selectBack = true;
        to.query.roomType = this.selectedRoom.roomTypeCode;
        to.query.buildCode = this.selectedRoom.buildingCode;
        to.query.roomCode = this.selectedRoom.roomCode;
      }
    }
    next(); //必须要有这个，否则无法跳转
  }
};
</script>

<style scoped>
/deep/.van-search {
  padding: 0px;
  height: 44px;
}
/deep/.van-nav-bar__title {
  max-width: 100%;
  margin: 0;
  padding-left: 10%;
}

.searchInput {
  height: 34px;
  padding: 5px 0 5px 0px;
  display: flex;
  justify-content: center;
  align-items: space-between;
  background: #fff;
  font-size: 14px;
}
.themeLInput {
  font-size: 15px !important;
}
.themeLCancel {
  font-size: 16px !important;
}
.themeLResult {
  font-size: 18px !important;
}
/*自定义返回箭头样式与系统一致*/
.backIcon {
  display: flex;
  height: 32PX !important;
  overflow: hidden;
  margin: 6PX 0 6PX -9PX !important;
}
.backIcon img {
  width: 32PX !important;
}
.left {
  background: #f7f8fa;
  background-image: url(../../assets/images/mall/search_black.png);
  background-repeat: no-repeat;
  -webkit-background-size: 14px 14px;
  background-size: 10px 10px;
  background-position: 12px 12px;
  border-radius: 17px;
  flex: 1;
  display: flex;
}
.search-input {
  margin-left: 30px;
  background: transparent;
  border: 0;
  height: 20px;
  line-height: 20px;
  color: #333333;
  padding: 0;
  flex: 1;
}
.search-input::-webkit-search-cancel-button{
  -webkit-appearance: none;
}
.searchActionBtn {
  padding: 0 10px;
  font-size: 14px;
  color: #232323;
  line-height: 34px;
  height: 34px;
}
.searchBtn {
  color: #fb5768;
}
</style>