<template>
  <div>
    <!--<van-nav-bar
      id="NaviBar"
      title="选择房产"
      :right-text="isNoArea ? '游客进入' : ''"
      fixed
      left-arrow
      @click-right="onClickRight"
      @click-left="onClickLeft"
    />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="选择房产" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
        <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onClickRight">{{isNoArea ? '游客进入' : ''}}</div>
      </template>
    </navigation-bar>

    <div>
      <van-cell :title="areaName" icon="location-o" @click="onClickLeft" :class="{themeL:bindThemeL}" :style="{paddingTop:paddingTopArea}"/>
    </div>
    <div>
      <van-search placeholder="请输入房间号搜索" disabled shape="round" @click="onClickSearch" :class="{themeL:bindThemeL}"/>
    </div>
    <van-cell title="类型" :value="typeName" is-link @click="selectClass" :class="{listThemeL:bindThemeL}" />
    <van-cell title="楼栋" :value="buildName" is-link @click="selectBuilding" :class="{listThemeL:bindThemeL}" />
    <van-cell title="房间号" :value="roomName" is-link @click="selectRoom" :class="{listThemeL:bindThemeL}" />

    <div style="background:#FFF;text-align:center;" :style="{paddingTop:paddingTopBtn}">
      <van-button
        round
        size="large"
        color="#FB5768"
        style="width: 220px;height: 44px;line-height: 44px;"
        :class="{themeL:bindThemeL}"
        @click="bindRoom"
      >确认提交</van-button>
    </div>
    <van-popup v-model="showPicker" position="bottom">
      <van-picker show-toolbar :columns="dataSource" @cancel="onCancel" @confirm="onConfirm" :class="{themeL:bindThemeL}"/>
    </van-popup>
  </div>
</template>

<script>
import {
  getBuildingList,
  getRoomList,
  bindRoomApply
} from "../../services/MineService";
import Vue from "vue";
import Axios from "axios";
import { Toast, Dialog } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "SelectHouse",
  components: {
    NavigationBar
  },
  data() {
    return {
      typeName: "请选择房产类型",
      buildName: "请选择房产楼栋",
      roomName: "请选择房间号",
      roomClasses: [],
      buildList: [],
      roomList: [],
      showPicker: false,
      areaCode: "",
      areaName: "",
      dataSource: [],
      roomType: "",
      cate1: "房间",
      cate2: "储藏室",
      cate3: "车位",
      cate4: "公寓",
      cate5: "商铺",
      roomCode: "",
      buildCode: "",
      cityCode: "",
      cityName: "",
      isNoArea: false,
      headerItemWidth:{
        width:'110px'
      },
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      paddingTopArea: this.$curTheme==0?"10px":"20px",
      paddingTopBtn: this.$curTheme==0?"30px":"50px",
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    onClickRight() {
      this.setGuestInfo();
      this.$router.replace("/homePage");
    },
    setGuestInfo() {
      var info = this.$userInfo;
      info.AREA_CODE = this.areaCode;
      info.AREA_NAME = this.areaName;
      info.CITY_CODE = this.cityCode;
      info.CITY_NAME = this.cityName;
      console.log(this.$userInfo);
      this.$setUserInfo(info);
    },
    onClickSearch() {
      this.$router.push({
        path: "/searchRoom",
        query: { areaCode: this.areaCode }
      });
    },
    selectClass() {
      this.dataSource = this.roomClasses;
      this.showPicker = true;
    },
    selectBuilding() {
      this.dataSource = this.buildNames;
      this.showPicker = true;
    },
    selectRoom() {
      // if (this.buildCode == "") {
      //   this.$toast('请先选择房产楼栋')
      //   return
      // }
      this.dataSource = this.roomNames;
      this.showPicker = true;
    },

    onConfirm(value, index) {
      this.selectAction(value, index);
    },
    selectAction(value, index, showNext = true) {
      if (value == "" || value == null || value == undefined) {
        this.showPicker = false;
        return;
      }
      if (this.dataSource == []) return;
      this.showPicker = false;
      if (this.dataSource == this.roomClasses) {
        this.typeName = value;
        //let type = value == this.cate1 ? "0" : value == this.cate2 ? "1" : "2";
        let type = "0";
        if (value == this.cate1) {
          type = "0";
        } else if (value == this.cate2) {
          type = "1";
        } else if (value == this.cate3) {
          type = "2";
        } else if (value == this.cate4) {
          type = "3";
        } else if (value == this.cate5) {
          type = "4";
        }

        this.type = type;
        let param = { AREA_CODE: this.areaCode, ROOM_TYPE: type };
        this.buildList = [];
        this.roomList = [];
        this.buildName = "请选择房产楼栋";
        this.roomName = "请选择房间号";
        this.roomCode = "";
        this.fetchBuildings(param, showNext);
      } else if (this.dataSource == this.buildNames) {
        this.buildName = value;
        this.buildCode = this.buildList[index].BUILD_CODE;
        let param = {
          AREA_CODE: this.areaCode,
          BUILD_CODE: this.buildCode,
          ROOM_TYPE: this.type
        };
        this.roomList = [];
        this.roomName = "请选择房间号";
        this.roomCode = "";
        this.fetchRoomList(param, showNext);
      } else {
        this.roomName = value;
        this.roomCode = this.roomList[index].ROOM_CODE;
      }
    },
    onCancel() {
      this.dataSource = [];
      this.showPicker = false;
    },
    bindRoom() {
      //2019.12.9 qiao 修改ZHSQYYPT-4263问题
      if (this.type == "") {
        this.$toast("请选择房产类型");
        return;
      } else if (this.buildName == "请选择房产楼栋") {
        this.$toast("请选择房产楼栋");
        return;
      } else if (this.roomCode == "") {
        this.$toast("请选择房间号");
        return;
      }
      let param = {
        ROOM_CODE: this.roomCode,
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        AREA_CODE: this.areaCode
      };
      bindRoomApply(param)
        .then(res => {
          this.parasResultData(res);
        })
        .catch(error => {
          // Toast(error);
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    parasResultData(res) {
      if (res.result == false) {
        this.$toast(res.msg);
        return;
      }

      console.log(res);
      if (res.data) {
        if (res.data.is_owner == true) {
          this.resetLocalAreaInfo();
          //是房主
          Dialog.alert({
            title: res.data.message,
            confirmButtonColor: "#FFF"
          }).then(() => {
            Vue.prototype.$refreshHomePage = true;
            this.$router.replace("/homePage");
          });
        } else {
          //判断是游客时，本地保存当前选择小区
          if (this.$userInfo.isNoArea) {
            this.setGuestInfo();
          }
          //非房主，显示联系房主审批
          this.$router.push({
            name: "roomApply",
            params: {
              message: res.data.owner_phone,
              url: res.data.shareBindingPath,
              showWeChatShare: res.data.havePropertyOwner
            }
          });
        }
      } else {
        this.$toast(res.msg);
      }
    },
    resetLocalAreaInfo() {
      var info = this.$userInfo;
      info.isNoArea = 0;
      info.AREA_CODE = this.areaCode;
      info.AREA_NAME = this.areaName;
      info.BUILD_CODE = this.buildCode;
      info.BUILD_NAME = this.buildName;
      info.ROOM_CODE = this.roomCode;
      info.ROOM_NAME = this.roomName;
      info.CITY_CODE = this.cityCode;
      info.CITY_NAME = this.cityName;
      console.log(this.$userInfo);
      this.$setUserInfo(info);
    },
    fetchBuildClasses() {
      let roomParam = { AREA_CODE: this.areaCode, ROOM_TYPE: 0 };
      let storeParam = { AREA_CODE: this.areaCode, ROOM_TYPE: 1 };
      let carSpaceParam = { AREA_CODE: this.areaCode, ROOM_TYPE: 2 };
      let gongyuParam = { AREA_CODE: this.areaCode, ROOM_TYPE: 3 };
      let shangpuParam = { AREA_CODE: this.areaCode, ROOM_TYPE: 4 };
      Axios.all([
        getBuildingList(roomParam),
        getBuildingList(storeParam),
        getBuildingList(carSpaceParam),
        getBuildingList(gongyuParam),
        getBuildingList(shangpuParam)
      ])
        .then(
          Axios.spread((roomRes, storeRes, carRes, gongyuRes, shangpuRes) => {
            this.roomClasses = [];
            this.roomClasses.push(this.cate1);
            if (storeRes.buildList.length > 0)
              this.roomClasses.push(this.cate2);
            if (carRes.buildList.length > 0)
              this.roomClasses.push(this.cate3);
            if (gongyuRes.buildList.length > 0)
              this.roomClasses.push(this.cate4);
            if (shangpuRes.buildList.length > 0)
              this.roomClasses.push(this.cate5);
            console.log("【房产】getBuildingList: "+this.roomClasses)
          })
        )
        .catch(err => {
          // Toast(err);
          this.$toast("网络异常，请稍后重试！");
          // console.log(err.response);
        });
    },
    fetchRoomList(param, showNext = true) {
      getRoomList(param)
        .then(data => {
          this.roomList = data.roomList;
          this.dataSource = this.roomNames;
          this.showPicker = showNext;
        })
        .catch(error => {
          // Toast(error);
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    fetchBuildings(param, showNext = true) {
      getBuildingList(param)
        .then(data => {
          this.buildList = data.buildList;
          this.roomList = [];
          this.dataSource = this.buildNames;
          this.showPicker = showNext;
        })
        .catch(error => {
          // Toast(error);
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    //根据搜索添加房间
    searchRoomWithCodes(type, buildCode, roomCode) {
      /*this.typeName =
        type == "0" ? this.cate1 : type == "1" ? this.cate2 : this.cate3;*/
      if (type == "0") {
        this.typeName = this.cate1;
      } else if (type == "1") {
        this.typeName = this.cate2;
      } else if (type == "2") {
        this.typeName = this.cate3;
      } else if (type == "3") {
        this.typeName = this.cate4;
      } else if (type == "4") {
        this.typeName = this.cate5;
      }

      let buildParam = { AREA_CODE: this.areaCode, ROOM_TYPE: type };
      let roomParam = {
        AREA_CODE: this.areaCode,
        BUILD_CODE: buildCode,
        ROOM_TYPE: type
      };
      Axios.all([getBuildingList(buildParam), getRoomList(roomParam)])
        .then(
          Axios.spread((buildRes, roomRes) => {
            this.buildList = buildRes.buildList;
            this.roomList = roomRes.roomList;
            this.roomCode = roomCode;

            for (let build of this.buildList) {
              if (build.BUILD_CODE == buildCode) {
                this.buildName = build.BUILD_NAME;
                this.buildCode = buildCode;
              }
            }
            for (let room of this.roomList) {
              if (room.ROOM_CODE == roomCode) {
                this.roomName = room.ROOM_NAME;
              }
            }
          })
        )
        .catch(err => {
          // Toast(err);
          // this.$toast("网络异常，请稍后重试！");
        });
    },
    //进入页面默认选择房间
    selectDefaultClasses() {
      this.roomClasses = ["房间"];
      this.dataSource = this.roomClasses;
      this.selectAction("房间", 0, false);
    }
  },
  computed: {
    buildNames: function() {
      var arr = [];
      console.log(this.buildList);
      for (let build of this.buildList) {
        arr.push(build.BUILD_NAME);
      }
      return arr;
    },
    roomNames: function() {
      var arr = [];
      for (let room of this.roomList) {
        arr.push(room.ROOM_NAME);
      }
      return arr;
    }
  },
  mounted() {
    console.log(this.$route.query);
    this.areaCode = this.$route.query.areaCode;
    this.areaName = this.$route.query.areaName;
    this.cityCode = this.$route.query.cityCode;
    this.cityName = this.$route.query.cityName;
    console.log(this.$userInfo);
    this.selectDefaultClasses();
    this.fetchBuildClasses();

    this.isNoArea = this.$userInfo.isNoArea == 1 ? true : false;
    if (this.$route.query.selectBack) {
      let type = this.$route.query.roomType;
      let buildCode = this.$route.query.buildCode;
      let roomCode = this.$route.query.roomCode;

      this.searchRoomWithCodes(type, buildCode, roomCode);
    }
    // if(this.$route.params)
  },
  beforeRouteLeave(to, from, next) {
    if (this.showPicker) {
      this.showPicker = false;
      next(false);
    } else {
      if (to.name == "selectArea" && Object.keys(this.cityCode).length != 0) {
        console.log(this.cityCode);
        to.query.city = this.cityCode;
        to.query.cityName = this.cityName;
      }
      console.log(to);
      console.log(from);
      next();
    }
  }
};
</script>

<style scoped>
/deep/.van-picker__cancel {
  color: #3a3a3a;
}
/deep/.van-picker__confirm {
  color: #fb5768;
}
/deep/.van-dialog__confirm,
.van-dialog__confirm:active {
  color: #fb5768;
}
.rightBtn{
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #333333;
  letter-spacing: -0.05px;
  line-height: 32px;
  padding-right: 10px;
  width: 100%;
  text-align: right;
}
/*大字模式*/
.rightBtnThemeL{
  font-size: 17px;
}
.themeL {
  font-size: 18px !important;
}
.listThemeL {
  font-size: 18px !important;
  padding-top: 20px !important;
  padding-bottom: 20px !important;
}
/deep/.themeL .van-field__control[type=search]{
  font-size: 15px !important;
}
/deep/.themeL .van-picker__cancel {
 font-size: 18px !important;
}
/deep/.themeL .van-picker__confirm {
  font-size: 18px !important;
}
/deep/.themeL .van-picker-column__item {
  font-size: 20px !important;
}
</style>
<style >
.van-dialog__footer {
  text-align: center;
}
</style>