import http from "../http";
import { Toast } from "vant";



//获取收藏列表
const getCollectList = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-sys-user/collect/getCollectList', reqParam, "",false).then(result => {
            if (result.resultCode === '0') {
                resolve(result.data)
            }else{
                Toast(result.msg)
                reject(result.msg);
            }
            // result.resultCode === '0' ? resolve(result.data) : reject(result.msg);
        }).catch(error => {
            Toast('网络异常，请稍后重试！');
            reject(error);
        })

    });
}

