const state = {
    previewId: '' //  图片预览id
}

// 定义 getters demo
var getters = {

}

const actions = {
    setPreviewId(context, id) {
        context.commit('setPreviewId', id);
    }
}

const mutations = {
    setPreviewId(state, id) {
        state.previewId = id;
    } 
}
// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
