const state = {
    hasOpenDoor: false, //是否有手机开门按钮
    hasPopWin: false, //是否有弹出框显示
    closePopWin: false, //关闭弹出框状态位
    phoneDoorRefresh: false //是否刷新手机开门
};

// 定义 getters
let getters = {
    getOpenDoorFlag(state) {
        return state.hasOpenDoor
    },

    getPopWinFlag(state) {
        return state.hasPopWin
    },

    getClosePopWinFlag(state) {
        return state.closePopWin
    },
    getPhoneDoorRefreshFlag(state) {
        return state.phoneDoorRefresh
    }
};

const actions = {
    //修改手机开门按钮状态
    setOpenDoorFlag(context, flag) {
        console.log("[vuex.home] setOpenDoorFlag: "+flag);
        context.commit('setOpenDoorFlag', flag);
    },

    //修改当前页面弹出框显示状态, true:显示，false：不显示
    setPopWinFlag(context, flag) {
        console.log("[vuex.home] setPopWinFlag: "+flag);
        context.commit('setPopWinFlag', flag);
    },

    //修改关闭弹窗状态，true:关闭弹窗, false:不关闭弹窗
    setClosePopWinFlag(context, flag) {
        console.log("[vuex.home] setClosePopWinFlag: "+flag);
        context.commit('setClosePopWinFlag', flag);
    },
    //修改手机开门刷新状态，true:刷新, false:不刷新
    setPhoneDoorRefreshFlag(context, flag) {
        console.log("[vuex.home] setPhoneDoorRefreshFlag: "+flag);
        context.commit('setPhoneDoorRefreshFlag', flag);
    }
};

const mutations = {
    // 设置是否有手机开门
    setOpenDoorFlag(state, flag) {
        state.hasOpenDoor = flag;
    },

    // 修改当前页面弹出框显示状态, true:显示，false：不显示
    setPopWinFlag(state, flag) {
        state.hasPopWin = flag;
    },

    // 修改关闭弹窗状态，true:关闭弹窗, false:不关闭弹窗
    setClosePopWinFlag(state, flag) {
        state.closePopWin = flag;
    },

    // 修改手机开门刷新状态，true:刷新, false:不刷新
    setPhoneDoorRefreshFlag(state, flag) {
        state.phoneDoorRefresh = flag;
    }
};

// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
