<template>
  <div style="height:100%;width:100%;display:flex;flex-direction:column;">
    <!--<van-nav-bar id="NaviBar" :title="title" left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title">
    </navigation-bar>
    <div style="flex:1;width:100%;overflow:auto;">
      <div style="padding:12px;" v-html="content"></div>
    </div>
  </div>
</template>

<script>
import { getProtocol,getActivityInfo } from "../../services/MineService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "ActivityIntro",
  components: {
    NavigationBar
  },
  data() {
    return {
      title: "",
      content: "",
      activityId:""
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    getProtocol() {
      let param = { PROTOCOL_CODE: 5 };
      getProtocol(param)
        .then(data => {
          this.title = data.PROTOCOL_NAME;
          this.content = data.CONTENT;
        })
        .catch(error => {
          // this.$toast(error);
        });
    },
     //获取活动信息
    initActivityInfo() {
      let param = { promotionId: this.activityId };
      getActivityInfo(param)
        .then(data => {
          this.title = data.promotionName;
          this.content = data.promotionRuleDesc;
        })
        .catch(error => {
          // this.$toast(error);
        });
    }
  },
  mounted() {
    let query = this.$route.query
    if(Object.keys(query).length == 2){
      this.title = query.title
      this.content = query.content
      return
    }else if(Object.keys(query).length == 1){
      this.activityId = query.promotionId
      this.initActivityInfo()
      return
    }
    this.getProtocol();
  }
};
</script>

<style scoped>
/deep/p {
    display: block;
    margin-block-start: 5px !important;
    margin-block-end: 5px !important;
    margin-inline-start: 0px !important;
    margin-inline-end: 0px !important;
}
/deep/img{
  max-width: 100%;
}
</style>