// iPhone X、iPhone XS
var isIPhoneX = /iphone/gi.test(window.navigator.userAgent) && window.screen.width === 375 && window.screen.height === 812;
// iPhone XR XSMax
var isIPhoneXR = /iphone/gi.test(window.navigator.userAgent) && window.screen.width === 414 && window.screen.height === 896;

var isFullScreen = isIPhoneX || isIPhoneXR;

export default {
    isIPhoneX,
    isIPhoneXR,
    isFullScreen
};