/**
 * 动态加载高德地图
 *
 * @export
 * @param {*} key 高德地图key
 * @param {*} plugins 高德地图插件
 * @param {string} [v='1.4.15'] 高德地图版本
 * @returns
 */export default function loadMap () {
    return new Promise(function (resolve, reject) {
        if (typeof AMap !== 'undefined') {
            // eslint-disable-next-line no-undef
            resolve(AMap)
            return true
        }
        window.onCallback = function () {
            resolve(AMap)
        }
        let script = document.createElement('script')
        script.type = 'text/javascript'
        script.src = `https://webapi.amap.com/maps?v=1.4.15&key=529ff32129046a07fd11ce5a2054a797&plugin=AMap.Driving&plugin=AMap.Autocomplete&plugin=AMap.Geolocation&callback=onCallback`
        script.onerror = reject
        document.head.appendChild(script)
    })
}
