<template>
  <div>
    <!--<van-nav-bar id="NaviBar" title="提交申请" :left-arrow="false" />-->
    <navigation-bar :isShowLeft="isShowIcon" :isShowRight="isShowIcon" pageName="提交申请">
    </navigation-bar>
    <div>
      <!-- 图片 -->
      <div class="topView" :class="{topViewThemeL:bindThemeL}">
        <img src="../../assets/images/home/property_payment/submitted_successfully.png" alt />
      </div>
      <!-- 信息 -->
      <div class="contentFirst" :class="{contentFirstThemeL:bindThemeL}">已提交申请，等待审批</div>
      <div class="contentMsg" :class="{contentMsgThemeL:bindThemeL}">{{message}}</div>
      <div class="contentThird">
        <div class="text" :class="{textThemeL:bindThemeL}">审批通过后，您将获得该房间的家庭成员身份；</div>
        <div class="text" :class="{textThemeL:bindThemeL}">如果您是房主，请前往物业处修改预留手机号后，</div>
        <div class="text" :class="{textThemeL:bindThemeL}">再重新绑定房间</div>
      </div>
      <!-- 按钮 -->
      <div>
        <div class="finishBtn" :class="{btnThemeL:bindThemeL}" @click="checkStoragePermissions" v-if="showWeChatShare">微信通知TA</div>
        <div class="confirmBtn" :class="{btnThemeL:bindThemeL}" style="text-align:center;" @click="closeAction">我知道了</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
import Vue from "vue";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "RoomApply",
  components: {
    NavigationBar
  },
  data() {
    return {
      message: "",
      showWeChatShare: false,
      hasStoragePermission: false,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      isShowIcon: false //是否显示标题的左侧返回按钮
    };
  },
  methods: {
    closeAction() {
      Vue.prototype.$refreshHomePage = true; //yuanl add for 回首页需要刷新小区列表
      this.$router.replace("/homePage"); //qiao add for ZHSQYYPT-5492 ZHSQYYPT-5957
    },
    shareToWechat() {
      //是房主
      if (this.hasStoragePermission) {
        Dialog.confirm({
          title: "确认打开微信?",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.openWechatAndShare();
          })
          .catch(() => {});
      } else {
        Dialog.confirm({
          message: "请您打开存储权限，以正常使用分享功能"
        })
          .then(() => {
            this.requestStoragePermissions();
          })
          .catch(() => {
            //oncancel
          });
      }
    },
    openWechatAndShare() {
      Wechat.share(
        {
          message: {
            title: this.$userInfo.OWNER_NICK_NAME + "请求加入家庭,请您审核",
            description: "联系电话:" + this.$userInfo.PHONE,
            thumb:
              "https://xwjfile.juhaolian.cn/hisense/download/others/roomApply.png",
            media: {
              type: Wechat.Type.WEBPAGE,
              webpageUrl: this.$route.params.url
            }
          },
          scene: Wechat.Scene.SESSION
        },
        () => {
          console.log("share success");
          //alert("Success");
        },
        reason => {
          //alert("Failed: " + reason);
          console.log("share faild");
          this.$toast(reason); //无微信时提示用户未安装微信
        }
      );
    },
    checkStoragePermissions() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareToWechat();
      } else {
        var list = "android.permission.WRITE_EXTERNAL_STORAGE";
        var permissions = cordova.plugins.permissions;
        permissions.checkPermission(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareToWechat();
            } else {
              _this.hasStoragePermission = false;
              _this.shareToWechat();
            }
          },
          function() {
            _this.hasStoragePermission = false;
            _this.shareToWechat();
          }
        );
      }
    },
    requestStoragePermissions() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let _this = this;
      if (isiOS) {
        this.hasStoragePermission = true;
        this.shareToWechat();
      } else {
        var list = ["android.permission.WRITE_EXTERNAL_STORAGE"];
        var permissions = cordova.plugins.permissions;
        permissions.requestPermissions(
          list,
          function(status) {
            if (status.hasPermission) {
              _this.hasStoragePermission = true;
              _this.shareToWechat();
            } else {
              _this.hasStoragePermission = false;
            }
          },
          function() {
            _this.hasStoragePermission = false;
          }
        );
      }
    }
  },
  mounted() {
    console.log(this.$route.params);
    this.message = this.$route.params.message;
    this.showWeChatShare = this.$route.params.showWeChatShare;
  }
};
</script>

<style scoped lang="scss">
.topView {
  margin-top: 30px;
  margin-bottom: 29px;
  text-align: center;
  img {
    width: 250px;
    height: 225px;
  }
}
.topViewThemeL {
  margin-bottom: 5px !important;
}
.contentFirst {
  font-size: 15px;
  color: #121212;
  text-align: center;
  font-weight: bold;
}
.contentFirstThemeL {
  font-size: 20px !important;
  line-height: 40px;
}
.contentMsg {
  font-size: 14px;
  color: #121212;
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
}
.contentMsgThemeL {
  font-size: 17px !important;
  line-height: 35px;
}
.text {
  font-size: 14px;
  color: #333333;
  text-align: center;
}
.textThemeL {
  font-size: 15px !important;
}
.contentThird {
  padding-bottom: 15px;
}
.finishBtn {
  background: #fb5768;
  text-align: center;
  font-size: 16px;
  color: white;
  width: 59%;
  height: 44px;
  line-height: 44px;
  background: #fb5768;
  box-shadow: 0 0 8px 0 rgba(197, 58, 72, 0.39);
  border-radius: 22px;
  margin-left: 20%;
  margin-right: 22%;
  margin-top: 15px;
}
.confirmBtn {
  background: #ffffff;
  border: 1px solid #fb5768;
  border-radius: 22px;
  height: 40px;
  width: 58%;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  color: #fb5768;
  letter-spacing: -0.05px;
  margin-top: 15px;
  margin-left: 20%;
  line-height: 40px;
}
.btnThemeL {
  font-size: 18px !important;
}
</style>
