<template>
  <div class="mainView" :class="{themeL:bindThemeL}">
    <!--<van-nav-bar id="NaviBar" title="个人设置" left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="个人设置" >
    </navigation-bar>
    <div :class="{themeL:bindThemeL}">
      <van-cell
        title="头像设置"
        is-link
        style="align-items: center;margin-bottom:10px;"
        @click="showActionSheet"
      >
        <img
          slot="default"
          style="width:50px;height:50px;border-radius:25px;object-fit:cover"
          :src="(userIcon == '' || userIcon == undefined )? require('../../assets/images/default/user_default_avata.png') : userIcon"
          :key="(userIcon == '' || userIcon == undefined )? require('../../assets/images/default/user_default_avata.png') : userIcon"
        />
      </van-cell>

      <van-cell class="title-class" title="昵称" is-link style="align-items: center;">
        <van-field
            class="value-class"
            v-model="userName"
            placeholder="请输入用户名"
            style="text-align:right;"
            maxlength="18"
            @input="inputAction"
        />
      </van-cell>
      <van-cell
          title="性别"
          :value="sex"
          is-link
          style="align-items: center;height:64px;"
          @click="selectSex"
      >
        <van-field style="text-align:right;" v-model="sex" readonly placeholder="请选择" />
      </van-cell>
    </div>

    <div style="padding:25px;position:relative" :class="{themeL:bindThemeL}">
      <van-button round size="large" color="#FB5768" @click="saveInfo" :disabled="isDisable">保 存</van-button>
    </div>

    <van-popup v-model="showPicker" position="bottom" :class="{themeL:bindThemeL}">
      <van-picker
        show-toolbar
        :columns="columns"
        :default-index="selctedIndex"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>
    <van-action-sheet
      v-model="showAddSheet"
      :actions="actions"
      cancel-text="取消"
      round
      @select="selectActionSheet"
      close-on-click-action
      safe-area-inset-bottom
    />
  </div>
</template>
<script>
// import Vue from 'vue'
import { upDateUserInfo, getUserInfo } from "../../services/MineService";
import { Toast } from "vant";
import { store } from "../../utils/storage";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "UserProfile",
  components:{
    NavigationBar
  },
  data() {
    return {
      showPicker: false,
      showAddSheet: false,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      columns: ["男", "女"],
      selctedIndex: 0,
      sex: "",
      userName: "",
      userIcon: "",
      WJDM: "",
      isDisable: false,
      hasChangeIcon: false,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  methods: {
    inputAction(value) {
      if (value.length >= 18) {
        this.$toast("昵称最多输入18个字");
      } else {
        this.isNormlName(value);
      }
    },

    // 利用 RegExp 对象优雅实现： even(文本框内容)
    isNormlName(even) {
      // 规则对象(flag)
      var flag = new RegExp(
        "[`~!@#$^&*()=|{}':;',\\[\\].<>《》/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？ ]"
      );
      // 判断 even 是否包含特殊字符
      if (flag.test(even)) {
        console.log("包含！");
        this.$toast("请输入有效的昵称");
        return false;
      } else {
        console.log("不包含！");
        return true;
      }
    },
    selectSex() {
      this.showPicker = true;
      this.selctedIndex = this.columns.indexOf(this.sex);
    },
    onConfirm(value) {
      this.sex = value;
      this.showPicker = false;
    },
    showActionSheet() {
      this.showAddSheet = true;
    },
    addSelect(value) {
      console.log(value);
    },
    onClickLeft() {
      this.$router.back(-1);
    },
    // 拉取用户信息
    initUserInfo() {
      let param = { OWNER_CODE: this.$userInfo.OWNER_CODE };
      getUserInfo(param)
        .then(data => {
          this.paras(data);
        })
        .catch(error => {
          // Toast(error);
          console.log(error);
        });
    },
    paras(info) {
      this.userName = info.OWNER_NICK_NAME;
      this.sex = info.SEX == 1 ? "男" : "女";
      this.userIcon = info.PIC_URL ? info.PIC_URL : "";
    },
    //修改用户信息
    commitUserInfo() {
      let sex = this.sex == "男" ? "1" : "0";
      let name = this.userName;
      let param = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        AREA_CODE: this.$userInfo.AREA_CODE,
        OWNER_NICK_NAME: name,
        INTERESTS: "",
        PERSONAL_STATEMENT: "",
        SEX: sex,
        PIC_URL: this.userIcon,
        WJDM: this.WJDM
      };
      console.log(param);
      upDateUserInfo(param)
        .then(data => {
          if (data.success) {
            Toast.success(data.msg);
            var info = this.$userInfo;
            info.PIC_URL = this.userIcon;
            info.OWNER_NICK_NAME = this.userName;
            this.$setUserInfo(info);
            console.log(this.$userInfo.PIC_URL);
            setTimeout(() => {
              this.onClickLeft();
              this.isDisable = false;
            }, 500);
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          // Toast(error);
          console.log("qqq " + error);
          this.$toast("网络异常，请稍后重试！");
          this.isDisable = false;
        });
    },
    saveInfo() {
      this.isDisable = true;
      if (this.userName == "") {
        Toast({ message: "请输入用户名" });
        this.isDisable = false;
        return;
      }
      if (!this.isNormlName(this.userName)) {
        this.isDisable = false;
        return;
      }
      if (this.sex == "") {
        this.isDisable = false;
        Toast("请选择性别");
        return;
      }
      if (this.hasChangeIcon) {
        this.uploadFile(this.userIcon);
      } else {
        this.commitUserInfo();
      }
    },
    selectActionSheet(item, index) {
      index == 0 ? this.useCamera() : this.getPicture();
    },
    //  调用摄像头插件进行拍照
    useCamera() {
      this.$plugin.usePlugin("camera")(
        
        imageData => {
          if (this.$devicePlatform == "android") {
            //裁剪图片
            this.$plugin.usePlugin("crop")(imageData,
              url => {
                //success callback
                console.log("yuanl=> 裁剪url = "+url);
                url = url.split('?');
                this.userIcon = url[0];
                this.hasChangeIcon = true;
              },
              error => {
                //fail callback
                console.log("Crop error. "+JSON.stringify(error));
                if (error.recapture == "true") {
                  console.log("调用相机重拍. ");
                  this.useCamera();
                } else if (error.message == "User cancelled") {
                  console.log("用户取消裁剪操作. ");
                } else {
                  this.$toast("裁剪图片出现异常，请重试！");
                }
              },
              0, //从拍照调用裁剪插件
              1, 1 //限制1:1比例裁剪
            );
          } else {
            this.hasChangeIcon = true;
            this.userIcon = imageData;
          }

          // this.uploadFile(imageData);
        },
        error => {
          console.log("userProfile " + error);
          //this.$toast("调用相机出现异常，请检查设置"); //delete for ZHSQYYPT-6708
          // Toast(error);
        },true,true
      );
    },
    // 从本地相册进行上传
    getPicture() {
      this.$plugin.usePlugin("getPictures")(
        1,
        images => {
          images.forEach(imageData => {
            // this.uploadFile(imageData);
            if (this.$devicePlatform == "android") {
              //裁剪图片
              this.$plugin.usePlugin("crop")(imageData,
                url => {
                  //success callback
                  console.log("yuanl=> 裁剪url = "+url);
                  url = url.split('?');
                  this.userIcon = url[0];
                  this.hasChangeIcon = true;
                },
                error => {
                  //fail callback
                  console.log("Crop error. "+JSON.stringify(error));
                  if (error.recapture == "true") {
                    console.log("调用相机重拍. ");
                    this.useCamera();
                  } else if (error.message == "User cancelled") {
                    console.log("用户取消裁剪操作. ");
                  } else {
                    this.$toast("裁剪图片出现异常，请重试！");
                  }
                },
                1, //从相册调用裁剪插件
                1, 1 //限制1:1比例裁剪
              );
            } else {
              this.hasChangeIcon = true;
              this.userIcon = imageData;
            }
          });
        },
        error => {
          console.log("userProfile line220 " + error);
          //this.$toast("网络异常，请稍后重试！"); //delete for ZHSQYYPT-6708
          // Toast(error);
        },
        true,true
      );
    },
    // 上传图片数据
    uploadFile(fileURL) {
      var _this = this;
      console.log("qiao+++ " + fileURL + " toURL ");
      let loading = Toast.loading({
        duration: 0,
        mask: true,
        message: "加载中..."
      });
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var success = function(result) {
        loading.clear();
        let resData = JSON.parse(result.response);
        if (resData.resultCode == 0) {
          _this.userIcon = resData.data;
          _this.WJDM = resData.data;
          _this.commitUserInfo();
        } else {
          console.log("userProfile line 277 " + result);
          _this.$toast("网络异常，请稍后重试！");
        }
      };
      var fail = function(error) {
        loading.clear();
        console.log("userProfile " + error);
        _this.$toast("网络异常，请稍后重试！");
      };
      var options = new FileUploadOptions();
      var params = {};
      params.file = fileURL;
      params.source = "yzApp";
      params.business = "userPic";
      options.params = params;
      var headers = { contentType: "multipart/form-data" };
      options.headers = headers;
      var ft = new FileTransfer();
      ft.upload(fileURL, encodeURI(reqUrl), success, fail, options);
    }
  },
  mounted() {
    this.userName = this.$userInfo.OWNER_NICK_NAME;
    this.userIcon = this.$userInfo.PIC_URL;
    this.initUserInfo();
  },
  beforeRouteLeave(to, from, next){
    if(this.showPicker) {
      this.showPicker = false
      next(false)
    }else if(this.showAddSheet) {
      this.showAddSheet = false
      next(false)
    }else{
      next()
    }
  }
};
</script>

<style scoped>
/deep/.title-class .van-cell__value {
  display: flex;
  flex: 8;
  justify-content: flex-end;
}
/deep/.van-cell {
  background-color: transparent;
}
/deep/.van-field__control {
  text-align: right;
}
/deep/.van-picker__cancel {
  color: #3a3a3a;
}
/deep/.van-picker__confirm {
  color: #fb5364;
}
/deep/.van-field__body {
  width: 100%;
}

.mainView {
  background-color: #f9f9f9;
  height: 100%;
}

/*大字模式*/
/deep/.themeL .van-cell {
  background-color: transparent;
  font-size: 17px !important;
}
/deep/.themeL .van-button {
  font-size: 18px !important;
}
/*弹出框大字模式*/
/deep/.themeL .van-picker__cancel {
  font-size: 18px !important;
}
/deep/.themeL .van-picker__confirm {
  font-size: 18px !important;
}
/deep/.themeL .van-picker-column__item {
  font-size: 20px !important;
}
/*actionsheet大字模式*/
/deep/.themeL .van-action-sheet__item {
  font-size: 18px !important;
}
</style>