<template>
  <div style="background:#F9F9F9;height:100%;">
    <navigation-bar  @onLeftClick="onClickLeft" pageName="切换城市">
    </navigation-bar>
    <div>
      <van-cell :title="'当前定位：'+cityName" icon="location-o" :class="{themeL:bindThemeL}"/>
    </div>
    <van-divider :style="{padding:'0 10px',margin:'0'}"/>
    <div style="height:100%;overflow: auto;">
      <van-cell
          :title="city.CITY_NAME"
          v-for="city in cities"
          :key="city.CITY_CODE"
          @click="selectCityAction(city)"
          :class="{listThemeL:bindThemeL}"
          :border="hasBorder"
      />
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { getCityList, getCityListNew } from "../../services/MineService";
import { Toast } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import loadMap from '../../components/AMapLoader/loadMap';
export default {
  name: "SelectCity",
  components: {
    NavigationBar
  },
  data() {
    return {
      cities: [],
      selectCity: {},
      cityName:'',
      /*大字模式*/
      bindThemeL: this.$curTheme==0 ? false : true,
      hasBorder: this.$curTheme==0 ? true : false,
    };
  },
  methods: {
    onClickLeft() {
      this.selectCity = {};
      this.$router.back(-1);
    },
    initCityList() {
      /*getCityList()
        .then(data => {
          this.cities = data;
        })
        .catch(error => {
          // Toast(error);
        });*/

      //新接口
      getCityListNew()
        .then(data => {
          //新老接口返回数据字段映射转换
          data.forEach((item, index) => {
            item.CITY_CODE = item.cityCode;
            item.CITY_NAME = item.cityName;
          })

          this.cities = data;
        })
        .catch(error => {
          // Toast(error);
        });
    },
    selectCityAction(city) {
      console.log(city);
      this.selectCity = city;
      this.$router.back(-1);
    },
    getCurrentLocation() {
      let that = this;
      loadMap()
        .then(AMap => {
          AMap.plugin('AMap.Geolocation', function () {
            var geolocation = new AMap.Geolocation({
              enableHighAccuracy: true,
              // 设置定位超时时间，默认：无穷大
              timeout: 10000
            });
            geolocation.getCurrentPosition();
            AMap.event.addListener(geolocation, 'complete', onComplete);
            AMap.event.addListener(geolocation, 'error', onError);
            function onComplete (data) {
              // data是具体的定位信息
              that.cityName = data.addressComponent.city;
            }
            function onError (data) {
              // 定位出错
              console.log('定位失败错误：', data);
            }
          })
        })
        .catch((err) => {
          console.log('地图加载失败！'+err);
        });
    //   console.log(navigator);
    //   navigator.geolocation.getCurrentPosition(
    //     position => {
    //       //定位成功回调
    //       console.log("success");
    //       console.log(position);
    //       var coordtransform = require("coordtransform");
    //       var coord = coordtransform.wgs84togcj02(
    //         position.coords.longitude,
    //         position.coords.latitude
    //       );
    //       let longitude = coord[0]; //118.78307918718679;//gcj[0];//
    //       let latitude = coord[1]; //32.04093850800938;//gcj[1];//
    //       var url =
    //         "https://apis.map.qq.com/ws/geocoder/v1/?location=" +
    //         latitude +
    //         "," +
    //         longitude +
    //         "&key=HC3BZ-W7SHU-GUTVY-2MOH3-HZMC5-F7F3D&get_poi=0&output=json";
    //       console.log(url);
    //       axios
    //         .get(url)
    //         .then(data => {
    //           console.log(data);
    //           console.log(data.data);
    //           console.log(data.data.result);
    //           console.log(data.data.result.address_component);
    //           console.log(data.data.result.address_component.city);
    //           this.cityName = data.data.result.address_component.city;
    //           console.log(this.cityName);
    //         })
    //         .catch(error => {
    //           //定位失败回调
    //           console.log(error);
    //         });
    //     },
    //     error => {
    //       console.log(error);
    //     }
    //   );
    // }
  },
  },
  mounted() {
    this.getCurrentLocation();
      setTimeout(() => {
          if (this.cityName == ''){
              this.cityName = "请检查设置";
          }
      }, 5000);
    this.initCityList();
  },
  beforeRouteLeave(to, from, next) {
    if ((to.name == "selectArea" || to.name == "selectAreaThemeL") && Object.keys(this.selectCity).length != 0) {
      console.log(this.selectCity)
      to.query.city = this.selectCity;
    }
    console.log(to);
    console.log(from);
    next();
  }
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
.themeL .van-cell__title {
  font-size: 18px !important;
  line-height: 32px !important;
}
.themeL .van-cell__left-icon {
  height: 32px !important;
  line-height: 32px !important;
}
.listThemeL .van-cell__title {
  font-size: 18px !important;
  line-height: 20px !important;
}
</style>