<template>
  <div style="display:flex;flex-direction:column;height:100%;">
    <!--<van-nav-bar
      id="NaviBar"
      title="积分中心"
      left-arrow
      z-index="2"
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
      <template slot="right">
        <div style="height:44px">
          <van-icon name="info-o" />
          <span style="font-size:12px;margin-left:3px;color:#333333;">积分说明</span>
        </div>
      </template>
    </van-nav-bar>-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="积分中心" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
        <div class="rightTitle">
          <van-icon name="info-o" size="20"/>
          <div class="rightBtn" @click="onClickRight">积分说明</div>
        </div>
      </template>
    </navigation-bar>

    <van-pull-refresh v-model="headerLoading" @refresh="onRefresh">
      <div class="TopBgView" :class="{hidePoint : totalPoints == '正在获取积分'}">
        <div style="text-align:center;position:relative;" :class="{hasActivity:showActivity}">
          <span
            class="pointNum"
          >{{(totalPoints == '正在获取积分' ? '' : (totalPoints == 0 ? '暂无积分' : totalPoints))}}</span>
          <!-- 小区转盘活动view -->
          <div class="activity_logo" @click="goPointsActivity" v-if="showActivity">
            <div class="activity_rotate"></div>
            <div class="activity_pointer"></div>
            <div class="activity_name"></div>
          </div>
        </div>

        <div style="text-align:center;" v-if="willExpirePoint != 0">
          <span style="color:white;font-size:13px;">{{willExpirePoint}}积分即将过期</span>
        </div>
        <div class="weekView" v-if="showSignView">
          <!-- //周一至周日 -->
          <div class="dayList">
            <van-row type="flex" justify="space-between">
              <van-col
                span="4"
                :class="{day : true, active:  getSignData.signFlag==0&&getSignData.signDays== index||getSignData.signFlag==1&&getSignData.signDays== index+1 }"
                v-for="(day,index) in signDays"
                :key="index"
              >{{day}}</van-col>
            </van-row>
          </div>
          <!-- 加分项 + 连续天数展示 签到按钮 -->
          <div class="pointView">
            <!-- 加分数值 -->
            <van-row type="flex" justify="space-between">
              <van-col class="point" span="4" v-for="(point,index) in points" :key="index">
                <van-image
                  style="width: 70%;height: 30px;"
                  fit="contain"
                  :src="require('../../assets/images/home/checkin/icon_checkin_tips.png')"
                />
                <span class="pointText" :class="{iosStyle:isIosDevice}">+{{point}}</span>
              </van-col>
              <!-- 连续天数指示图标 -->
            </van-row>
            <van-row type="flex" justify="space-between">
              <van-col span="4" v-for="i in 7" :key="i">
                <div style="position: relative;width: 100%;height: 40px;">
                  <img
                    class="signImage"
                    :src="activeIndex >= i ? require('../../assets/images/home/checkin/icon_checkin_star_Light.png') : require('../../assets/images/home/checkin/icon_checkin_star_gray.png')"
                  />
                  <div :class="{startLine:true,  activeLine : activeIndex>=i}" v-if="i != 7"></div>
                </div>
              </van-col>
            </van-row>
            <!-- 连续天数text -->
            <div class="tipText">
              <span>当前已连续签到</span>
              <span style="font-size: 18px;color: #FB5768;">{{activeIndex}}</span>
              <span>天</span>
            </div>
            <!-- 签到按钮 -->
            <div style="text-align: center;">
              <van-button
                :color="isSigned ? '#666666' : '#FB5768'"
                :disabled="isSigned"
                round
                style="width: 135px;height: 40px;line-height: 40px;margin-bottom:10px;"
                @click="signAction"
              >{{isSigned ? "今日已签到" : "立即签到"}}</van-button>
            </div>
          </div>
        </div>
      </div>

      <div>
        <van-list v-model="footerLoading" :finished="finished" @load="onLoad">
          <van-cell
            :title="item.content_name"
            :value="(item.point_value >0  ? '+' : '') +item.point_value+'积分'"
            size="large"
            :label="item.create_time"
            v-for="(item,index) in list"
            :key="index"
          />
        </van-list>
      </div>
    </van-pull-refresh>
    <van-dialog
      style="border:none !important;"
      v-model="showDialog"
      title="签到成功，恭喜您积分"
      confirm-button-text="我知道了"
      confirm-button-color="#FFF"
      :message="signSuccessText"
    ></van-dialog>
  </div>
</template>

<script>
  import {MyPoints} from './MyPoints'
  export default {
    name: 'MyPoints',
    mixins:[MyPoints]
  }
</script>

<style scoped>
@keyframes smallTransTable {
  from {
    transform: rotateZ(0deg);
  }
  to {
    transform: rotateZ(359deg);
  }
}

.rightTitle{
  display: flex;
  align-items: center;
}
.rightBtn{
  font-family: PingFangSC-Regular;
  font-size: 14PX;
  color: #333333;
  letter-spacing: -0.05PX;
  line-height: 32PX;
  padding-right: 10PX;
  padding-left: 5PX;
  width: 100%;
  text-align: right;
}

/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.van-pull-refresh {
  flex: 1;
  overflow: auto;
}
/deep/.van-dialog {
  height: 161px;
}
/deep/.van-dialog .van-button--large {
  width: 108px;
  height: 36px;
}
/deep/.van-dialog__header {
  font-family: PingFangSC-Medium;
  font-size: 16px;
  color: #333333;
  letter-spacing: -0.05px;
  margin-top: 30px;
  padding-top: 0px;
}
/deep/.van-dialog__message {
  font-size: 28px;
  color: #fb5768;
  font-weight: bolder;
}
/deep/.van-dialog__footer {
  background-color: #ffffff;
}
/deep/.van-hairline--top::after {
  border: none;
}

.TopBgView {
  background-image: linear-gradient(-180deg, #fc4e4e 0%, #542fc8 100%);
  padding-top: 10px;
}
.hidePoint{
  padding-top: 0px;
}

.pointNum {
  font-family: PingFangSC-Medium;
  font-size: 30px;
  color: #fff;
  letter-spacing: 0.16px;
  text-align: center;
}

.hasActivity {
  height: 70px;
}

.weekView {
  padding: 10px 20px 20px 20px;
  /* margin-bottom: 15px; */
}

.pointView {
  margin-top: -1px;
  padding-top: 15px;
  background: url('../../assets/images/personal_center/pointCenter-weekBg.png')  no-repeat;
  background-size: 100% 100%;
  /* box-shadow: 0 0 7px 0 rgba(255,192,59,0.51); */
}

.day {
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #ffc6a5;
  letter-spacing: -0.04px;
}

.active {
  background: url('../../assets/images/personal_center/pointCenter-dayBg.png') no-repeat;
  background-size: 100% 100%;
  color: #333333;
  /* box-shadow: 0 0 7px 0 rgba(255,192,59,0.51); */
}

.startLine {
  background-color: #fff4e4;
  height: 3px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1.5px;
  z-index: 0;
}

.signImage {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 50%;
  left: 50%;
  margin-top: -15px;
  margin-left: -15px;
  z-index: 1;
}

.activeLine {
  background-color: #ffd69b;
}

.point {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.pointText {
  height: 20px;
  font-size: 12px;
  font-family: PingFangSC-Medium;
  color: #ffffff;
  transform: rotate(20deg);
  -ms-transform: rotate(20deg);
  /* IE 9 */
  -moz-transform: rotate(20deg);
  /* Firefox */
  -webkit-transform: rotate(20deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(20deg);
  position: absolute;
}
.isIosDevice{
  top: 4px;
  left: 17px;
}
.tipText {
  padding-top: 5px;
  padding-bottom: 10px;
  text-align: center;
  font-size: 14px;
  color: #333333;
}
.activity_logo {
  width: 80px;
  height: 80px;
  position: absolute;
  right: 6px;
  bottom: -10px;
  background: url(../../assets/images/home/lucky_turning/luck.png) center
    no-repeat;
  background-size: contain;
}

.activity_rotate {
  width: 44px;
  height: 44px;
  background: url(../../assets/images/home/lucky_turning/luck_biaopan.png)
    center no-repeat;
  background-size: contain;
  position: absolute;
  top: 25px;
  left: 16px;
  animation: smallTransTable infinite linear 2s;
}

.activity_pointer {
  width: 80px;
  height: 80px;
  background: url(../../assets/images/home/lucky_turning/luck_coin.png) center
    no-repeat;
  background-size: contain;
  position: absolute;
  top: 8px;
  left: -6px;
}

.activity_name {
  width: 55px;
  height: 23px;
  background: url(../../assets/images/home/lucky_turning/luck_title.png) center
    top no-repeat;
  background-size: contain;
  position: absolute;
  /* bottom: -32px; */
  top: 57px;
  left: 9px;
  animation: pointsTital 1 linear 0.5s forwards;
}
</style>
