import {getRecommendGoodsService,refeshRecommendGoodsService,getGoodsForCatePageService,getGoodsForCateService,getGoodsByShopService,getGoodsForSearchService} from '../../services/goodsService';
import { getAllOrdersPlanB, getunevaluatedOrders} from "../../services/orderListService";
const state = {
    orderListPage:1,//订单列表
    pageNumber: 1, //优品首页
    moreGoodsPageNumber:1, //更多分类
    shopGoodsPageNumber:1,  //店铺商品
    goodsSearchPageNumber:1,  //商品搜索

    isShowHomeWindow:true, //首页弹窗是否显示过
    hasShowThemeAlert:false, //大字模式弹框
    shopFlag:-1, //店铺状态 0 从支付页面跳转的
    flag:-1, //订单列表 10 从支付页面跳转的
    tabIndex:0,//订单列表的tab--liumeiqin
    tabFlag:false,//订单列表返回上一页状态--liumeiqin
    afterTabIndex:0,//售后列表返回上一页状态--liumeiqin
    flashSaleTabIndex:0,//秒杀列表返回上一页状态--liumeiqin

    scrollTopNum:0, //订单列表滑动高度
    listLengthNum:0, //订单列表数据长度

    setSpuCode:0,
    setQualityCode:0,
    setActivityCode:0,
    setFlashSaleSpuCode:0,
}

// 定义 getters demo
var getters = {}

const actions = {
    /**
     * 订单列表  全部商品
     * */
    //获取订单列表列表
    getOrderListGoods(context, reqData) {
        return new Promise((resolve, reject) => {
            getAllOrdersPlanB(reqData).then(noteData => {
                context.commit('orderListPageChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getEvaluatedGoods(context, reqData) {
        return new Promise((resolve, reject) => {
            getunevaluatedOrders(reqData).then(noteData => {
                context.commit('orderListPageChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    /**
    * 优品首页
    */
    //获取推荐商品列表
    getRecommendGoods(context, reqData) {
        return new Promise((resolve, reject) => {
            getRecommendGoodsService(reqData).then(noteData => {
                context.commit('pageNumChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getnewRecommendGoods(context, reqData) {
        return new Promise((resolve, reject) => {
            refeshRecommendGoodsService(reqData).then(noteData => {
                // context.commit('pageNumChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },

    /**
     *更多分类
     */
    getGoodsForCate(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsForCateService(reqData).then(noteData => {
                context.commit('moreGoodsPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getGoodsForNewCate(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsForCateService(reqData).then(noteData => {
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getGoodsForCatePageStore(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsForCatePageService(reqData).then(noteData => {
                context.commit('moreGoodsPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    /**
     *店铺商品
     */
    getGoodsForShop(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsByShopService(reqData).then(noteData => {
                context.commit('shopGoodsPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getNewGoodsForShop(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsByShopService(reqData).then(noteData => {
                // context.commit('shopGoodsPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },

    /**
     * 店铺搜索
     * */
    getGoodsForSearch(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsForSearchService(reqData).then(noteData => {
                context.commit('goodsSearchPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },
    getNewGoodsForSearch(context, reqData) {
        return new Promise((resolve, reject) => {
            getGoodsForSearchService(reqData).then(noteData => {
                // context.commit('goodsSearchPageNumberChange', {pageNumber: reqData.page});
                resolve(noteData);
            }, fail => {
                reject(fail);
            })
        })
    },

}

const mutations = {
    //订单列博熬当前数据分页数
    orderListPageChange(state, payload) {
        if (payload.custPageNum) {
            state.orderListPage = payload.pageNumber
        } else {
            state.orderListPage += 1;
        }
    },
    //优品首页当前数据分页数
    pageNumChange(state, payload) {
        if (payload.custPageNum) {
            state.pageNumber = payload.pageNumber
        } else {
            state.pageNumber += 1;
        }
    },
    //更多分类当前数据分页数
    moreGoodsPageNumberChange(state, payload) {
        if (payload.custPageNum) {
            state.moreGoodsPageNumber = payload.pageNumber
        } else {
            state.moreGoodsPageNumber += 1;
        }
    },

    //店铺商品当前数据分页数
    shopGoodsPageNumberChange(state, payload) {
        if (payload.custPageNum) {
            state.shopGoodsPageNumber = payload.pageNumber
        } else {
            state.shopGoodsPageNumber += 1;
        }
    },
    goodsSearchPageNumberChange(state, payload){
        if (payload.custPageNum) {
            state.goodsSearchPageNumber = payload.pageNumber
        } else {
            state.goodsSearchPageNumber += 1;
        }
    },
    isShowHomeWindowChange(state, payload){
        if (payload.custPageNum) {
            state.isShowHomeWindow = payload.isShowHomeWindow
        }
    },
    hasShowThemeAlertChange(state, payload){
        if (payload.custPageNum) {
            state.hasShowThemeAlert = payload.hasShowThemeAlert
        }
    },
    shopFlagChange(state, payload){
        if (payload.custPageNum) {
            state.shopFlag = payload.shopFlag
        }
    },
    setSpuCodeChange(state, payload){

        state.setSpuCode = payload.spuCode

    },
    setQualityCodeChange(state, payload){

        state.setQualityCode = payload.qualityCode

    },
    setActivityCodeChange(state, payload){

        state.setActivityCode = payload.activityCode

    },
    setFlashSaleSpuCodeChange(state, payload){
        state.setFlashSaleSpuCode = payload.flashSaleSpuCode

    },
    orderFlagChange(state, payload){
        state.flag = payload.flag
    },
    leaveOrderList(state,payload){
        state.tabIndex=payload.tabIndex
        state.tabFlag=payload.tabFlag
        state.afterTabIndex=payload.afterTabIndex
        state.flashSaleTabIndex=payload.flashSaleTabIndex
    },
    setScrollTopNumChange(state,payload){
        state.scrollTopNum=payload.scrollTopNum
    },
    setListLengthNumChange(state,payload){
        state.listLengthNum=payload.listLengthNum
    },
}
// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
