<template>
  <div style="height:100%;">
    <div style="padding-top:1px;background:#F9F9F9;" :class="{themeL:bindThemeL}">
      <div class="cellItem" v-for="(item,index) in list" :key="index">
        <!-- 成员信息 -->
        <div class="info">
          <div style="display:flex;max-width: 75%">
            <div style="display:flex;align-items:center;margin-right:10px;">
              <img
                style="width:38px;height:38px;border-radius:29px;"
                :src="item.PIC_URL.length == 0 ? require('../../assets/images/default/user_default_avata.png') : item.PIC_URL"
              />
            </div>
            <div style="display:flex;flex-direction:column">
              <span class="memberName">{{item.OWNER_NICK_NAME}}</span>
              <span class="phoneNum">{{item.PHONE}}</span>
            </div>
          </div>
          <div style="display:flex;align-items:center;min-width: 20%">
            <img
              style="width:15px;height:15px;margin-right: 5px;"
              src="../../assets/images/mall/call_small.png"
            />
            <span class="btnText" @click="callPhone(item.PHONE)">联系TA</span>
          </div>
        </div>
        <!-- 登录本人显示解除绑定 -->
        <div v-if="item.PHONE == minePhone || isOwner">
          <van-divider style="border: 0 solid #E5E5E5;padding:0 16px;margin: 0;" />
          <div style="text-align: right;">
            <van-button
              style="width:90px;height:30px;line-height:30px;margin:15px;font-size:12px;"
              :style="btnStyle"
              round
              color=" #BDBDBD"
              plain
              @click="removeBindFamily(item)"
            >解除绑定</van-button>
          </div>
        </div>
      </div>
      <div style="text-align:center;" v-if="isOwner && list.length == 0" >
        <span style="font-size:14px;color:#333333;">目前家里就您自己，快去邀请家人来注册绑定吧~</span>
      </div>
      <!--只绑定虚拟小区时显示如下-->
      <div class="noList" v-if="!isOwner && list.length == 0" >
        <img src="../../assets/images/default/no_home_img.png" />
        <div>暂无家庭成员哦~</div>
      </div>
    </div>
  </div>
</template>

<script>
import { removeBindFamily } from "../../services/MineService";
import { Toast,Dialog } from "vant";

export default {
  name: "MemberList",
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      minePhone: this.$userInfo.PHONE,
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      btnStyle: {
        fontSize:this.$curTheme==0?"12px":"15px",
        width:this.$curTheme==0?"90px":"100px"
      }
    };
  },
  props: {
      isOwner:false,
  },
  methods: {
    removeBindFamily(item) {
      Dialog.confirm({
        title: "确认删除家庭关系？",
        confirmButtonColor:"#FB5768"
      })
        .then(() => {
          this.confirmRemoveBind(item);
        })
        .catch(() => {
          // on cancel
        });
    },
    confirmRemoveBind(item) {
      let param = {
        OWNER_CODE: item.OWNER_CODE,
        ROOM_CODE: item.ROOM_CODE,
        UUID: item.UUID
      };
      removeBindFamily(param)
        .then(data => {
          this.list.splice(this.list.indexOf(item), 1);
        })
        .catch(error => {
          // Toast(error);
        });
    },
    callPhone(phone){
      var num = phone
      window.location.href = 'tel://' + num
    },

  },
  mounted() {
  }
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}


.cellItem {
  background-color: #ffffff;
  margin: 15px;
  border-radius: 7px;
}
.info {
  height: 64px;
  display: flex;
  padding-right: 12px;
  padding-left: 12px;
  justify-content: space-between;
  align-items: center;
}

.memberName {
  font-weight: bold;
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
}
.themeL .memberName {
  font-size: 17px;
  height: 30px;
  line-height: 30px;
}
.phoneNum {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #888888;
  letter-spacing: -0.04px;
}
.themeL .phoneNum {
  font-size: 15px;
}
.btnText {
  font-size: 14px;
  color: #3a3a3a;
  letter-spacing: -0.04px;
}
.themeL .btnText {
  font-size: 16px;
}
/*只绑定虚拟小区空列表*/
.noList img {
  margin: 125px 100px 20px 100px;
  width: 180px;
  display: inline-block;
}
.noList div {
  font-family: PingFangSC-Regular;
  letter-spacing: 0;
  text-align: center;
  width: 100%;
  font-size: 16px;
  color: #9A9A9A;
}
</style>