/*================================================
* 公共处理函数
* ================================================*/
import Vue from "vue";
import { Dialog } from "vant";

/* 手机号脱敏处理，如18912345678，脱敏后为：189****5678 */
const phoneMask = function(phone) {
  if (phone.indexOf("*") != -1) {
    //测试脱敏的手机号单独处理，正常手机号不应该走这里
    let pat = /(\d{2})\d*(\d{4})/;
    let modify = phone.replace(pat,'$1****$2');
    return modify;
  }

  let pat = /(\d{3})\d*(\d{4})/;
  let modify = phone.replace(pat,'$1****$2');
  return modify;
};

/*微信分享*/
function shareToWechat() {
  let hasStoragePermission = false;
  let shareUrl = "";
  let title = "";
  let description = "";
  let thumb = "";
  let successCb = null;
  let failCb = null;

  /*外部调用接口*/
  this.share = function (type,param,successCb,failCb) {
    this.init(param,successCb,failCb);
    this.checkStoragePermissions(type);
  }

  /*参数初始化*/
  this.init = function(param,successCb,failCb) {
    if (param) {
      this.shareUrl = param.shareUrl;
      this.title = param.title;
      this.description = param.description;
      this.thumb = param.thumb;
    }
    this.successCb = successCb;
    this.failCb = failCb;
  }

  /*先检查权限，再分享
    入参：
    1： 微信好友
    2： 朋友圈
  */
  this.checkStoragePermissions = function(x) {
    console.log("shareToWechat.checkStoragePermissions: x = "+x)

    if (Vue.prototype.$devicePlatform == "pc") {
      if (this.successCb != null) {
        this.successCb();
      }
      return;
    }

    const platform = navigator.userAgent;
    const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    let _this = this;
    if (isiOS) {
      _this.hasStoragePermission = true;
      _this.shareAction(x);
    } else {
      var list = "android.permission.WRITE_EXTERNAL_STORAGE";
      var permissions = cordova.plugins.permissions;
      permissions.checkPermission(
        list,
        function(status) {
          if (status.hasPermission) {
            _this.hasStoragePermission = true;
            _this.shareAction(x);
          } else {
            _this.hasStoragePermission = false;
            _this.shareAction(x);
          }
        },
        function() {
          _this.hasStoragePermission = false;
          _this.shareAction(x);
        }
      );
    }
  };

  this.shareAction = function(x){
    console.log("shareToWechat.shareAction: x = "+x)
    if (this.hasStoragePermission) {
      this.shareToFriend(x)
    }else{
      Dialog.confirm({
        message: "请您打开存储权限，以正常使用分享功能"
      })
        .then(() => {
          this.requestStoragePermissions(x);
        })
        .catch(() => {
          //oncancel
        });
    }
  };

  this.requestStoragePermissions = function(x) {
    console.log("shareToWechat.requestStoragePermissions: x = "+x)
    const platform = navigator.userAgent;
    const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    let _this = this;
    if (isiOS) {
      this.hasStoragePermission = true;
      this.shareAction(x);
    } else {
      var list = ["android.permission.WRITE_EXTERNAL_STORAGE"];
      var permissions = cordova.plugins.permissions;
      permissions.requestPermissions(
        list,
        function(status) {
          if (status.hasPermission) {
            _this.hasStoragePermission = true;
            _this.shareAction(x);
          } else {
            _this.hasStoragePermission = false;
          }
        },
        function() {
          _this.hasStoragePermission = false;
        }
      );
    }
  };
  //分享到朋友圈或者微信好友
  this.shareToFriend = function(x) {
    console.log("shareToWechat.shareToFriend: x = "+x+",this.shareUrl="+this.shareUrl)
    Wechat.share(
      {
        message: {
          title: this.title,
          description: this.description,
          thumb: this.thumb,
          media: {
            type: Wechat.Type.WEBPAGE,
            webpageUrl: this.shareUrl
          }
        },
        scene: x == 1 ? Wechat.Scene.SESSION : Wechat.Scene.TIMELINE //1是微信好友 2是朋友圈
      },
      () => {
        if (this.successCb != null) {
          this.successCb();
        }
      },
      reason => {
        if (this.failCb != null) {
          this.failCb(reason);
        }
      }
    );

  }
}

var gShareAction = new shareToWechat();

export {
  phoneMask,
  gShareAction
};