<template>
  <div style="height:100%;background:#F9F9F9;display:flex;flex-direction:column;">
    <navigation-bar  @onLeftClick="onClickLeft" pageName="绑定授权" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
        <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onClickRight">管理</div>
      </template>
    </navigation-bar>
    <div style="height:100%;overflow: scroll">
      <!--房产信息展示区-->
      <div class="areaTitle">
        <div>
          {{showAreaInfo}}
        </div>
        <div>
          {{areaMember}}
        </div>
      </div>
      <!--家庭成员列表-->
      <div class="memberList">
        <div class="memberItem" v-for="(item, index) in memberList">
        <span ref="TagName" v-html="item.tagName">
<!--          {{item.tagName}}-->
        </span>
          <div class="ownerLabelBox" v-if="item.relateFlag=='1'">房主</div>
          <div class="ownerLabelBox memberLabelBox" v-if="item.relateFlag=='0'">成员</div>
        </div>
      </div>

      <!--家庭成员待授权列表-->
      <div class="memberList" style="margin-top: 20px" v-if="memberListOther.length > 0">
        <div class="memberItem" style="color:#333333">
          您可以将此房产家庭成员身份授权给以下人员：
        </div>
        <!--<div class="memberItem" v-for="(item, index) in memberListOther">
        <span>
          {{item.tagName}}
        </span>
        </div>-->
        <van-checkbox-group v-model="checkResult">
          <van-checkbox  checked-color="#FB5768" v-for="(item,index) in memberListOther" :name="index" :key="index" ref="otherTagName">{{item.tagName}}</van-checkbox>
        </van-checkbox-group>
      </div>

      <div v-if="memberListOther.length > 0">
        <div class="authBtn" :class="{btnThemeL:bindThemeL}" @click="auth">授予家庭成员身份</div>
      </div>
      <div class="authBtn inviteBtn" :class="{btnThemeL:bindThemeL}" @click="invite">微信邀请家庭成员</div>
    </div>
    <!--微信邀请家庭成员-->
    <div class="invitePopWin" v-if="isShowInvitePop">
      <div class="invitePopBox">
        <div class="title">
          微信邀请家庭成员
        </div>
        <!-- 输入手机号，调起手机号键盘 -->
        <van-field v-model="invitePhone" type="tel" placeholder="请输入家庭成员手机号"/>
        <!--微信图标-->
        <div class="wechatImg" @click="shareToWechat">
          <img src="@/assets/images/common/share_wechat.png" alt="">
        </div>
        <!--微信好友-->
        <div class="wechatTxt" @click="shareToWechat">
          微信好友
        </div>

        <div class="closeBtn">
          <img src="@/assets/images/common/icon_close.png" alt="" @click="closePopWin">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import { getRoomOwners, addRoomMember, getShareRoomUrl } from "../../services/MineService";
  import {phoneMask, gShareAction} from "../../utils/common";
  import { Dialog } from "vant";

  export default {
    name: "MyHousesBindAuth",
    components: {
      NavigationBar
    },
    mounted() {
      this.showAreaInfo = this.$route.query.areaName
        +"-"+this.$route.query.buildName
        +"-"+this.$route.query.unitNo
        +"-"+this.$route.query.roomName;
      this.areaCode = this.$route.query.areaCode;
      this.roomCode = this.$route.query.roomCode;

      console.log("MyHousesBindAuth：roomCode = "+this.roomCode)
      if (this.roomCode == "") {
        this.$toast("您已解绑该房间")
        this.$router.back(-1);
        return;
      }

      //获取业主当前房产已绑定用户列表和此业主在此小区其他房产下绑定人员列表
      this.getAreaMemberList();
    },
    data() {
      return {
        headerItemWidth:{
          width:'110px'
        },
        showAreaInfo: "",
        areaMember: "当前成员如下：",
        memberList: [
          /*{
            tagName: "张三张三张三张三张三张三张三张三(132****2550)",
            relateFlag: "0"
          },
          {
            tagName: "9187_YZ(132****1234)",
            relateFlag: "0"
          },
          {
            tagName: "9187_YZ(132****1234)",
            relateFlag: "1"
          },
          {
            tagName: "业主昵称(132****1234)",
            relateFlag: "0"
          }*/
        ],
        memberListOther: [
          /*{
            tagName: "张三张三张三张三张三张三张三张三张三(132****1234，12号楼-1-1103)",
            ownerCode: "111",
            ownerName: "张三",
            ownerNickName: "张三张三张三张三张三",
            phone:"132****1234",
            buildName:"9999号楼",
            unitNo:"3",
            roomName:"402",
          },
          {
            tagName: "李四(132****1234，12号楼-1-1103)",
            ownerCode: "222",
            ownerName: "李四",
            ownerNickName: "李四",
            phone:"132****1234",
          },
          {
            tagName: "王五(132****1234，12号楼-1-1103)",
            ownerCode: "333",
            ownerName: "王五",
            ownerNickName: "王五",
            phone:"132****1234",
          },
          {
            tagName: "陈六(132****1234，12号楼-1-1103)",
            ownerCode: "444",
            ownerName: "陈六",
            ownerNickName: "陈六",
            phone:"132****1234",
          }*/
        ],
        checkResult: [],

        //保存传参
        areaCode: "",
        roomCode: '',

        //是否展示微信邀请弹窗
        isShowInvitePop: false,
        //邀请家庭成员号码
        invitePhone: "",
        //邀请之前先检查权限
        hasStoragePermission:false,

        /*大字模式*/
        bindThemeL: this.$curTheme==0?false:true
      };
    },
    methods: {
      onClickLeft() {
        this.$router.back(-1);
      },
      onClickRight() {
        //this.$router.push("/myFamilyNew");
        this.$router.push({
          path: "/myFamilyNew",
          query: {
            areaCode: this.areaCode,
            roomCode:this.roomCode
          }
        });
      },
      //获取业主当前房产已绑定用户列表和此业主在此小区其他房产下绑定人员列表
      getAreaMemberList() {
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode:this.areaCode,
          roomCode:this.roomCode
        };

        getRoomOwners(param)
          .then(result => {
            if (result && result.resultCode == 0) {
              //当前小区当前房间的成员列表
              if (result.data.currentRoomowners) {
                result.data.currentRoomowners.forEach((item, index) => {
                  item.tagName = item.ownerNickName+"("+phoneMask(item.phone)+")";
                })

                this.memberList = result.data.currentRoomowners;

                /*let w = document.documentElement.clientWidth || document.body.clientWidth;
                this.$nextTick(()=>{
                  this.$refs.TagName.map((item,index)=>{
                    if(item.offsetWidth>(w/2)){
                      this.memberList[index].tagName = this.memberList[index].ownerNickName+"<br\>("+phoneMask(this.memberList[index].phone)+")";
                    }
                  })
                })*/
              }

              //当前小区其它房间的成员列表
              if (result.data.otherRoomowners) {
                result.data.otherRoomowners.forEach((item, index) => {
                  item.tagName = item.ownerNickName+"("+phoneMask(item.phone)+"，"
                    +item.buildName
                    +"-"+item.unitNo
                    +"-"+item.roomName+")";
                })

                this.memberListOther = result.data.otherRoomowners;
                /*let w = document.documentElement.clientWidth || document.body.clientWidth;
                this.$nextTick(()=>{
                  console.log(this.$refs.otherTagName)
                  this.$refs.otherTagName.map((item,index)=>{
                    console.log(item.$el.childNodes[1].offsetWidth)
                    if(item.$el.childNodes[1].offsetWidth>(w*0.8)){
                      console.log(this.memberListOther[index])
                      this.memberListOther[index].tagName = this.memberListOther[index].ownerNickName+"<br\>("+phoneMask(this.memberListOther[index].phone)+"，"
                              +this.memberListOther[index].buildName
                              +"-"+this.memberListOther[index].unitNo
                              +"-"+this.memberListOther[index].roomName+")";
                      item.$el.childNodes[1].innerHTML = this.memberListOther[index].tagName;
                    }
                  })
                })*/
              }

            } else {
              console.log("【Error】getAreaMemberList: "+result.msg);
            }
          })
          .catch(error => {
            console.log(error);
          });
      },
      //点击授权按钮
      auth() {
        console.log("选择项： "+this.checkResult)
        if (this.checkResult.length == 0) {
          this.$toast("请选择相关人员授予家庭成员身份")
          return;
        }

        let param = {
          "ownerInfo": [],
          "roomAllName": this.showAreaInfo,
          "roomCode": this.roomCode,
          "userCode": this.$userInfo.OWNER_CODE
        };

        this.checkResult.forEach((item, index) => {
          console.log("item = "+item+", index = "+index)
          param.ownerInfo.push({
            "ownerCode": this.memberListOther[item].ownerCode,
            "ownerName": this.memberListOther[item].ownerName
          })
        });
        console.log("param: "+JSON.stringify(param))


        addRoomMember(param)
          .then(result => {
            console.log("addRoomMember: result = "+JSON.stringify(result))
            if (result.resultCode == "0") {
              //授权成功
              let message = "授权成功！"
              message += this.memberListOther[this.checkResult[0]].ownerNickName
              if (this.checkResult.length > 1) {
                message += "（等"+this.checkResult.length+"人）\n"
              } else {
                message += "\n"
              }
              message += "已成为【"+this.showAreaInfo+"】家庭成员"
              Dialog.alert({
                message: message,
                closeOnClickOverlay: true
              }).then(() => {
                this.checkResult = []
                //重新获取家庭成员列表
                this.getAreaMemberList();
              });

            } else if (result.resultCode == "10023") {
              //已授权
              let message = ""
              message += this.memberListOther[this.checkResult[0]].ownerNickName
              if (this.checkResult.length > 1) {
                message += "（等"+this.checkResult.length+"人）\n"
              } else {
                message += "\n"
              }
              message += "已绑定【"+this.showAreaInfo+"】 请勿重复授权"
              Dialog.alert({
                message: message,
                closeOnClickOverlay: true
              }).then(() => {
                this.checkResult = []
              });

            } else {
              this.$toast(result.msg)
              this.checkResult = []
            }
          })
          .catch(error => {
            console.log(error);
          });
      },
      //点击微信邀请家庭成员按钮
      invite() {
        this.invitePhone = "";
        this.isShowInvitePop = true;
      },
      //关闭邀请弹窗
      closePopWin() {
        this.isShowInvitePop = false;
      },
      //分享邀请到微信好友
      shareToWechat() {
        let _this = this;
        console.log("shareToWechat: input phone = "+this.invitePhone)

        //校验手机号
        let phoneNumreg = /^1[0-9]{10}$/;
        if (this.invitePhone === undefined || this.invitePhone.length == 0) {
          this.$toast("请输入手机号");
          return;
        } else if (!phoneNumreg.test(this.invitePhone)) {
          this.$toast("请输入有效的手机号码");
          return;
        }

        //获取分享url
        let param = {
          "ownerCode": this.$userInfo.OWNER_CODE,
          "ownerName": this.$userInfo.OWNER_NAME,
          "ownerPhone": this.$userInfo.PHONE,
          "phone": this.invitePhone,
          "roomAllName": this.showAreaInfo,
          "roomCode": this.roomCode,
          "shareTo": "1"
        };
        console.log("shareToWechat: param = "+JSON.stringify(param))
        getShareRoomUrl(param).then(response => {
          console.log("shareToWechat: response = "+JSON.stringify(response))
          if (response.resultCode == "0") {
            _this.isShowInvitePop = false;
            let shareUrl = response.data;
            let title = this.$userInfo.OWNER_NAME+"邀请您成为家庭成员";
            let des = "联系电话：\n"+phoneMask(this.$userInfo.PHONE)
            console.log("shareToWechat: shareUrl = "+shareUrl)
            console.log("shareToWechat: title = "+title)
            console.log("shareToWechat: des = "+des)
            gShareAction.share(1, {
              shareUrl: shareUrl,
              title: title,
              description: des,
              //thumb: "https://xwjfile.juhaolian.cn/hisense/download/others/icon_share_house.jpg"
	      thumb: "https://xwjfile.juhaolian.cn/hisense/download/others/roomApply.png"
            },function(){
              _this.$toast("分享成功")
            },function(){
              _this.$toast("分享失败")
            })

          } else {
            console.log("shareToWechat: get share url fail = "+error);
          }
        }).catch(error => {
          console.log("shareToWechat: get share url error = "+error);
        });

      },
    }
  }
</script>

<style scoped lang="scss">
  .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
  }
  .rightBtnThemeL {
    font-size: 17px !important;
  }
  .areaTitle {
    display: flex;
    height: 100px;
    background-image: linear-gradient(164deg, #FF9BA5 0%, #FB5364 57%);
    font-size: 18px;
    color: white;
    flex-direction: column;
    justify-content: center;
    padding: 0px 15px;
  }
  .memberList {
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
    background: white;
  }
  .memberItem {
    display: flex;
    align-items: flex-start;
    word-wrap: break-word;
    word-break: break-all;

    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #3A3A3A;
    letter-spacing: 0;
    /*height: 50px;*/
    line-height: 25px;
    padding-top: 12.5px;
    padding-bottom: 12.5px;
    border-bottom: 0.5px solid #ECECEC;
  }
  .ownerLabelBox {
    background:  #FFB004;
    border-radius: 3px;
    padding: 0 6px;
    margin-top: 5px;
    margin-left: 8px;
    white-space: nowrap;
    color: white;
    letter-spacing: 2px;
    font-size: 10px;
    text-align: center;
    height: 16px;
    line-height: 16px;
  }
  .memberLabelBox{
    background:  #B4985C;
  }
  .authBtn {
    background: #fb5768;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: white;
    width: 78%;
    height: 44px;
    line-height: 44px;
    background: #fb5768;
    box-shadow: 0 0 8px 0 rgba(197, 58, 72, 0.39);
    border-radius: 22px;
    margin-left: 11%;
    margin-right: 11%;
    margin-top: 25px;
  }
  .inviteBtn {
    background: #f9f9f9;
    border: 1px solid #fb5768;
    color: #fb5768;
    letter-spacing: -0.05px;
    margin-bottom: 25px;
  }
  .btnThemeL {
    font-size: 18px !important;
  }

  /deep/ .van-checkbox {
    border-bottom: 0.5px solid #ECECEC;
  }
  /deep/ .van-checkbox__label {
    font-size: 16px !important;
    color: #3A3A3A !important;
    /*height: 50px;*/
    line-height: 25px;
    display: flex;
    align-items: center;
    padding-top: 12.5px;
    padding-bottom: 12.5px;
  }

  /*微信邀请弹窗*/
  .invitePopWin {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0,0,0,0.6);
    box-sizing: border-box;
    z-index: 100;

    .invitePopBox{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background: white;
      width: 300px;
      height: 220px;
      border-radius: 6px;
      .title {
        font-weight: bold;
        font-size: 16px;
        text-align: center;
        margin: 25px 10px 20px;
      }
      .wechatImg {
        margin-top: 23px;
        text-align: center;
        img {
          width: 45px;
        }
      }
      .wechatTxt {
        font-size: 12px;
        color: #a3a3a3;
        text-align: center;
      }
      .closeBtn{
        width: 100%;
        top: 0;
        right: 0;
        position: absolute;
        img {
          width: 15px;
          height: 15px;
          float: right;
          margin-top: 10px;
          margin-right: 10px;
        }
      }
      /*手机号输入框*/
      /deep/ .van-cell {
        width: 86% !important;
        margin-left: 7% !important;
        margin-right: 7% !important;
        background: #f9f9f9 !important;
        align-items: center !important;
        border-radius: 4px !important;
      }
    }
  }
</style>
