<template>
  <!-- <div class="view"> -->
  <van-action-sheet
    v-model="showServers"
    :actions="getActions()"
    cancel-text="取消"
    @select="changeServer"
    @cancel="showServers = false"
  />
  <!-- </div> -->
</template>

<script>
import config from "../../common/config";
import { store, storage } from "../../utils/storage";
export default {
  name: "ResetServer",
  data() {
    return {
      showServers: false
    };
  },
  methods: {
    changeServer(actionItem) {
      this.$setUserInfo(null);
      this.clearTags();
      this.deleteAlias();
      this.showServers = false;

      let custEnv = config.urlMap[actionItem.key];
      console.log(custEnv);
      storage.setStorage("custUrl", custEnv);
      store.set("custUrl", custEnv);
      this.$router.replace("/login");

      this.updateOriginSet(custEnv.homeUrl);
    },
    //更新原生中的服务器配置
    updateOriginSet(homeUrl) {
      if (this.$devicePlatform == "android") {
        let ota = navigator.ota;
        let params = {
          appUrl:homeUrl
        };
        ota.changeNetworkStatus(params, function(res){
          console.log("updateOriginSet: Call ota success. ");
        },function(err){
          console.log("updateOriginSet: Call ota err. ");
        });
      } else if (this.$devicePlatform == "ios") {
        window.broadcaster.fireNativeEvent("changeNetworkStatus", {
          appUrl: homeUrl
        }, function () {
          console.log("updateOriginSet: event fired!");
        })
      } else {
        console.log("updateOriginSet: do nothing.")
      }
    },
    //移除极光推送 别名
    deleteAlias() {
      this.$plugin.usePlugin("deleteAlias")(
        Number(this.random()), //sequence
        () => {
          //success callback
          console.log("极光推送: 删除别名成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：删除别名失败.");
        }
      );
    },
    //移除极光推送 标签
    clearTags() {
      this.$plugin.usePlugin("cleanTags")(
        Number(this.random()), //sequence
        () => {
          //success callback
          storage.setStorage("jpushTags", null, 60);
          console.log("极光推送：移除标签成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：移除标签失败.");
        }
      );
    },
    //生成随机数
    random: function() {
      var myDate = new Date(); //获取系统当前时间
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber =
        year +
        month +
        date +
        hours +
        minutes +
        seconds +
        Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    },
    //获取最新列表内容
    getActions: function() {
      var arr = [];
      let custEnv = storage.getStorage("custUrl");
      for (let server in config.urlMap) {
        console.log(server);
        let nm = config.urlMap[server].envName;
        let host = config.urlMap[server].host;

        if (nm == custEnv.envName) {
          arr.push({ name: nm+" ("+host+")", key: server, color: "#fb5768" });
        } else {
          arr.push({ name: nm+" ("+host+")", key: server });
        }
      }
      return arr;
    }
  },
  computed: {
    actions() {
      var arr = [];
      for (let server in config.urlMap) {
        console.log(server);
        let nm = config.urlMap[server].envName;
        arr.push({ name: nm, key: server });
      }
      return arr;
    }
  }
};
</script>

