<template>
  <div class="messagechat-page">
    <navigation-bar class="nav-view" @onLeftClick="onBack" pageName="" leftImgType="1"></navigation-bar>
    <div class="robot-head">
      <!--  语音机器人区域    -->
      <div class="robot-img">
        <img v-if="roborStatus ===3" src="../../assets/images/home/voiceAssistant/robot_error.png" />
        <img v-if="roborStatus ===1" src="../../assets/images/home/voiceAssistant/robot_normal.png" />
        <div class="robot-listening" v-if="roborStatus ===2"></div>
      </div>
      <div class="speechStatus" v-if="roborStatus===2||roborStatus===3">{{speechStatusMsg}}</div>
    </div>
    <ul class="chat-content"  ref="msgchatScorll">
      <li
        :style="{'margin-top':index==0?'22px':'15px'}"
        v-for="(message, index) in messageList"
        :key="index"
      >
        <div v-if="message.isSystem">
          <!-- 消息时间 -->
          <!-- <div
            class="chat-time"
            v-show="showMessageTimer(messageList, index)"
          >{{messageTimerFormat(message.time)}}</div>-->
          <div class="chat-time" v-show="showMessageTimer(messageList, index)">{{messageTimerFormat(message.timeStamp)}}</div>
          <div class="chat-system">
            <div class="text-msg" v-if="message.intention_code === 666 || message.intention_code === 400000 || message.intention_code === 110001">
              <!-- 普通消息 -->
              <span>{{message.response_content}}</span>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 110000 || message.intention_code === 777">
              <!-- 提示消息 -->
              <div class="line-text" v-if="message.intention_code === 110000">对不起，我还正在努力学习中...</div>
              <div class="line-text">您可以这样对我说：</div>
              <div class="line-text">我要报修+【报事报修内容】</div>
              <div class="line-text">我要投诉+【投诉建议内容】</div>
              <div class="line-text" v-for="(text, index) in textArray" :key="index">
                <span>我要查询</span>
                <span style="color:#4A90E2" @click="tapLab(text, true)">{{text}}</span>
                <span v-if="index<2">进展</span>
              </div>
              <div class="line-text">我要买【商品名】</div>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 2">
              <!-- 报修说明 -->
              <div class="line-text">
                <strong>如何语音发起报事报修？</strong>
              </div>
              <ul>
                <li class="line-text li-line">首先说出“我要报修”</li>
                <li class="line-text li-line">然后说出您想要报修的内容。</li>
              </ul>
              <div class="line-text" style="margin-top:7px">
                <strong>如何语音查询报事报修？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">通过说出“查询报事报修进展”“查询报修进展”进行查询。</li>
              </ul>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 3">
              <!-- 投诉建议 -->
              <div class="line-text">
                <strong>如何语音发起投诉建议？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">首先说出“我要投诉”“我要建议”</li>
                <li class="line-text li-line">然后说出您想要投诉或者建议的内容。</li>
              </ul>
              <div class="line-text" style="margin-top:7px">
                <strong>如何语音查询投诉建议？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">通过说出“查询投诉建议进展”“查询投诉进展”进行查询。</li>
              </ul>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 4">
              <!-- 物业活动 -->
              <div class="line-text">
                <strong>如何语音查询物业活动？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">通过说出“帮我查询物业活动”进行查询。</li>
              </ul>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 5">
              <!-- 物业电话 -->
              <div class="line-text">
                <strong>如何语音查询物业电话？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">通过说出“帮我查询物业电话”进行查询。</li>
              </ul>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 6">
              <!-- 物业缴费 -->
              <div class="line-text">
                <strong>如何语音查询物业缴费？</strong>
              </div>
              <div class="line-text">您可以这样操作：</div>
              <ul>
                <li class="line-text li-line">通过说出“帮我查询物业缴费”“帮我查下缴费”“我要缴物业费”进行查询。</li>
              </ul>
            </div>
            <div class="text-msg" style="width:80%" v-else-if="judgeCommitCommand(message.intention_code)">
              <!-- 工单创建展示页 -->
              <strong style="color: #333333;">{{getWorkOrderTitle(message.intention_code)}}</strong>
              <div style="color: #4A4A4A; margin-top: 10px">{{'房间：' + $userInfo.AREA_NAME + $userInfo.BUILD_NAME + $userInfo.ROOM_NAME}}</div>
              <div class="workOrderText" style="margin-top: 10px">
                <van-field
                        :ref="'workTextArea' + index"
                        style="background: #F7F7F7; border-radius: 2px; width:auto;padding: 5px 5px;"
                        v-model="message.response_content"
                        rows="1"
                        autosize
                        type="textarea"
                        placeholder="请输入详细问题，我们一定努力解决"
                        @focus="workTextFocus($event, index)"
                />
              </div>
              <div style="margin:15px 0px;   display: flex; justify-content: center;">
                <van-button class="cancel-button" type="default" @click="cancelWorkOrder(index)">取消</van-button>
                <van-button class="commit-button" type="default" @click="commitWorkOrder(index)">提交</van-button>
              </div>
            </div>
            <div class="text-msg" v-else-if="message.intention_code === 888">
              <!-- 完成提示页 -->
              <span v-if="message.textArray.length > 0">{{message.textArray[0]}}</span>
              <span class="jump-text" v-if="message.textArray.length > 1" @click="jumpWorkOrderDetail(message)">{{message.textArray[1]}}</span>
              <span v-if="message.textArray.length > 2">{{message.textArray[2]}}</span>
            </div>
            <div class="text-msg" v-else-if="judgeSearchCommand(message.intention_code)">
              <!-- 查询跳转提示 -->
              <span>{{'正在为您跳转' + message.intention_content + '...'}}</span>
            </div>
            <div class="text-msg" v-else>
              <!--未识别标识 -->
              <span>{{'未识别 ' + message.response_content}}</span>
            </div>
          </div>
        </div>

        <!-- 当前用户发送的 -->
        <div v-else>
          <!-- <div
            class="chat-time"
            v-show="showMessageTimer(messageList, index)"
          >{{messageTimerFormat(message.time)}}</div>-->
          <div class="chat-customer">
            <div class="text-msg" v-if="message.intention_code === 666">
              <span>{{message.response_content}}</span>
            </div>
          </div>
        </div>
      </li>
      <!-- 解决底部高度不够，键盘弹出工单无法拉到键盘上方问题 -->
      <div v-if="showKeyBoardH != 0" :style="{height: showKeyBoardH + 'px'}"> </div>
    </ul>
    <div class="chat-bottom">
      <!-- 语音输入按钮区域 -->
      <div class="count-down">{{countDownVal > 0 ? countDownVal + '”' : ''}}</div>
      <div class="input-voice">
        <img
                src="../../assets/images/home/voiceAssistant/voice_button.png"
                @touchstart="goToSpeech"
                @touchend="stopSpeech"
        />
        <div class="container" v-if="isPressSpeechBtn">
          <div class="wave" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import MessageJs from "./messageFunction";
  import NavigationBar from "@/common/currency/NavigationBar.vue";
  import Vue from "vue";
  import { Toast, Button, Dialog } from "vant";
  Vue.use(Toast).use(Button).use(Dialog);

  import { getWorkLabelList } from "../../services/servers/compaintReporting";
  import { getVoiceDiscernService, getVoiceErrorCorrectService} from "../../services/voiceAssistant";
  import { queryAreaInfoService } from "../../services/propertyBillService";
  import { workOrderAdd } from "../../services/servers/compaintReporting";

  export default {
    name: "messageChat",
    created() {
      this.checkAudioPermissions();
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        if (from.path == "/homePage"
        || from.path == "/homePageThemeL"
        ||  from.path == '/servicePage'
        || from.path == '/servicePageThemeL') {
          vm.nextTickPageSet();
        }
      })
    },
    activated() {
      // this.initMethod();
      console.log('执行了 --- activated: ' + this.scrollOffsetY);
      this.roborStatus=1;
      this.speechStatusMsg='';
      this.isPressSpeechBtn=false;
      this.voiceFlag=false;
      this.sendVal=false;
      this.scrollToBottom(false);
      window.addEventListener("native.keyboardshow", this.keyboardshow);
      window.addEventListener("native.keyboardhide", this.keyboardhide);
      document.addEventListener("visibilitychange", this.visibilitychange);
      if (this.$devicePlatform != "pc") {
        window.broadcaster.addEventListener("windowFocusOut", this.stopSpeech);
      }
    },
    deactivated() {
      console.log('执行了 --- deactivated: ' + this.scrollOffsetY);
      this.keyboardhide();
      window.removeEventListener("native.keyboardshow", this.keyboardshow);
      window.removeEventListener("native.keyboardhide", this.keyboardhide);
      document.removeEventListener('visibilitychange', this.visibilitychange)
      if (this.$devicePlatform != "pc") {
        window.broadcaster.addEventListener("windowFocusOut", this.stopSpeech);
      }
      this.stopTimer();
      this.roborStatus=1;
      this.speechStatusMsg='';
      this.isPressSpeechBtn=false;
      this.voiceFlag=false;
      this.sendVal=false;

      //过滤去掉跳转提示
      let messageLength = this.messageList.length;
      if (messageLength > 0) {
        const ele = this.messageList[messageLength - 1];
        if (ele.isSystem && this.judgeSearchCommand(ele.intention_code)) {
          this.messageList.splice(messageLength - 1, 1);
        }
      }
    },
    beforeRouteLeave(to, from, next) {
      // this.scrollOffsetY = this.$refs.msgchatScorll.scrollTop;
      // console.log('执行了 --- beforeRouteLeave: ' + this.scrollOffsetY);
      next();
    },
    beforeCreate() {},
    components: {
      NavigationBar
    },
    data() {
      return {
        // refreshing: false,
        //消息列表
        messageList: [],
        textArray: [
          "【报事报修】",
          "【投诉建议】",
          "【物业活动】",
          "【物业电话】",
          "【物业缴费】",
          "【优惠券】",
          "【订单】"
          
        ],
        countDownVal: 0,
        countTimer: '',
        //当前scrollY轴滚动偏移值
        keyboardTime: 0,
        focusDomToBottomVal: 0,
        preKeyBScroolTop: 0,
        showKeyBoardH: 0,
        scrollOffsetY: 0,
        roborStatus: 1, //头部机器人状态控制变量 1正常 2输入中 3识别错误
        speechStatusMsg: "识别失败，请重新讲话...", //语音识别结果状态提示
        isHasAudioPermissions: false, //判断是否有语音权限（Android用）
        isPressSpeechBtn: false,
        sendVal: false, //语音搜索状态控制
        voiceFlag: false, //语音识别标识
        labelList: [] //工单标签列表
      };
    },
    methods: {
      nextTickPageSet() {
        //机器人位置调整、引导语添加
        this.$nextTick(() => {
          let robotHead = document.getElementsByClassName('robot-head')[0];
          // console.log('=====>>>>> 下边输出对是状态栏对高度');
          // console.log(this.$stbHeight);
          if (this.$devicePlatform == 'android' || this.$devicePlatform == 'pc') {
            if (robotHead) {
              robotHead.style.paddingTop = this.$stbHeight + 'px';
            }
          }
          //计算内容对高度
          // if (robotHead) {
          //   let offsetTop = window.getComputedStyle(robotHead, false).height;
          //   offsetTop = parseInt(offsetTop) - 50;
          //   let chatContent = document.getElementsByClassName('chat-content')[0];
          //   chatContent.style.top = offsetTop + 'px';
          // }
          //展示系统引导语
          setTimeout(() => {
            this.addSystemMessageMethod();
          }, 800);
        });
      },
      //获取工单标签
      getWorkOrderLab(typeFlag, item) {
        let labelList = {};
        labelList.type = typeFlag;
        labelList.deptCode = this.$userInfo.PARENT_DEPT_CODE;
        let that = this;
        getWorkLabelList(labelList)
                .then(res => {
                  if (res.resultCode == "0") {
                    if (res.data.length > 0) {
                      let labelCode = res.data[0].labelCode;
                      that.submitWorkOrder(typeFlag, item, labelCode);
                    }
                  }
                })
                .catch(error => {
                  Toast(error);
                  console.log(error + "未获取到工单标签");
                });
      },
      //提交报修工单
      submitWorkOrder(typeFlag, item, labelCode) {
        if (item.response_content.length <= 0) {
          Toast("请填写工单详细问题");
          return;
        }
        let data = {
          terminalType: "3",
          typeFlag: typeFlag,
          labelCode: labelCode,
          reportType: "0",
          content: item.response_content,
          picUrl: [],
          areaCode: this.$userInfo.AREA_CODE,
          buildCode: this.$userInfo.BUILD_CODE,
          roomCode: this.$userInfo.ROOM_CODE,
          areaName: this.$userInfo.AREA_NAME,
          buildName: this.$userInfo.BUILD_NAME,
          unitNo: this.$userInfo.UNIT_NO,
          roomName: this.$userInfo.ROOM_NAME,
          ownerCode: this.$userInfo.OWNER_CODE,
          ownerName: this.$userInfo.OWNER_NICK_NAME,
          ownerPhone: this.$userInfo.PHONE,
          createdBy: this.$userInfo.OWNER_CODE
        };
        let _that = this;
        workOrderAdd(data).then(
                res => {
                  if (res.resultCode == "0") {
                    item.commitFlag = true;
                    let rmIndex = _that.messageList.indexOf(item);
                    _that.messageList.splice(rmIndex, 1);
                    _that.commitWorkOrderSuccess(res.data, typeFlag);
                  } else {
                    Toast(res.msg);
                  }
                },
                error => {Toast(error);}
        );
      },
      commitWorkOrderSuccess(orderId, flag) {

        let typeText  = flag === 0 ? '已为您创建报事报修工单' : '已为您创建投诉建议工单';
        let text = typeText + orderId + '，' + this.$userInfo.AREA_NAME + this.$userInfo.BUILD_NAME + this.$userInfo.ROOM_NAME + '，点击或说出查看详情查询进展情况。';
        let item = {
          intention_code: 888,
          response_content: text,
          intention_content: "",
          isSystem: true,
          timeStamp: MessageJs.getCurrentTimeStamp(),
          orderId: orderId,
          typeFlag: flag
        };
        item.textArray = this.getMessageKeyword(item.response_content);
        this.messageList.push(item);
        this.scrollToBottom(true);
      },
      jumpWorkOrderDetail(message) {
        this.$router.push({
          name: 'complaintReportingDetail',
          query: {
            complateCode:message.orderId,
            typeFlag:message.typeFlag,
          }
        })
      },
      // 添加系统问候语
      addSystemMessageMethod() {
        let timestamp = MessageJs.getCurrentTimeStamp();
        let greetMsg =  {
          intention_code: 666,
          response_content: "Hi～我是您的语音助理小信。",
          isSystem: true,
          timeStamp: timestamp
        };
        let guideMes = {
          intention_code: 777,
          response_content: "",
          intention_content: "",
          isSystem: true,
          timeStamp: timestamp
        };

        if (!this.messageList || this.messageList.length == 0) {
          this.messageList = [greetMsg];
        }
        else {
          let preMsg = this.messageList[this.messageList.length - 1];
          if (preMsg.intention_code === guideMes.intention_code) {
            return;
          }
          this.messageList.push(greetMsg);
        }
        this.scrollToBottom(true);
        setTimeout(() => {
          this.messageList.push(guideMes);
          this.scrollToBottom(true);
        }, 800);
      },
      //根据关键字拆分
      getMessageKeyword(text) {
        let msg = text;
        let array = [];
        const keyWord = "查看详情";
        if (msg.indexOf(keyWord) != -1) {
          array = msg.split(keyWord);
          array.splice(1, 0, keyWord);
        } else {
          array.push(msg);
        }

        return array;
      },
      //点击特定标签
      tapLab(latText, isSearch) {
        this.sendTextMessage(isSearch ? '我要查询'+latText : latText);
      },
      //判断是否是跳转页面
      judgeSearchCommand(comCode) {
        if (comCode === 100000
          || comCode === 100001
          || comCode === 100002
          || comCode === 100003
          || comCode === 100004
          || comCode === 100005
          || comCode === 100006
          || comCode === 310000
          || comCode === 320000 
        // || comCode === 200000
        ) {
          return true;
        }
        else {
          return false;
        }
      },
      //判断是否是提交指令
      judgeCommitCommand(comCode) {
        if (comCode === 310001
                || comCode === 320001
        ) {
          return true;
        }
        else {
          return false;
        }
      },
      getWorkOrderTitle(comCode){
        if (comCode === 310001) {
          return '报事报修';
        }
        else if (comCode === 320001){
          return '投诉建议';
        }
      },
      //跳转页面
      goToPage(data){
        switch (data.intention_code) {
          case 100000:
            //商品搜索
            this.$router.push({
              name: 'goodsSearch',
              params: {
                speechTxt : data.response_content,
                isRefresh : 1,
              }
            })
            break;
          case 100001:
            //订单查询
            this.$router.push({ name: 'myOrderList', params: { orderType: 0 } });
            break;
          case 100002:
            //购物车查询
            this.$router.push("/shoppingCart");
            break;
          case 100003:
            //电话查询
            this.$router.push({name: "commonPhone"});
            break;
          case 100004:
            //缴费查询
            this.queryAreaInfo();
            break;
          case 100005:
            //物业活动
            this.$router.push({
              name: "propertyActivityList"
            });
            break;
          case 100006:
            //优惠券查询
            this.$router.push({
              name: "couponList"
            });
            break;
          case 310000:
            //报修查询
            this.$store.dispatch('changeTypeFlag', 0);
            this.$router.push({
              name: "complaintReporting",
              params: {typeFlag: 0}
            });
            break;
          case 320000:
            //投诉工单查询
            this.$store.dispatch('changeTypeFlag', 1);
            this.$router.push({
              name: "complaintReporting",
              params: {typeFlag: 1}
            });
            break;

          default:
            break;
        }
      },
      //网络判断
      netWorkJudge() {
        // 判断网络异常
        if (!window.navigator.onLine) {
          this.roborStatus = 3;
          this.speechStatusMsg = "识别失败，请检查您的网络~";
          return false;
        }
        return true;
      },
      stopSpeech() {
        this.stopTimer();
        var _this = this;
        setTimeout(() => {
          _this.isPressSpeechBtn = false;
        }, 500);
        // 判断网络异常
        this.netWorkJudge();
        
        var ifly = cordova.plugins.IflyForXwjPlugin;
        if (_this.sendVal) {
          _this.sendVal = false;
          ifly.stopListen(
                  function(spRes) {
                    console.log("success " + spRes);
                    if (spRes) {
                      _this.roborStatus = 1;
                      _this.sendTextMessage(spRes);
                    } else {
                      _this.roborStatus = 3;
                      _this.speechStatusMsg = "识别失败，请重新讲话...";
                    }
                  },
                  function(errMes) {
                    _this.roborStatus = 3;
                    _this.speechStatusMsg = "识别失败，请重新讲话...";
                    console.log("识别失败"+errMes);
                  },
                  null
          );
        } else {
          if (_this.isHasAudioPermissions) {
            _this.speechStatusMsg = "说话时间太短，请长按说话";
            _this.roborStatus = 3;
          }
          _this.sendVal = false;
          _this.roborStatus = 1;
        }
      },
      //语音搜索
      goToSpeech(e) {
        // 判断网络异常
        if (!this.netWorkJudge()){
          return;
        }
        //正在录音
        if (this.isPressSpeechBtn) {
          return;
        }
        this.isPressSpeechBtn = true;
        this.startTimer();
        var _this = this;
        _this.roborStatus = 2;
        // _this.speechStatusMsg = "正在聆听，请讲话  ...";
        var ifly = cordova.plugins.IflyForXwjPlugin;
        if (_this.isHasAudioPermissions) {
          _this.sendVal = true;
          _this.speechStatusMsg = "正在聆听，请讲话  ...";
          ifly.startListen(null, null, false, true);
        } else {
          Dialog.confirm({
            message: "请您打开麦克风权限，以正常使用语音识别功能"
          })
          .then(() => {
            _this.checkAudioPermissions();
          })
          .catch(() => {
            //oncancel
          });
        }
      },
      checkAudioPermissions() {
        let _this = this;
        if (this.$devicePlatform == 'ios') {
          // this.isHasAudioPermissions = true;
          // return true;
          var permissionPlugin = cordova.plugins.iOSPermission;
            permissionPlugin.checkMicrophonePermission(
              res => {
                this.isHasAudioPermissions = res== '0' ? false : true;
              },
              err => {
                this.isHasAudioPermissions = false;
              }
          );
        } 
        else if (this.$devicePlatform == 'android') {
          var list = ["android.permission.RECORD_AUDIO"];
          var permissions = cordova.plugins.permissions;
          permissions.requestPermissions(
                  list,
                  function(status) {
                    if (status.hasPermission) {
                      _this.isHasAudioPermissions = true;
                    }
                  },
                  function(status) {
                    alert("请打开麦克风权限");
                    _this.isHasAudioPermissions = false;
                  }
          );
        }
        else {
          this.isHasAudioPermissions = false;
        }
      },
      //取消工单
      cancelWorkOrder(index) {
        let item = this.messageList[index];
        if (item.commitFlag) {
          this.$toast('工单已创建，无法进行取消操作');
          return;
        }

        Dialog.confirm({
          title:"确认取消此工单？",
          confirmButtonText: "确认取消",
          cancelButtonText:"我再想想"
        }).then(() => {
          // on confirm
          this.messageList.splice(index, 1);
        }).catch(() => {
          Dialog.close;
        })
      },
      //提交工单
      commitWorkOrder(index) {
        let item = this.messageList[index];
        if (item.commitFlag) {
          this.$toast('工单已提交，请勿重复提交');
          return;
        }
        let type = item.intention_code === 310001 ? 0 : 1;
        this.getWorkOrderLab(type, item);
      },
      getUserInutCode(inputText) {
        // inputText = '我想要苹果'
        let reqData = {
          ownerCode: this.$userInfo.OWNER_CODE,
          inputTxt: inputText,
          areaCode: this.$userInfo.AREA_CODE
        };
        getVoiceDiscernService(reqData)
        .then(data => {
          data.isSystem = true;
          data.timeStamp = MessageJs.getCurrentTimeStamp();
          this.messageList.push(data);
          this.scrollToBottom(true);
          if (this.judgeSearchCommand(data.intention_code)) {
            setTimeout(() => {
              this.goToPage(data);
            }, 800);
          }
        })
        .catch(error => {
          console.log("getVoiceDiscernService: " + error);
        });
      },
      //缴费查询跳转
      queryAreaInfo() {
        var reqData = {
          owner_code: this.$userInfo.OWNER_CODE
        };
        queryAreaInfoService({ data: JSON.stringify(reqData) })
                .then(result => {
                  //请求成功
                  // console.log(JSON.stringify(result));
                  if (result.result) {
                    if (result.data.areaList.length <= 1) {
                      this.$router.push({name: "propertyBillList"});
                    } else {
                      this.$router.push({name: "paymentRoomList"});
                    }
                  }
                })
                .catch(error => {
                  //加载失败后，关闭加载等待的文字
                });
      },
      //获取历史消息列表
      getHistroyMessageList() {},
      // 启动监听新消息
      startObserveNewMessage() {},
      // 计时开始
      startTimer(){
        let self = this;
        this.countDownVal = 59;
        this.countTimer = setInterval(function () {
          self.countDownVal--;
          if (self.countDownVal === 0) {
            self.voiceInputEnd();
          }
        }, 1000);
      },
      // 停止计时
      stopTimer(){
        let self = this;
        if (this.countTimer !== '') {
          clearInterval(self.countTimer);
          this.countTimer = '';
        }
        this.countDownVal = 0;
      },
      //语音输入结束
      voiceInputEnd() {
        this.stopSpeech();
      },
      //返回按钮
      onBack() {
        history.back();
      },
      //用户发送文本消息
      sendTextMessage(txt) {
        //防止短时间重复输入文本的语音sdk bug
        if (this.voiceFlag) {
          return;
        }
        this.voiceFlag = true;
        setTimeout(() => {
          this.voiceFlag = false;
        }, 666);

        let reqData = {
          ownerCode: this.$userInfo.OWNER_CODE,
          inputTxt: txt,
          areaCode: this.$userInfo.AREA_CODE
        };
        getVoiceErrorCorrectService(reqData)
        .then(data => {
          this.messageList.push({
            intention_code: 666,
            response_content: data.result,
            intention_content: "",
            isSystem: false,
            timeStamp: MessageJs.getCurrentTimeStamp()
          });
          this.getUserInutCode(txt);
          this.scrollToBottom(true);
        })
        .catch(error => {
          console.log("getVoiceErrorCorrectService: " + error);
        });
      },
      //滚动到底
      scrollToBottom(animation = true) {
        if (animation == true) {
          setTimeout(() => {
            //滚动条长度
            var currentDistance =
                    this.$refs.msgchatScorll.scrollHeight -
                    this.$refs.msgchatScorll.clientHeight;
            //当前滚动条距离顶部的距离
            var currentScroll_y = this.$refs.msgchatScorll.scrollTop;
            if (currentDistance > 0 && currentDistance > currentScroll_y) {
              currentScroll_y =
                      Math.ceil((currentDistance - currentScroll_y) / 6) +
                      currentScroll_y;
              currentScroll_y =
                      currentScroll_y > currentDistance
                              ? currentDistance
                              : currentScroll_y;
              //微信和qq浏览器不支持 scrollTo？
              //this.$refs.msgchatScorll.scrollTo(0,currentScroll_y);
              this.$refs.msgchatScorll.scrollTop = currentScroll_y;
              this.scrollToBottom(true);
            }
          }, 20);
        } else {
          let scrollH = this.$refs.msgchatScorll.scrollHeight;
          this.$refs.msgchatScorll.scrollTop = scrollH;
          // console.log('yz------->  ' + scrollH);
        }
      },
      scrollToPointAnimation(offSetY) {
        let offV = offSetY/6.0;
        for (let i = 0; i < 6; i++) {
          let currentScroll_y = this.$refs.msgchatScorll.scrollTop;
          this.$refs.msgchatScorll.scrollTop = currentScroll_y + offV;
        }
      },
      workTextFocus(event, index) {

        let obj = event.target.offsetParent.offsetParent.offsetParent;
        setTimeout(() => {
          let screenH = window.innerHeight;
          let objRect = obj.getBoundingClientRect();
          // let objH = obj.offsetHeight;
          this.focusDomToBottomVal = screenH - (objRect.top + objRect.height);
        }, 200);
      },
      getNowTime() {
        var now = Date.now();
        if (now - this.keyboardTime <= 150) {
          this.keyboardTime = now;
          // ignore
          return false;
        } else {
          this.keyboardTime = now;
          return true;
        }
      },
      /**
       * 键盘弹出事件
       */
      keyboardshow(e) {
        if (!this.getNowTime()) {
          console.log("keyborad handle less than 150, ignore!!!");
          return
        }
        // console.log('键盘弹出的高度为： ' + e.keyboardHeight);
        this.showKeyBoardH = e.keyboardHeight;
        this.$nextTick(() => {
          setTimeout(() => {
            if (this.focusDomToBottomVal < e.keyboardHeight) {
              let diffV = e.keyboardHeight - this.focusDomToBottomVal;
              let currentScroll_y = this.$refs.msgchatScorll.scrollTop;
              this.preKeyBScroolTop = currentScroll_y;
              this.$refs.msgchatScorll.scrollTop = currentScroll_y + diffV + 30;
              // this.scrollToPointAnimation(diffV);
            }
          }, 300);
        });
      },

      /**
       * 键盘隐藏事件
       */
      keyboardhide() {
        if (!this.getNowTime()) {
          console.log("keyborad handle less than 150, ignore+!!!");
          return
        }
        // this.replyInputShow = false;
        // this.$refs.myReply.style.bottom = "1.33333rem";
        if (this.preKeyBScroolTop != 0) {
          this.showKeyBoardH = 0;
          setTimeout(() => {
            this.$refs.msgchatScorll.scrollTop = this.preKeyBScroolTop;
            // this.scrollToPointAnimation(this.preKeyBScroolTop-this.$refs.msgchatScorll.scrollTop);
            this.preKeyBScroolTop = 0;
          }, 300);
        }
      },
      visibilitychange() {
        if (document.hidden) {
          // 页面被挂起
          this.keyboardhide();
        }
      }
    },
    computed: {
      // 消息时间格式化
      messageTimerFormat() {
        return function(timeStamp) {
          let time = MessageJs.timeFormatting2(timeStamp * 1000)
          return time;
        };
      },
      // 是否显示当前消息时间,5分钟内消息不显示时间
      showMessageTimer() {
        return function(messageList, index) {
          if (index == 0) {
            return true;
          }
          let curTimeStamp = messageList[index].time;
          let preTimeStamp = messageList[index - 1].time;
          let timeDiff = curTimeStamp - preTimeStamp;
          return timeDiff > 300 ? true : false;
        };
      }
    }
  };
</script>

