<template>
  <div style="height:100%;background:#F9F9F9">
    <!--<van-nav-bar id="NaviBar" title="修改密码" fixed left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="修改密码" >
    </navigation-bar>
    <div :class="{themeL:bindThemeL}">
      <!-- <van-cell-group> -->
      <van-field v-model="oldPass" type="password"  maxlength="16" clearable placeholder="请输入原密码" />
      <van-field v-model="newPass" type="password" maxlength="16" clearable placeholder="请输入新密码（6-16位字符）" />
      <van-field v-model="confirmPass" type="password" maxlength="16" clearable placeholder="请确认新密码" />
      <!-- </van-cell-group> -->
    </div>
    <div style="padding:25px" :class="{themeL:bindThemeL}">
      <van-button
        :color="couldCommit ? '#FB5364' : '#999999'"
        size="large"
        round
        :disabled="!couldCommit"
        @click="changePsw"
      >确 认</van-button>
    </div>
    <span
      style="font-size:12px;color:#999999;float: right;padding-right:25px;"
      :style="{fontSize:defFontSize}"
      @click="findPassword"
    >忘记密码？</span>
  </div>
</template>

<script>
import { changePassWord, requestLoginService, getRsaPublicKey } from "../../services/loginService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Vue from 'vue';
export default {
  name: "ResetPassword",
  components:{
    NavigationBar
  },
  data() {
    return {
      oldPass: "",
      newPass: "",
      confirmPass: "",
      couldCommit: false,
      publicKey: "",
      /*大字模式*/
      bindThemeL: this.$curTheme==0 ? false : true,
      defFontSize: this.$curTheme==0 ? "12px" : "15px"
    };
  },
  mounted(){
    this.getPubKey();
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    findPassword() {
      console.log("push到密码找回页面");
      this.$router.push("./recoverPassWord");
    },
    //修改登录按钮状态
    changngeLoginBtnStatus() {
      this.couldCommit =
        this.oldPass.length >= 6 &&
        this.newPass.length >= 6 &&
        this.confirmPass.length >= 6;
    },
    changePsw() {
      if (this.oldPass == this.newPass && this.newPass == this.confirmPass) {
        this.$toast("新密码与旧密码不能一致");
        return;
      }
      if (this.newPass != this.confirmPass) {
        this.$toast("新密码与确认密码不一致");
        return;
      }
      let baseNewWord =  btoa(this.newPass);
      let baseOldWord =  btoa(this.oldPass);
        //先校验原密码是否正确 ZHSQYYPT-4326
        let rsaInfo = this.$userInfo.PHONE+","+baseNewWord+","+baseOldWord;
        let rsaPassWord = Vue.prototype.$getRSASign(rsaInfo,this.publicKey);
        let reqData = {
          modifyInfo : rsaPassWord
        }
        console.log("modifyInfo qiao : "+reqData.modifyInfo);
      changePassWord(reqData)
              .then(data => {
                this.$toast(data.msg);
                if (data.resultCode == 0) {
                  this.backToMinePage();
                }
              })
              .catch(error => {
                // this.$toast(error);
              });
    },
    backToMinePage() {
      setTimeout(() => {
        this.$router.replace("/userSetting");
      }, 1000);
    },
    getPubKey(){
      getRsaPublicKey().then(res => {
        this.publicKey = res.data.publicKey;
      }).catch(error => {
        this.isLoading =false;
      });
    },
  },
  watch: {
    // 监听原密码输入
    oldPass: function(newValue) {
      this.changngeLoginBtnStatus();
    },
    //监听新密码输入
    newPass: function(newValue) {
      this.changngeLoginBtnStatus();
    },
    //监听确认密码输入
    confirmPass: function(newValue) {
      this.changngeLoginBtnStatus();
    }
  }
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.themeL .van-cell__value {
  height: 38px;
  line-height: 38px;
  font-size: 17px;
}
/deep/.themeL .van-button {
  font-size: 18px;
}
</style>