<template>
  <div class="navBar van-nav-bar" :style="navBarStyle">
    <div class="left" v-if="isShowLeft" @click="$emit('onLeftClick')" :style="bothWidth">
      <!-- 默认状态 -->
      <img v-if="isShowBack" :src="backImg" alt="" srcset="">
      <!-- 定制左侧具名插槽 -->
      <slot name="nav-left"></slot>
    </div>
    <div class="center">
      <!-- 默认状态 -->
      <span class="title" v-if="isDefault">{{pageName}}</span>
      <!-- 定制中间具名插槽 -->
      <slot name="nav-center"></slot>
    </div>
    <div class="right" v-if="isShowRight" :style="bothWidth">
      <!-- 定制右侧具名插槽 -->
      <slot name="nav-right"></slot>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      // 是否展示默认状态
      isDefault: {
        default: true,
        type: Boolean
      },
      // 两边的宽度
      bothWidth: {
        type: Object,
        default: function () {
          return {
            width: '36PX'
          }
        }
      },
      // 右侧是否展示
      isShowRight: {
        default: true,
        type: Boolean
      },
      // 左侧是否展示
      isShowLeft: {
        default: true,
        type: Boolean
      },
      // 是否展示默认后退按钮
      isShowBack: {
        default: true,
        type: Boolean
      },
      // 展示后退图片样式
      leftImgType: {
        type: String,
        default: '0',
      },
      // 默认状态下页面标题的名称
      pageName: {
        default: '',
        type: String
      },
      // navBar样式
      navBarStyle: {
        type: Object,
        default: function () {
          return {
            backgroundColor: '#fff'
          }
        }
      }
    },
    data() {
      return {
        backImg: require('@/assets/images/common/nav_return_black.png')
      }
    },

    mounted() {
      this.isLeftImg();
    },
    methods: {
      // 判断使用不同的左侧图片
      isLeftImg() {
        switch (this.leftImgType) {
          case '0':
            this.backImg = require('@/assets/images/common/nav_return_black.png');
            break;
          case '1':
            this.backImg = require('@/assets/images/common/nav_return_white.png');
            break;
        }
      }
    }
  }
</script>

<style scoped>
  .navBar {
    width: 100%;
    height: 44PX !important;
    line-height: 44PX !important;
    display: flex;
    background: #FFFFFF;
    box-shadow: 0 0 1PX 0 rgba(100, 100, 100, 0.50);
    justify-content: space-between;
    color: #333333;
    padding-top: 20PX;
    padding-top: constant(safe-area-inset-top);
    padding-top: env(safe-area-inset-top);
    z-index: 100;
  }

  .navBar .left {
    display: flex;
    height: 32PX !important;
    overflow: hidden;
    margin: 6PX 0 6PX 5PX !important;
  }

  .navBar .right {
    display: flex;
    flex-direction: row-reverse;
    height: 32PX;
    min-width: 32PX !important;
    margin: 6PX 5PX 6PX 0 !important;
  }

  .navBar .left img {
    height: 32PX !important;
    width: 32PX !important;
    display: block;
    align-self: center;
  }

  .navBar .center {
    display: flex;
    flex-grow: 1;
  }

  .navBar .center .title {
    height: 100%;
    line-height: 44PX !important;
    display: block;
    align-self: center;
    margin: 0 auto;
    font-size: 20PX !important;
    font-family: PingFangSC-Medium;
    letter-spacing: -0.06PX !important;
    max-width: 250px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  /deep/ .van-icon {
  
    align-items: center;
    display: flex;
}

</style>