const state = {
    reload: true,
    reloadMine: true,
    mineActive: 0,
    areaName: "",
    city: {
        cityName: "青岛市",
        cityCode: ""
    }
}

// 定义 getters demo
var getters = {

}

const actions = {
    setCity(context, city) {
        context.commit('setCity', city);
    },
    setAreaName(context, areaName) {
        context.commit('setAreaName', areaName);
    },
    setReload(context, reload) {
        context.commit('setReload', reload);
    },
    setReloadMine(context, reloadMine) {
        context.commit('setReloadMine', reloadMine);
    },
    setMineActive(context, mineActive) {
        context.commit('setMineActive', mineActive);
    },
}

const mutations = {
    setCity(state, city) {
        state.city = city;
    },
    setAreaName(state, areaName) {
        state.areaName = areaName;
    },
    setReload(state, reload) {
        state.reload = reload;
    },
    setReloadMine(state, reloadMine) {
        state.reloadMine = reloadMine;
    },
    setMineActive(state, mineActive) {
        state.mineActive = mineActive;
    }
}
// 最后统一导出
export default {
    state,
    getters,
    actions,
    mutations
}
