import http from "../http";

//获取发现全部信息服务
const getAllNoteService = (reqData, isShowLoading=false) => {
    return http.get('xwj-discovery-forum/note/getAppNoteList', reqData, "baseUrl", isShowLoading);
}

//发现点赞服务
const saveLikeService = (reqData) => {
    return http.post('xwj-discovery-forum/thumpUp/thumpUp', reqData, false, "baseUrl", false);
}

//删除指定数据
const delNoteDataService = (noteCode, ownerCode) => {
    return http.post('xwj-discovery-forum/note/deleteNote', { noteCode: noteCode, userCode: ownerCode }, true);
}

//新加发现动态
const addNoteService = (reqParam) => {
    return http.post('xwj-discovery-forum/note/sendNote', reqParam);
}


//添加举报信息
const saveReportNoticeService = (reportData) => {
    return http.post('xwj-discovery-forum/report/reportNote', reportData);

}

//添加评论
const addCommentInforService = (commentData) => {
    return http.post('xwj-discovery-forum/comment/commentNote', commentData, false, "baseUrl", false);
}

//评论回复
const saveReplyInforService = (replyData) => {
    return  http.post('xwj-discovery-forum/comment/replyComment', replyData, false, "baseUrl", false);
}

//删除当前评论
const delCommentInforService = (NOTE_CODE, EVAL_CODE) => {
    return http.post('xwj-discovery-forum/comment/removeComment', {evalCode: EVAL_CODE}, true);
}

//获取当前用户相关的动态提醒数
const getMineMessageCountService = (replyData, isShowLoading=false) => {
    return http.get('xwj-discovery-forum/reminder/getReminderCount', replyData, "baseUrl", isShowLoading);
}

// 获取消息提醒列表概览
const getMessageViewListService = (replyData) => {
    return http.get('xwj-discovery-forum/reminder/getMessageViewList', replyData, "baseUrl", false);
}

// 获取帖子详情
const getNoteDetailService = (replyData) => {
    return http.get('xwj-discovery-forum/note/getNoteDetail', replyData, "baseUrl", false);
}

// 获取我发布的帖子列表
const getOwnerNotesService = (replyData) => {
    return http.get('xwj-discovery-forum/note/getOwnNotes', replyData, "baseUrl", false);
}


export {
    getAllNoteService,
    saveLikeService,
    addNoteService,
    delNoteDataService,
    saveReportNoticeService,
    addCommentInforService,
    saveReplyInforService,
    delCommentInforService,
    getMineMessageCountService,
    getMessageViewListService,
    getNoteDetailService,
    getOwnerNotesService
}
