<template>
  <div class="activityView">
    <van-nav-bar
      id="NaviBar"
      :title="activityName"
      fixed
      left-arrow
      :class="themeCurrent=='1'?'van-nav-bar-l':''"
      @click-left="onClickLeft"
      v-show="show.normalTitle"
    />
    <van-nav-bar
      id="NaviBar_active"
      :style="setTopHeight"
      :title="activityName"
      fixed
      left-arrow
      :class="themeCurrent=='1'?'van-nav-bar-l':''"
      @click-left="onClickLeft"
      v-show="show.activeTitle"
    />
    <div
      style="height:100%;width:100%;overflow:auto;position:relative;"
      ref="activity_scroll"
      @scroll="handleScroll"
    >
      <div class="activity_main">
        <!-- 转盘积分信息 -->
        <div class="points_msg" style="height:80px">
          <p :class="themeCurrent=='1'?'rule-l':'rule'">当前积分</p>
          <p :class="themeCurrent=='1'?'points_total-l':'points_total'">{{pointsTotal}}</p>
          <p :class="themeCurrent=='1'?'rule-l':'rule'">抽奖{{pointConsumed}}积分/次</p>
        </div>
        <div class="active_body">
          <!-- 转盘区域 -->
          <div class="activity_content">
            <div class="active_arrow"></div>
            <div :class="{'active_light1':lightOn,'active_light2':!lightOn}"></div>
            <div class="active_button" @click="startAward"></div>
            <div class="active_rotate_wrapper" :style="rotateDeg">
              <div class="active_pan"></div>
              <div class="active_award_list">
                <div
                  :class="'active_award_item' +' ' + 'circle'+(index+1)"
                  v-for="(item,index) in awardCollection"
                  :key="index"
                >
                  <img
                    :src="item.prizeIconUrl"
                    style="position: relative;top: -3%;transform: rotateZ(180deg)"
                  />
                  <p>{{item.prizeName}}</p>
                </div>
              </div>
            </div>
            <div class="active_gold"></div>
          </div>

          <!-- 底部获奖名单 -->
          <div class="activity_table">
            <div class="winner_top">
              <div :class="themeCurrent=='1'?'winner_tips-l':'winner_tips'" @click="goActivityRule">规则说明</div>
            </div>
            <div class="" :class="themeCurrent=='1'?'winner_record-l':'winner_record'">
              <ul class="winner_board">
                <li
                  v-for="(item,index) in awaradRecord"
                  :key="index"
                  :class="{stickColor :item.isTop==1}"
                >恭喜{{item.areaName}}用户{{item.ownerName}}获得{{item.prizeName}}！</li>
              </ul>
              <div :class="themeCurrent=='1'?'winner_btn-l':'winner_btn'" @click="goAwaradRecord">查看中奖记录</div>
            </div>

          </div>
        </div>
      </div>
    </div>

    <win-prize
      @hide-winning-modal="hideWinningModal"
      @check="check"
      :award-message="awardSussMessage"
      v-if="isShowWinningInfo"
    />

    <no-win-prize
      @hide-no-winning-modal="hideNoWinningModal"
      @reset-award="resetAward"
      v-if="isShowNoWinningInfo"
    />

  </div>
</template>

<script>
import {
  getLotteryWinnersList,
  getPrizesList,
  getActivityInfo,
  getCurrentPoints,
  getLuckyDraw
} from "../../services/MineService";
import WinPrize from "./WinPrize";
import NoWinPrize from "./NoWinPrize";
export default {
  name: "PointActivity",
  components: {
    WinPrize,
    NoWinPrize
  },
  data() {
    return {
      activityName: "",
      lightOn: false,
      pointsTotal: "",
      pointConsumed: "",
      getActivityMsgObj: {},
      awaradRecord: [],
      awaradSource: [],
      awardCollection: [],
      activityId: "1",
      isShowNoWinningInfo: false,
      isShowWinningInfo: false,
      rotateDeg: "",
      awardRunType: false,
      lockStatus: "",
      awardFinshType: true,
      isWinBool: "",
      rotateStopDeg: 0,
      awardSussMessage: "",
      show: {
        normalTitle: true, //透明吸顶
        activeTitle: false //不透明吸顶
      },
      setTopHeight: {
        "padding-top": this.$stbHeight + "px"
      },
      themeCurrent: "",
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back(-1);
    },
    goActivityRule() {
      this.$router.push({
        path: "/activityIntro",
        query: {
          title: "活动规则",
          content: this.getActivityMsgObj.promotionRuleDesc
        }
      });
    },
    goAwaradRecord() {
      this.$router.push("/prizeRecord");
    },

    //  *转盘灯闪烁
    lightChange() {
      window.setInterval(() => {
        this.lightOn = !this.lightOn;
      }, 1000);
    },
    //获取中奖纪录列表数据
    initAwaradRecord() {
      let param = { promotionId: this.activityId };
      getLotteryWinnersList(param)
        .then(data => {
          this.awaradSource = data;
          var strr = 0;
          if (this.awaradSource.length > 3) {
            //大于三条开始滚动
            this.awaradRecord = this.awaradSource.splice(strr, 3);
            this.awaradSource.push(this.awaradRecord[0]);
            this.awaradSource.push(this.awaradRecord[1]);
            this.awaradSource.push(this.awaradRecord[2]);
            this.loopPlayRecord(0);
          } else {
            this.awaradRecord = this.awaradSource;
          }
        })
        .catch(error => {
          // this.$toast(error);
        });
    },
    //转盘上奖品的信息
    initAwardActivityMsg() {
      let param = { promotionId: this.activityId };
      getPrizesList(param)
        .then(data => {
          this.awardCollection = data;
        })
        .catch(error => {
          // this.$toast(error);
        });
    },
    //获取活动信息
    initActivityInfo() {
      let param = { promotionId: this.activityId };
      getActivityInfo(param)
        .then(data => {
          this.getActivityMsgObj = data;
          this.activityName = data.promotionName;
          this.pointConsumed = data.pointConsumed;
        })
        .catch(error => {
          // this.$toast(error);
        });
    },
    //获取积分总数
    initPointsBalance() {
      let param = { ownerCode: this.$userInfo.OWNER_CODE };
      getCurrentPoints(param)
        .then(data => {
          this.lockStatus = data.lockStatus; //账户积分是否锁定 0 正常  1锁定
          this.pointsTotal = data.pointBalance;
        })
        .catch(error => {
          // this.$toast(error);
        });
    },
    //循环推新数据进行展示，
    loopPlayRecord(index) {
      setTimeout(() => {
        var newPlayData = this.awaradSource[index];
        this.awaradRecord[0] = this.awaradRecord[1]; //第一位展示数组地二位数据
        this.awaradRecord[1] = this.awaradRecord[2]; //第二位展示数组第三位数据
        this.awaradRecord[2] = newPlayData; //第三位展示最新数据
        if (index == this.awaradSource.length - 1) {
          index = -1;
        }
        this.loopPlayRecord(index + 1);
      }, 3000);
    },
    // 隐藏中奖信息
    hideWinningModal() {
      this.resetAward1();
      this.isShowWinningInfo = false;
    },
    // 隐藏中奖信息
    hideNoWinningModal() {
      this.resetAward1();
      this.isShowNoWinningInfo = false;
    },
    // 展示中奖信息
    showWinningInfo() {
      this.isShowWinningInfo = true;
    },
    // 展示未中奖信息
    showNoWinningInfo() {
      this.isShowNoWinningInfo = true;
    },
    // 再玩一次
    resetAward() {
      this.resetAward1();
      this.hideNoWinningModal();
      this.startAward();
    },
    startAward() {
      //账户积分是否锁定
      if (this.lockStatus == 1) {
        this.$toast("账户积分已锁定");
        return;
      }
      //转盘正在转动，防止多点转盘按钮
      if (this.awardRunType) {
        return;
      }
      //防止转盘奖项未加载出来
      if (!this.awardCollection || !this.awardCollection.length) {
        this.$toast("转盘奖项初始化失败");
        return;
      }
      if (
        this.pointsTotal < this.getActivityMsgObj.pointConsumed ||
        this.pointsTotal == null ||
        this.pointsTotal == undefined
      ) {
        this.$toast("积分不足");
        return;
      }
      this.awardRunType = true;
      this.setActivityFinishMsg(); //获取中奖奖品
    },
    setActivityFinishMsg() {
      let param = {
        ownerCode: this.$userInfo.OWNER_CODE,
        promotionId: this.activityId,
        buildName: this.$userInfo.BUILD_NAME,
        unitNo: this.$userInfo.UNIT_NO,
        roomName: this.$userInfo.ROOM_NAME,
        phone: this.$userInfo.PHONE,
        areaCode: this.$userInfo.AREA_CODE,
        areaName: this.$userInfo.AREA_NAME,
        ownerName: this.$userInfo.OWNER_NAME
      };
      getLuckyDraw(param)
        .then(data => {
          this.computeGetAwardDeg(data.prizeId);
          this.awardFinshType = true;
          //todo 拼接中奖奖品传给中奖子页面
          if (data.prizeType == 1) {
            this.awardSussMessage =
              data.prizeName +
              "（" +
              data.quantityOfOnePrize +
              data.quantityUnit +
              "），可在我的中奖记录中查看";
          } else if (data.prizeType == 2) {
            this.awardSussMessage = "积分" + data.quantityOfOnePrize;
          }
          this.isWinBool = data.prizeType;
          setTimeout(() => {
            this.awardCallback();
            this.initPointsBalance();
          }, 10000);
        })
        .catch(error => {
          this.$toast(error);
          this.awardRunType = false;
          this.resetAward1();
        });
    },
    //计算中奖商品在转盘上的角度
    computeGetAwardDeg(prizeId, initZero) {
      console.log("prizeId" + prizeId);
      // if (initZero) {
      //   $scope.rotateStopDeg = 0;
      //   return;
      // }
      //防止获取在已经获取到接口
      try {
        var erverAwardDeg = 360 / 8;
        var tArray = this.awardCollection.slice(0).reverse();
        var ratoteNum = 0;
        for (var p = 0; p < tArray.length; p++) {
          if (tArray[p].prizeId === prizeId) {
            this.rotateStopDeg = Math.ceil(
              erverAwardDeg * (p + 1) - erverAwardDeg / 2
            );
            ratoteNum = this.rotateStopDeg + 3600;
            console.log(ratoteNum);
            this.rotateDeg =
              "transform:rotateZ(" + ratoteNum + "deg);transition:all 10s;";
            break;
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
    //转盘转动完毕后的回调
    awardCallback() {
      this.awardRunType = false;
      //todo 弹哪个框 成功弹框或者失败弹框
      if (this.isWinBool == 3) {
        this.showNoWinningInfo();
      } else {
        this.showWinningInfo();
      }
    },

    //重置转盘数据
    resetAward1() {
      this.rotateDeg = "transform:rotateZ(0deg);";
      this.awardRunType = false;
    },
    //去中奖列表页面
    check() {
      this.$router.push("/prizeRecord");
    },
    handleScroll() {
      //监听滚动设置吸顶格式
      const _this = this;
      _this.$nextTick(() => {
        let scrollTop = _this.$refs.activity_scroll.scrollTop;
        console.log("===qiao=== : " + scrollTop);
        if (scrollTop > 30) {
          _this.show.activeTitle = true;
          _this.show.normalTitle = false;
          _this.isShowTopBtn = true;
        } else {
          _this.show.activeTitle = false;
          _this.show.normalTitle = true;
          _this.isShowTopBtn = false;
        }
      });
    }
  },
  mounted() {
    this.themeCurrent = this.$curTheme;
    let query = this.$route.query;
    this.activityId = query.promotionId;
    this.activityName = query.title ? query.title : "";
    this.pointConsumed = query.pointConsumed ? query.pointConsumed : "";
    this.lightChange(); // 转盘灯变换
    this.initAwaradRecord(); //获取中奖纪录列表数据
    this.initAwardActivityMsg(); //转盘上奖品的信息
    this.initPointsBalance(); //获取积分总数
    this.initActivityInfo(); //获取活动信息
    window.addEventListener("scroll", this.handleScroll, true);
  }
};
</script>

