<template>
  <div :style="{'background-color':bgColor,'height':'100%'}" :class="{themeL:bindThemeL}">
    <!--<van-nav-bar id="NaviBar" title="关于我们" fixed left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="关于我们">
    </navigation-bar>
    <div class="topView">
      <div class="content">
        <img src="../../assets/images/personal_center/logo.png"/>
        <span class="textlabel">信我家</span>
        <span class="textlabel">版本号:{{appVersion}}</span>
      </div>
    </div>
    <van-cell class="mycellstyle"
        :class="{mycellThemeL:bindThemeL}"
        title="检查更新" is-link @click="checkNewVersion" v-if="isAndroid" />
    <!-- <div class="space"></div> -->

    <reset-server ref="resetServer"/>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { getAppVersion } from "../../services/MineService";
import config from "../../common/config";
import ResetServer from './ResetServer'
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "AboutUs",
  components: {
    ResetServer,
    NavigationBar
  },
  data() {
    return {
      isAndroid: false,
      appVersion: "",
      clickTimes: 0,
      bgColor:this.$devicePlatform == "android" ?'rgb(249, 249, 249)' :'#FFF',
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  computed: {
    actions() {
      var arr = [];
      for (let server in config.urlMap) {
        console.log(server);
        let nm = config.urlMap[server].envName;
        arr.push({ name: nm, key: server });
      }
      return arr;
    }
  },
  methods: {
    checkNewVersion() {
      /*getAppVersion()
        .then(data => {
          this.paraseData(data);
        })
        .catch(error => {
          console.log(error);
        });*/
      let ota = navigator.ota;
      let obj = {};
      ota.ota(obj,
        (resultStatus) => {
          console.log("Call ota success. ");
        },
        (message) => {
          console.log("Call ota fail. " + message);
          if (message == "没有新版本") {
            this.showLatestDialog(this.appVersion);
          } else {
            Dialog.alert({
              title: message
            }).then(() => {});
          }
        })
    },
    onClickLeft() {
      this.$router.back(-1);
    },
    paraseData(res) {
      if (res.resultCode == "0") {
        //版本不生效
        // if (res.data.versionStatus == 1) {
        //   return;
        // }
        let versionObj = JSON.parse(localStorage.getItem("appVersion"));
        console.log(res.data.appVersion, versionObj.value);
        //对比版本号
        if (res.data.appVersion > versionObj.value) {
          //非最新版本
          if (res.data.updateType == 0) {
            //不支持静默升级
          } else if (updateType == 1) {
            //强制升级
            this.showForceUpdateDialog(res.data);
          } else {
            //普通升级可关闭
            this.showNormalUpdateDialog(res.data);
          }
        } else {
          //是最新版本
          this.showLatestDialog(res.data.appVersion);
        }
      }
    },
    showLatestDialog(version) {
      Dialog.alert({
        title: "已经是最新版本了哦",
        message: "版本号：" + version
      }).then(() => {});
    },
    showForceUpdateDialog(data) {
      Dialog.alert({
        title: "提示",
        message: "您的APP不是最新版本"
      }).then(() => {});
    },
    showNormalUpdateDialog(data) {
      Dialog.confirm({
        title: "提示",
        message: "您的APP不是最新版本",
        confirmButtonText: "去更新",
        confirmButtonColor: "#FB5768"
      })
        .then(() => {
          // on confirm
        })
        .catch(() => {
          // on cancel
        });
    },
    clickMultTimes() {
      if (!(config.version.includes('RC') || config.version.includes('IT') || config.version.includes('DEV'))){
        console.log('appVersion 不包含 RC 或 DEV 或 IT')
        return
      }
      this.clickTimes++;
      if (this.clickTimes <= 11) return;

      this.clickTimes = 0;
      Dialog.confirm({
        title: "是否需要修改网络环境"
      })
        .then(() => {
          this.$refs.resetServer.showServers = true;
        })
        .catch(() => {
          // on cancel
        });
    }
  },
  mounted() {
    this.appVersion = JSON.parse(localStorage.getItem("appVersion")).value;
    this.isAndroid = this.$devicePlatform == "android";
  },
};
</script>

<style scoped>
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
/deep/.van-cell {
  height: 57px;
  line-height: 35px;
}
/deep/.mycellstyle .van-cell__right-icon{
  line-height: 35px;
}
/deep/.mycellThemeL .van-cell__title {
  font-size: 17px !important;
}

.topView {
  width: 100%;
  height: 0;
  padding-top: 66%;
  position: relative;
  margin-bottom: 10px;
  background: #fff;
}

.content {
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  margin-top: -35px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
img {
  width: 70px;
  height: 70px;
  margin-bottom: 10px;
}
.textlabel {
  font-size: 14px;
  color: #bdbdbd;
}
.themeL .textlabel {
  font-size: 17px;
}
.space{
  height: 900px;
}
</style>