
import http from "../http";

//获取品质生活详情信息服务
const getQualityDetailService = (articleNo) => {
    return http.get('xwj-discovery-life/articlePage/getToAppPage', { articleNo });
}

//获取品质生活商品信息服务
const getQualityGoodsService = (areaCode, spuCodeList) => {
    return http.get('xwj-commerce-goods/goods/getToArticle', { areaCode, spuCodeList });
}

//获取品质生活列表数据
const getQualityListService = (page, rows = 10) => {
    // console.log("service")
    return http.get('xwj-discovery-life/qualityLife/getToAppPage', { page, rows: 10 }, 'baseUrl', false);
}

//品质生活文章添加收藏
const saveCollectService = (collectType, contentCode, userCode) => {
    return http.post('xwj-sys-user/collect/saveCollect', { collectType, contentCode, userCode });
}

//获取品质生活文章收藏列表
const getCollectService = (collectType, contentCode, userCode) => {
    return http.get('xwj-sys-user/collect/getCollectList', { collectType, contentCode, userCode });
}


//品质生活文章删除收藏
const deleteCollectService = (collectCodes) => {
    return http.post('xwj-sys-user/collect/deleteCollect', { collectCodes});
}

//品质生活文章分享获取链接
const getShareArticleUrlService = (reqData) => {
    return http.post('xwj-sys-user/share/shareArticle', reqData );
}

const getReadInfoService = (articleNo) => {
    return http.get('xwj-discovery-life/articlePage/addReadTime', { articleNo } );
}





export {
    getQualityDetailService,
    getQualityGoodsService,
    getQualityListService,
    saveCollectService,
    getCollectService,
    deleteCollectService,
    getShareArticleUrlService,
    getReadInfoService
}
