

export default {
    timeFormatting: timeFormatting,
    getCurrentTimeStamp: getCurrentTimeStamp,
    timeFormatting2: timeFormatting2
}

//获取秒级时间戳
function getCurrentTimeStamp() {
    var tmp = Date.parse( new Date() ).toString();
    tmp = tmp.substr(0,10);

    return tmp.valueOf();
    // return Date.parse(new Date());
}

//dateTimeStamp是一个时间毫秒
function timeFormatting(dateTimeStamp) {   
    var minute = 1000 * 60;      //把分，时，天，周，半个月，一个月用毫秒表示
    var hour = minute * 60;
    var day = hour * 24;
    var week = day * 7;
    // var halfamonth = day * 15;
    var month = day * 30;
    var now = new Date().getTime();   //获取当前时间毫秒
    var diffValue = now - dateTimeStamp;//时间差

    if(diffValue < 0) {
        return;
    }

    var minC = diffValue/minute;  //计算时间差的分，时，天，周，月
    var hourC = diffValue/hour;
    var dayC = diffValue/day;
    var weekC = diffValue/week;
    var monthC = diffValue/month;
    let result = '';
    if(monthC >= 1 && monthC <= 3){
        result = " " + parseInt(monthC) + "月前"
    }
    else if(weekC >= 1 && weekC <= 3){
        result = " " + parseInt(weekC) + "周前"
    }
    else if(dayC >= 1 && dayC <= 6){
        result = " " + parseInt(dayC) + "天前"
    }
    else if(hourC >= 1 && hourC <= 23){
        result = " " + parseInt(hourC) + "小时前"
    }
    else if(minC >= 1 && minC <= 59){
        result =" " + parseInt(minC) + "分钟前"
    }
    else if(diffValue >= 0 && diffValue <= minute){
        result = "刚刚"
    }
    else {
        var datetime = new Date();
        datetime.setTime(dateTimeStamp);
        var Nyear = datetime.getFullYear();
        var Nmonth = datetime.getMonth() + 1 < 10 ? "0" + (datetime.getMonth() + 1) : datetime.getMonth() + 1;
        var Ndate = datetime.getDate() < 10 ? "0" + datetime.getDate() : datetime.getDate();
        // var Nhour = datetime.getHours() < 10 ? "0" + datetime.getHours() : datetime.getHours();
        // var Nminute = datetime.getMinutes() < 10 ? "0" + datetime.getMinutes() : datetime.getMinutes();
        // var Nsecond = datetime.getSeconds() < 10 ? "0" + datetime.getSeconds() : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate
    }
    return result;
}

//dateTimeStamp是一个时间毫秒
function timeFormatting2(dateTimeStamp) {   
    var minute = 1000 * 60;      //把分，时，天，周，半个月，一个月用毫秒表示
    var hour = minute * 60;
    var day = hour * 24;
    var week = day * 7;
    // var halfamonth = day * 15;
    var month = day * 30;
    var now = new Date().getTime();   //获取当前时间毫秒
    var diffValue = now - dateTimeStamp;//时间差

    if(diffValue < 0) {
        return;
    }

    var minC = diffValue/minute;  //计算时间差的分，时，天，周，月
    var hourC = diffValue/hour;
    var dayC = diffValue/day;
    var weekC = diffValue/week;
    var monthC = diffValue/month;
    let result = '';
    var datetime = new Date();
    datetime.setTime(dateTimeStamp);
    var Nyear = datetime.getFullYear();
    var Nmonth = datetime.getMonth() + 1 < 10 ? "0" + (datetime.getMonth() + 1) : datetime.getMonth() + 1;
    var Ndate = datetime.getDate() < 10 ? "0" + datetime.getDate() : datetime.getDate();
    var Nhour = datetime.getHours() < 10 ? "0" + datetime.getHours() : datetime.getHours();
    var Nminute = datetime.getMinutes() < 10 ? "0" + datetime.getMinutes() : datetime.getMinutes();
    var Nsecond = datetime.getSeconds() < 10 ? "0" + datetime.getSeconds() : datetime.getSeconds();
    if(monthC >= 1
        || weekC >= 1
        || dayC >= 1){
        result = Nyear + "-" + Nmonth + "-" + Ndate + ' ' + Nhour + ':' + Nminute;
    }
    else if(hourC >= 1
        || minC >= 1 
        || diffValue >= 0){
            result = Nhour + ':' + Nminute;
    }

    
    return result;
}

const emojiMap = {         // 根据[呲牙]可匹配的路径地址
    '[微笑]': 'emoji_0.png',
    '[呲牙]': 'emoji_1.png',
    '[下雨]': 'emoji_2.png'
  }
  
const emojiUrl = 'http://xxxxxxxx/emoji/'   // 为<img src="https://main.qcloudimg.com/raw/6be88c30a4552b5eb93d8eec243b6593.png"  style="margin:0;">图片的地址
  
function parseText (payload) {
    let renderDom = []
    // 文本消息
    let temp = payload.text
    let left = -1
    let right = -1
    while (temp !== '') {
        left = temp.indexOf('[')
        right = temp.indexOf(']')
        switch (left) {
            case 0:
                if (right === -1) {
                    renderDom.push({
                        name: 'text',
                        text: temp
                    })
                    temp = ''
                } else {
                    let _emoji = temp.slice(0, right + 1)
                    if (emojiMap[_emoji]) {    // 如果您需要渲染表情包，需要进行匹配您对应[呲牙]的表情包地址
                    renderDom.push({
                        name: 'img',
                        src: emojiUrl + emojiMap[_emoji]
                    })
                    temp = temp.substring(right + 1)
                    } else {
                    renderDom.push({
                        name: 'text',
                        text: '['
                    })
                    temp = temp.slice(1)
                    }
                }
                break
            case -1:
                renderDom.push({
                    name: 'text',
                    text: temp
                })
                temp = ''
                break
                default:
                renderDom.push({
                    name: 'text',
                    text: temp.slice(0, left)
                })
                temp = temp.substring(left)
                break
        }
    }
    return renderDom
}

// 最后的 renderDom 结构为[{name: 'text', text: 'XXX'}, {name: 'img', src: 'http://xxx'}......]
// 渲染当前数组即可得到想要的 UI 结果，如：XXX<img src="https://main.qcloudimg.com/raw/6be88c30a4552b5eb93d8eec243b6593.png"  
