<template>
  <div style="height:100%;background:#F9F9F9;display: flex;flex-direction: column;" :class="{themeL:bindThemeL}">
    <navigation-bar  @onLeftClick="onClickLeft" pageName="家庭成员" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
        <div class="applyBtn" v-if="isOwner&&list.length>0">
          <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onClickRight">人员申请</div>
          <div class="red-point" v-show="hasUnApprovedMember"></div>
        </div>
      </template>
    </navigation-bar>
    <div class="no-networking" v-show="networkError">
      <img :src="noNetworkImg">
      <h3>网络请求失败</h3>
      <p>请检查您的网络</p>
      <div @click="getRoomAndMemberList">重新加载</div>
    </div>
    <div class="noList" v-if="list.length == 0 && showEmpty" >
      <img src="../../assets/images/default/no_home_img.png" />
      <div>您还没绑定房间信息哦，<br/>快去绑定吧!~</div>
    </div>
    <div style="flex:1;overflow:auto;">
      <van-list style="padding-top:1px;">
        <div v-for="(item,index) in list" :key="index">
          <!--房间信息-->
          <div class="cellItem">
            <div class="leftContent">
              <div style="display: flex;align-items: center;padding-right: 10px;">
                <img
                    src="../../assets/images/personal_center/user_address_icon_location.png"
                />
              </div>
              <div class="textContent">
                <div class="firstLine">
                  <span class="areaName" :class="{areaNameThemeL:bindThemeL}">{{item.areaName}}</span>
                  <div class="ownerLabelBox" v-if="item.relateFlag=='1'">房主</div>
                  <div class="ownerLabelBox memberLabelBox" v-if="item.relateFlag=='0'">成员</div>
                </div>
                <span
                    class="houseNum"
                    :class="{houseNumThemeL:bindThemeL}"
                >{{item.buildName}}{{item.unitNo == 0 ? "" : "-"+item.unitNo}}-{{item.roomName}}</span>
              </div>
            </div>
            <div class="rightContent">
              <van-button
                  class="unBindBtn"
                  :class="{unBindBtnThemeL:bindThemeL}"
                  color='#4A4A4A'
                  round
                  plain
                  @click="unBindRoom(item)"
              >解除绑定</van-button>
              <div class="arrowRect" v-if="!item.isShowMemberList" @click="showMembers(item)">
                <img class="arrowImg" src="../../assets/images/common/icon_arrow_down.png" />
              </div>
              <div class="arrowRect" v-else="item.isShowMemberList" @click="showMembers(item)">
                <img class="arrowImg" src="../../assets/images/common/icon_arrow_up.png" />
              </div>
            </div>
          </div>

          <!--家庭成员信息-->
          <div v-if="item.isShowMemberList">

            <div v-for="(member,memberIndex) in item.currentRoomowners" :key="memberIndex" v-if="member.approvalStatus=='1'&&member.approvalResult=='1'">

              <div class="memberItem">
                <div class="leftContent">
                  <div style="display: flex;align-items: center;padding-right: 10px;" :class="{themeL:bindThemeL}">
                    <img class="photoImg"
                         :src="member.picUrl.length == 0 ? require('../../assets/images/default/user_default_avata.png') : member.picUrl"
                    />
                  </div>
                  <div class="textContent">
                    <div class="firstLine memberLine">
                      <span class="memberName" :class="{areaNameThemeL:bindThemeL}">{{member.ownerNickName}}</span>
                      <span class="ownerLabelBox" v-if="member.relateFlag=='1'">房主</span>
                      <span class="ownerLabelBox memberLabelBox" v-if="member.relateFlag=='0'">成员</span>
                    </div>
                    <span
                        class="phoneNum"
                        :class="{themeL:bindThemeL}"
                    >{{member.phoneMask}}</span>
                  </div>
                </div>

                <div class="rightContent">
                  <van-button
                      v-if="item.relateFlag=='1' && member.relateFlag=='0'"
                      class="unBindBtn memberUnbind"
                      :class="{unBindBtnThemeL:bindThemeL}"
                      color="#666666"
                      round
                      plain
                      @click="removeBindFamily(member, item)"
                  >解除绑定</van-button>

                  <div class="contact">
                    <img
                        class="phoneImg"
                        src="../../assets/images/mall/call_small_red.png"
                    />
                    <span class="btnText" @click="callPhone(member.phone)">联系</span>
                  </div>
                </div>
              </div>
              <!--家庭成员申请加入房间-->
              <!--<div class="apply" v-if="member.approvalStatus == 0">
                <div class="agreeBtn rejectBtn" :class="{btnThemeL:bindThemeL}" @click="rejectClick(member,item)">拒绝</div>
                <div class="agreeBtn" :class="{btnThemeL:bindThemeL}" @click="passClick(member,item)">通过</div>
              </div>-->

              <van-divider style="height: 1px;margin: 0 10px;background:#ECECEC"></van-divider>
            </div>

          </div>

          <van-divider style="height: 1px;margin: 0 10px;background:#ECECEC"></van-divider>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import { getRoomAndMemberList, deleteRoomNew, removeBindFamily, approvalMember } from "../../services/MineService";
  import { Dialog } from "vant";
  import noNetworkImg from '@/assets/images/home/notice/img_net@2x.png'
  import defOwnerImg from '@/assets/images/default/user_default_avata.png'
  import {phoneMask} from "../../utils/common";

  export default {
    name: "MyFamilyNew",
    components: {
      NavigationBar
    },
    data() {
      return {
        headerItemWidth:{
          width:'110px'
        },
        areaCode: "",//传参
        roomCode: "",//传参，用于判断进入时的房间是否被解绑
        showEmpty: false,
        hasUnApprovedMember:false,//是否有待审核的家庭成员
        isOwner:false, //是否房主身份。同一小区下只要有一个房产是房主，就显示人员申请按钮，如果全是成员身份，隐藏人员申请按钮
        list: [],
        networkError: false,
        noNetworkImg: noNetworkImg,
        minePhone: this.$userInfo.PHONE,
        /*大字模式*/
        bindThemeL: this.$curTheme==0?false:true,
        btnStyle: {
          fontSize:this.$curTheme==0?"12px":"15px",
          width:this.$curTheme==0?"90px":"100px"
        }
      }
    },
    mounted() {
      this.areaCode = this.$route.query.areaCode;
      this.roomCode = this.$route.query.roomCode;
      console.log("myFamilyNew: this.areaCode ="+this.areaCode)
      console.log("myFamilyNew: this.roomCode ="+this.roomCode)

      this.getRoomAndMemberList();
    },
    //从家庭成员页返回绑定授权页面时，需要判断是否解绑了进入时的房间
    beforeRouteLeave(to, from, next) {
      //this.roomCode = "" //just for test
      if (to.name == "myHousesBindAuth" && this.roomCode == "") {
        console.log("myFamilyNew: 返回绑定授权页面的房间已经被解绑");
        to.query.roomCode = "";
      }
      console.log("myFamilyNew: to = "+to.name);
      console.log("myFamilyNew: from = "+from.name);
      next();
    },
    methods: {
      onClickLeft() {
        this.$router.back(-1);
      },
      onClickRight() {
        //this.$router.push("/myFamilyApply");
        this.$router.push({
          path: "/myFamilyApply",
          query: {
            areaCode: this.areaCode
          }
        });
      },
      //获取房间列表以及每个房间下的成员列表
      getRoomAndMemberList() {
        let areaCode = this.$userInfo.AREA_CODE;
        if (this.areaCode != undefined && this.areaCode != "") {
          areaCode = this.areaCode;
        }
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          areaCode: areaCode
        };
        getRoomAndMemberList(param)
          .then(result => {
            if (result.resultCode == 0) {
              this.networkError = false;

              //默认收起家庭成员列表
              if (result.data) {
                result.data.forEach((item, index) => {
                  item.isShowMemberList = false;
                  if (item.relateFlag == '1') {
                    this.isOwner = true;
                  }
                  item.currentRoomowners.forEach((item, index) => {
                    item.phoneMask = phoneMask(item.phone);
                    if (item.approvalStatus == '0') {
                      this.hasUnApprovedMember = true;
                    }
                  })
                })
                this.list = result.data;
              }
              this.showEmpty = true;
            } else {
              console.log("【Error】getRoomAndMemberList: "+result.msg);
            }
          })
          .catch(error => {
            console.log(error);
            // Toast(error);
          });
      },
      //解绑房间
      unBindRoom(item) {
        Dialog.confirm({
          title: "确认要解除绑定？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.confirmUnBind(item);
          })
          .catch(() => {
            // on cancel
          });
      },
      confirmUnBind(item) {
        let param = {
          ownerCode: this.$userInfo.OWNER_CODE,
          roomCode: item.roomCode
        };
        deleteRoomNew(param)
          .then(data => {
            if (this.roomCode != undefined && item.roomCode == this.roomCode) {
              //从绑定授权页面传过来的房间已经被解绑，将当前房间code清空
              this.roomCode = "";
            }

            this.list.splice(this.list.indexOf(item), 1);
            //yuanl add for 解绑房间后回首页需要重新刷新小区
            Vue.prototype.$refreshHomePage = true;
          })
          .catch(error => {
            // this.$toast("网络异常，请稍后重试！");
          });
      },
      //点击展开家庭成员列表
      showMembers(item) {
        item.isShowMemberList = !item.isShowMemberList;
      },
      //点击解除绑定
      removeBindFamily(member, item) {
        Dialog.confirm({
          title: "确认删除家庭关系？",
          confirmButtonColor:"#FB5768"
        })
          .then(() => {
            this.confirmRemoveBind(member, item);
          })
          .catch(() => {
            // on cancel
          });
      },
      confirmRemoveBind(member, item) {
        let param = {
          ownerCode: member.ownerCode,
          roomCode: member.roomCode,
          uuid: member.uuid
        };
        removeBindFamily(param)
          .then(data => {
            item.currentRoomowners.splice(item.currentRoomowners.indexOf(member), 1);
            this.$forceUpdate();
          })
          .catch(error => {
            // Toast(error);
          });
      },
      //点击联系
      callPhone(phone){
        var num = phone
        window.location.href = 'tel://' + num
      },
      //拒绝加入房间
      /*rejectClick(member, item) {
        Dialog.confirm({
          title: "确定审核？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.rejectAction(member, item);
          })
          .catch(() => {});
      },
      rejectAction(member, item) {
        let param = {
          OWNER_CODE: this.$userInfo.OWNER_CODE,
          APPROVAL_RESULT: "2",
          UUID: member.uuid
        };
        approvalMember(param)
          .then(data => {
            //重新获取家庭成员列表
            this.getMemberList(this.list[this.list.indexOf(item)]);
          })
          .catch(error => {
            // Toast(error);
          });
      },
      //同意加入房间
      passClick(member, item) {
        Dialog.confirm({
          title: "确定审核？",
          confirmButtonColor: "#FB5768"
        })
          .then(() => {
            this.confirmAction(member, item);
          })
          .catch(() => {});
      },
      confirmAction(member, item) {
        let param = {
          OWNER_CODE: this.$userInfo.OWNER_CODE,
          APPROVAL_RESULT: "1",
          UUID: member.uuid
        };
        approvalMember(param)
          .then(data => {
            //重新获取家庭成员列表
            this.getMemberList(this.list[this.list.indexOf(item)]);
          })
          .catch(error => {
            // Toast(error);
          });
      }*/
    }
  }
</script>

<style scoped lang="scss">
  /*标题栏*/
  .applyBtn {
    position: relative;
    width: 100%;
  }
  .rightBtn {
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
    line-height: 32px;
    padding-right: 10px;
    text-align: right;
  }
  .rightBtnThemeL {
    font-size: 17px !important;
  }

  /*人员申请小红点*/
  .red-point {
    position: absolute;
    background-color: #FC0238;
    z-index: 2;
    top: 5px;
    right: 8px;
    width: 8px;
    height: 8px;
    border-radius: 4px;
  }

  /*只绑定虚拟小区空列表*/
  .noList img {
    margin: 125px 100px 20px 100px;
    width: 180px;
    display: inline-block;
  }
  .noList div {
    font-family: PingFangSC-Regular;
    letter-spacing: 0;
    text-align: center;
    width: 100%;
    font-size: 16px;
    color: #9A9A9A;
    line-height: 25px;
  }

  .cellItem {
    height: 88px;
    background-color: #ffffff;
    margin: 0;
    display: flex;
    padding-right: 12px;
    padding-left: 12px;
    justify-content: space-between;
    align-items: center;
  }

  .leftContent {
    display: flex;
    align-items: center;
  }
  .leftContent img {
    width: 45px;
    height: 45px;
  }
  .themeL .leftContent img {
    width: 50px !important;
    height: 50px !important;
  }

  .textContent {
    display: flex;
    flex-direction: column;
    padding-right: 6px;
  }

  .unBindBtn {
    height: 25px;
    min-width: 70px;
    line-height: 25px;
    font-size: 12px;
    color: #666666;
    letter-spacing: 0;
    padding-left: 0;
    padding-right: 0;
  }
  .unBindBtnThemeL {
    font-size: 14px !important;
    padding-left: 5px !important;
    padding-right: 5px !important;
  }
  .memberUnbind {
    height: 23px;
    min-width: 55px;
    line-height: 23px;
    white-space: nowrap;
  }
  .memberUnbindThemeL {
    font-size: 14px !important;
    min-width: 60px;
  }

  .areaName {
    font-weight: bold;
    font-size: 16px;
    color: #333333;
    letter-spacing: 0;
  }
  .areaNameThemeL {
    font-size: 17px !important;
    line-height: 30px;
  }

  .houseNum {
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: #666666;
    letter-spacing: 0;
  }
  .houseNumThemeL {
    font-size: 15px !important;
    /*line-height: 30px;*/
  }

  /*新增房主、成员标签*/
  .firstLine{
    display: flex;
    flex-wrap: nowrap;
    margin-bottom:4px;
    align-items: center;

    .ownerLabelBox {
      background:  #FFB004;
      border-radius: 3px;
      padding: 0 6px;
      margin-left: 3px;
      white-space: nowrap;
      color: white;
      letter-spacing: 2px;
      font-size: 10px;
      text-align: center;
      height: 16px;
      line-height: 16px;
    }
    .memberLabelBox{
      background:  #B4985C;
    }
  }
  .themeL .firstLine {
    margin-bottom: 0;
  }
  .memberLine {
    align-items: flex-start;
    .ownerLabelBox {
      margin-top: 1px;
    }
  }
  .themeL .memberLine {
    .ownerLabelBox {
      margin-top: 4px;
    }
  }
  .rightContent {
    display: flex;
    align-items: center;
  }
  .arrowRect {
    height: 25px;
    line-height: 25px;
    display: flex;
    align-items: center;
  }
  .arrowImg {
    width: 14px;
    height: 14px;
    line-height: 14px;
    margin-left: 10px;
  }

  /*家庭成员列表*/
  .memberItem {
    /*height: 65px;*/
    background-color: #f9f9f9;
    margin: 0;
    display: flex;
    /*padding-right: 16px;
    padding-left: 16px;*/
    padding: 14px 14px;
    justify-content: space-between;
    align-items: center;
  }
  .themeL .memberItem {
    /*height: 75px;*/
  }
  .photoImg {
    border-radius: 50%;
    width: 35px !important;
    height: 35px !important;
  }
  .themeL .leftContent .photoImg {
    width: 40px !important;
    height: 40px !important;
  }
  .info {
    height: 64px;
    display: flex;
    padding-right: 12px;
    padding-left: 12px;
    justify-content: space-between;
    align-items: center;
  }
  .memberName {
    font-weight: bold;
    font-size: 14px;
    color: #3a3a3a;
    letter-spacing: 0;
    line-height: 16px;
    word-wrap: break-word;
    word-break: break-all;
  }
  .themeL .memberName {
    font-size: 17px;
    /*height: 30px;*/
    line-height: 25px;
  }
  .contact {
    display:flex;
    align-items:center;
    justify-content: center;
    width: 55px;
    height: 21px;
    border: 1px solid #FB5768;
    border-radius: 22px;
    margin-left: 8px;
  }
  .phoneImg {
    width:13px;
    height:12px;
    margin-right: 3px;
  }
  .phoneNum {
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #666666;
    letter-spacing: 0;
  }
  .themeL .phoneNum {
    font-size: 15px;
  }
  .btnText {
    font-size: 12px;
    color: #FB5678;
    letter-spacing: 0;
  }
  .themeL .btnText {
    font-size: 14px;
  }

  /*家庭成员申请加入*/
  .apply {
    display:flex;
    align-items:center;
    justify-content: center;
    margin-bottom: 10px;
  }
  .themeL .apply {
    margin-top: 5px;
    margin-bottom: 15px;
  }
  .agreeBtn {
    background: #fb5768;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: white;
    width: 38%;
    height: 30px;
    line-height: 30px;
    background: #fb5768;
    box-shadow: 0 0 16px 0 rgba(197,58,72,0.39);
    border-radius: 29px;
    margin: 0 15px;
    letter-spacing: 5px;
  }
  .rejectBtn {
    background: #ffffff;
    box-shadow: none;
    border: 1px solid #666666;
    color: #666666;
  }
  .btnThemeL {
    font-size: 16px !important;
  }

  /*无网络样式*/
  .no-networking {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 90px;
    font-family: PingFangSC-Regular;
    text-align: center;
    img {
      width: 66px;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 16px;
      color: #888888;
      margin: 10px 0px;
    }
    p {
      font-size: 14px;
      color: #C4C4C4;
      margin: 0 0 3px;
    }
    div,
    div:active {
      background-color: #ffffff;
      color: rgb(251, 87, 104);
      position: relative;
      margin: 10px;
      border-radius: 20px;
      border: 1px solid rgb(251, 87, 104);
      font-size: 16px;
      height: 40px;
      line-height: 40px;
      width: 125px;
    }
    div:active{
      opacity: 0.5;
    }
  }
</style>
