<template>
  <div id="app">
    <div class="app-body">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>

    <Tabbar v-show="tabBarShow"></Tabbar>
    <div class="placehold-footview" v-show="tabBarShow"></div>
    <div class="loading-view" v-show="loadingViewShow">
      <van-loading color="#fb5768" />
    </div>
  </div>
</template>

<script>
  import Vue from "vue";
  import { store, storage } from "./utils/storage";
  import Config from "./common/config";
  import Tabbar from "./page/Home/TabBar";
  import constUtils from "./common/constUtils";

  import { Loading, Dialog, Toast } from 'vant';
  Vue.use(Loading, Dialog, Toast);

  export default {
    name: "app",
    components: {
      Tabbar
    },
    data() {
      return {
        tabBarShow: true,
        loadingViewShow: false,
        backButtonPressed: false//标记是否按下了物理返回键
      };
    },
    watch: {
      $route(to, from) {
        console.log("【页面路由】curTheme: "+Vue.prototype.$curTheme+", to.path: " + to.path+", from.path: "+from.path);
        Vue.prototype.$fromPage = from.path;
        //判断是否显示tabbar
        if (to.path == "/homePage" ||
          to.path == "/homePageThemeL" ||
          to.path == "/productPage" ||
          to.path == "/productPageThemeL" ||
          to.path == "/findPage" ||
          to.path == "/minePage" ||
          to.path == "/minePageThemeL") {
          this.tabBarShow = true;
        } else {
          this.tabBarShow = false;
        }

        //更新状态栏
        this.$updateStb(to);
      },
    },
    computed: {
      total() {
        return this.$store.state.login.count;
      },
      shopFlag() {
        return this.$store.state.productPage.shopFlag;
      },
      flag() {

        return this.$store.state.productPage.flag;
      },
      setSpuCode(){
        return this.$store.state.productPage.setSpuCode
      },
      setQualityCode(){
        return this.$store.state.productPage.setQualityCode
      },
      setQualityCode(){
        return this.$store.state.productPage.setQualityCode
      },
      setFlashSaleSpuCode(){
        return this.$store.state.productPage.setFlashSaleSpuCode
      }
    },
    mounted() {
      this.addEventListener();
      window.logOut = this.logOutAction;

      // console.log(this.$store.getters.cbd);
      // this.$store.dispatch("increment");
      // return;
      //展示引导页
      let showBootPage = storage.getStorage('showBootPage');
      let appVersion = storage.getStorage('appVersion');
      if (showBootPage.$s_code_$exp
        || showBootPage
        || appVersion != Config.version) {
        this.$router.push("/bootPage");
        return;
      }

      //不展示引导页，判断登录信息
      if (this.$isLogin) {
        if (this.$userInfo.AREA_CODE && this.$userInfo.AREA_CODE.length > 0) {
          if (this.$curTheme == "0") {
            this.$router.push("/homePage");
          } else {
            this.$router.push("/homePageThemeL");
          }
        }
        else {
          // 跳转到房间绑定页面
          this.$router.push('/selectArea');
        }
      } else {
        this.$router.push("/login");
      }

      /*Vue.filter('keepTwoNum', function (value) {
        value = Number(value);
        return value.toFixed(2);
      });*/ //移到main.js，否则部分页面不生效

      store.set('goLoginRouter',()=>{
      this.clearTags();
      this.deleteAlias();
      console.log(this.$userInfo);
        storage.deleteStorage('userInfo');
      this.$router.replace("login");
    })
      this.checkNetPermission();
      window.showUnNetPrivacy = this.showNoNetPrivacyAlert;
    },
    created() {
      console.log(this.$router)
      console.log("window")
      console.log(window.location)
      console.log(window.location.href)

      //初始化平台信息
      this.initPlatformInfo();
      //如果不为空，清空本地图片文件夹
      if(this.isDeleteLocalImgFile!=null){
        console.log("app.vue"+this.isDeleteLocalImgFile)
        Vue.prototype.$deleteLacalImg(this.isDeleteLocalImgFile)
      }

      //监听deviceready
      document.addEventListener("deviceready", this.onDeviceReady, false);

      if (window.cordova && window.cordova.plugins && window.cordova.plugins.Keyboard) {
        cordova.plugins.Keyboard.hideKeyboardAccessoryBar(true);
        cordova.plugins.Keyboard.disableScroll(true);
      }
      //设置加载view显示回调
      store.set('showLoadingView', (isShow) => {
        this.loadingViewShow = isShow;
      });

      // 获取登录信息
      let userInfo = storage.getStorage("userInfo");
      if (!userInfo.$s_code_$exp) {
        Vue.prototype.$setUserInfo(userInfo, false);
      }

      let custEnv = "";
      //没有值
      if (storage.getStorage("custUrl").$s_code_$exp) {
        custEnv = Config.urlMap[Config.defaultEnv];
        console.log(custEnv)
      } else {
        custEnv = storage.getStorage("custUrl");
      }
      storage.setStorage("custUrl", custEnv)
      store.set("custUrl", custEnv);

      //获取当前主题
      Vue.prototype.$curTheme = localStorage.getItem("curTheme") ? localStorage.getItem("curTheme") : "0";
      console.log("当前主题："+Vue.prototype.$curTheme)
    },

    updated() {
      if (this.$route.path == '/shopPage'||this.$route.path == '/shopPageThemeL') {
        if (this.$stbHeight > 0) {
          //if (cordova.platformId == 'android') {
          if (this.$devicePlatform == 'android' || this.$devicePlatform == 'pc') {
            //给所有标题栏自动增加顶部padding,留出状态栏的高度
            let el = document.querySelector(".van-nav-bar");
            if (el) {
              el.style.paddingTop = this.$stbHeight + 'px';
              console.log("Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop);
            }
          } else {
            //ios在css样式中写死了20px
          }
        } else {
          //清除ios中写死的20px
          let el = document.querySelector(".van-nav-bar");
          if (el) {
            el.style.paddingTop = 0;
            console.log("Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop);
          }
        }
        return;
      }
      this.$nextTick(()=>{
        if (this.$stbHeight > 0) {
          //if (cordova.platformId == 'android') {
          if (this.$devicePlatform == 'android' || this.$devicePlatform == 'pc') {
            //给所有标题栏自动增加顶部padding,留出状态栏的高度
            let el = document.querySelector(".van-nav-bar");
            if (el) {
              el.style.paddingTop = this.$stbHeight + 'px';
              console.log("Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop);
            }
          } else {
            //ios在css样式中写死了20px
          }
        } else {
          //清除ios中写死的20px
          let el = document.querySelector(".van-nav-bar");
          if (el) {
            el.style.paddingTop = 0;
            console.log("Set [.van-nav-bar].paddingTop -> " + el.style.paddingTop);
          }
        }
      })
    },

    methods: {
      logOutAction() {
        this.clearTags();
        this.deleteAlias();
        console.log(this.$userInfo);
        storage.deleteStorage("userInfo");
        this.$router.replace("login");
      }, //
      //  添加事件监听
      addEventListener: function() {
        window.addEventListener("native.keyboardshow",e =>{
          Vue.prototype.$isShowKeyboard = true;
        });
        window.addEventListener("native.keyboardhide",e => {
          setTimeout(() => {
            Vue.prototype.$isShowKeyboard  = false;
          }, 200);
        });
      },

      //微信跳转回调
      onOpenWXShare: function (data) {
        //如果是已登录状态，直接跳转到相应页面,如果未登录，记录参数信息
        console.log("onOpenWXShare = ");
        if (this.$isLogin)
        {
          if (data.type == "product") {
            //商品详情
            if (this.$route.path == '/goodsDetail'||this.$route.path == '/goodsDetailThemeL') {
              //如果当前是详情页，更新数据
              this.$store.commit("setSpuCodeChange", {
                spuCode: data.params.spuCode
              });
            }else{
              //跳转详情页，根据打字模式判断
              if (this.$curTheme==0) {
                this.$router.push({ name: "goodsDetail", params: { spuCode: data.params.spuCode } });
              } else {
                this.$router.push({ name: "goodsDetailThemeL", params: { spuCode: data.params.spuCode } });
              }
            }
          }
          if (data.type == "qualityLife") {
            //文章页
            if (this.$route.path == '/qualityDetail') {
              //如果当前是文章页，更新数据
              this.$store.commit("setQualityCodeChange", {
                qualityCode: data.params.articleNo
              });
            }else{
              //跳转文章页，根据打字模式判断
              this.$router.push({name: 'qualityDetail',params: {articleNo:data.params.articleNo, articleImg: 'share'}});

            }

          }
          if (data.type == "activityTemplate") {

            if (this.$route.path == '/activityTemplate') {
              //如果当前是详情页，更新数据
              this.$store.commit("setActivityCodeChange", {
                activityCode: data.params.templateId
              });
            }else{
              //跳转详情页，根据打字模式判断
              this.$router.push({ name: "activityTemplate", params: { templateId: data.params.templateId } });
            }
          }
          if (data.type == "flashSaleGoods") {
            if (this.$route.path == '/flashSaleGoods'||this.$route.path == '/flashSaleGoodsThemeL') {
              //如果当前是秒杀详情页，更新数据
              this.$store.commit("setFlashSaleSpuCodeChange", {
                flashSaleSpuCode: data.params.activitySpuId + "," + data.params.spuCode
              });
            }else{
              //跳转秒杀详情页，根据打字模式判断
              if (this.$curTheme==0) {
                this.$router.push({name: 'flashSaleGoods',query: {spuCode: data.params.spuCode,activitySpuId: data.params.activitySpuId}});
              } else {
                this.$router.push({name: 'flashSaleGoodsThemeL',query: {spuCode: data.params.spuCode,activitySpuId: data.params.activitySpuId}});
              }
            }
          }
          if (data.type == "myfamily") {
            if (data.params.type == 1) {
              //家庭成员
              this.$router.push("/myFamilyNew")
            } else if (data.params.type == 2) {
              //问卷列表
              this.$router.push({
                name: "questionnaireList"
              });
            }
          }
        }
        else
        {
          storage.setStorage("WXShareData", data, 999);
          console.log("WXShareData = ");
        }
      },

      //应用开启时收到推送
      receiveNotification(event){
          let object = event.extras;
          console.log("极光推送消息回调：flag = " + object.messageBehavior);
          //收到绑定通知时，弹窗
          if ("110001" == object.messageBehavior) {
            Dialog.confirm({
              message: object.message
            }).then(() => {
                // 确定
                this.$router.push("/homePage");
            }).catch(() => {
                // on cancel
           });
         }
      },

      //极光推送消息回调
      openNotification(event) {
        try {

          let curUrl = window.location.href;
          let arrUrl = curUrl.split("#");
          let pageUrl = arrUrl[1];
          if(pageUrl == "/"||pageUrl == ""){
            //如果是通过推送打开应用，先跳转首页或者选择地址页
            let that = this;
            setTimeout(() => {
              that.onOpenNotification(event);
            }, 200);
            if (this.$userInfo.AREA_CODE && this.$userInfo.AREA_CODE.length > 0) {
              this.$router.push("/homePage");
            }
            else {
              // 跳转到房间绑定页面
              this.$router.push('/selectArea');
            }
          }else{
            this.onOpenNotification(event);
          }




        } catch (exception) {
          console.log("JPushPlugin:onOpenNotification" + exception);
        }
      },

      //极光推送跳转
      onOpenNotification(event){
        let alertContent = event.alert;
          let object = event.extras;
          console.log("极光推送消息回调：flag = " + object.flag);

          //跳转到工单详情
          //跳转到报修详情
          //跳转到投诉
          if ("0016" == object.flag || "0008" == object.flag || "0009" == object.flag) {
            this.$router.push({
              name: 'complaintReportingDetail',
              query: {
                complateCode: object.bizCode,
                typeFlag: 0,
              }
            })
          }
          //跳转到发现
          else if ("0014" == object.flag) {
            this.$router.push("/findPage");
          }
          //跳转到待缴账单
          else if ("0010" == object.flag) {
            this.$router.push("/propertyBillList");
          }
          //跳转到物业通知详情
          else if ("22" == object.flag) {
            let paramsObj = {
              UUID: object.uuid,
              NOTICE_CODE: object.noticeCode,
              DEPT_CODE: this.$userInfo.DEPT_CODE,
              ROOM_CODE: this.$userInfo.ROOM_CODE,
              OWNER_CODE: this.$userInfo.OWNER_CODE,
              AREA_CODE: this.$userInfo.AREA_CODE
            };
            this.$router.push({
              name: "noticeDetail",
              params: { noticeReq: paramsObj }
            });
          }
          //跳转到家庭成员页
          else if ("25" == object.flag) {
            //this.$router.push("/myFamily");
            this.$router.push("/myFamilyNew");
          }
          //点击微信分享跳转页面
          else if ("00001" == object.flag) {
            this.onOpenWXShare(object);
          }
          //不支持
          else {
            console.log("JPushPlugin:onOpenNotification：暂不支持该类型跳转。[ " + object.flag + " ]");
          }
      },

      //初始化平台信息
      initPlatformInfo() {
        if (navigator.platform == "Win32" || navigator.platform == "MacIntel") {
          Vue.prototype.$devicePlatform = "pc";
        } else {
          if (/(Android)/i.test(navigator.userAgent)) {
            //android
            Vue.prototype.$devicePlatform = "android";
          } else {
            Vue.prototype.$devicePlatform = "ios";
          }
        }
        console.log("当前平台：" + Vue.prototype.$devicePlatform);
      },

      //初始化status bar
      initStatusBar() {
        // 设置设备的状态栏高度
        if (this.$devicePlatform == 'android') {
          //Vue.prototype.$stbHeight = 24;

          let ota = navigator.ota;
          ota.getStbHeight({}, function(res){
            console.log("initStatusBar.getStbHeight: "+res);
            Vue.prototype.$stbHeight = res;
          },function(err){
            console.log("initStatusBar.getStbHeight: error.");
          });

          //恢复android 主应用背景
          ota.resetAppBg({}, function(){
            console.log("initStatusBar.resetAppBg: success.");
          },function(err){
            console.log("initStatusBar.resetAppBg: error.");
          });
        }
        else if (this.$devicePlatform == 'ios') {
          if (constUtils.isFullScreen) {
            Vue.prototype.$stbHeight = 44;
            Vue.prototype.$footBarHeight = 34;
          }
          else {
            Vue.prototype.$stbHeight = 20;
            Vue.prototype.$footBarHeight = 0;
          }
        }

        console.log("initStatusBar: 当前手机状态栏高度 -> " + this.$stbHeight);
        console.log("initStatusBar: 当前手机底部状态栏高度 -> " + this.$footBarHeight);

        if (this.$stbHeight > 0) {
          if (cordova.platformId == 'android') { // 安卓需要特殊处理, 否则背景透明不起作用
            console.log("initStatusBar: 设置状态栏背景透明.");
            StatusBar.overlaysWebView(true);
          }

          // 根据路由动态改变状态栏样式
          // 所有状态栏字体色为白色的路由都设置了一个路由元meta,属性statusBgc为1
          this.$route.meta.statusBgc ? StatusBar.styleLightContent() : StatusBar.styleDefault()
        }
      },

      //平台准备好回调
      onDeviceReady() {
        console.log("onDeviceReady: 注册物理返回键监听.");
        //监听backbutton
        document.addEventListener("backbutton", this.eventBackButton, false);

        this.$plugin.usePlugin("jPush")(() => {
          console.log("极光推送插件初始化.");
        }, (event) => {
          //极光推送消息回调
          this.openNotification(event);
        }, (event) => {
          //极光推送消息回调
          this.receiveNotification(event);
        });

        //初始化status bar
        this.initStatusBar();
      },

      //处理物理返回键
      eventBackButton() {
        let curUrl = window.location.href;
        let arrUrl = curUrl.split("#");
        let pageUrl = arrUrl[1];
        console.log("按下物理返回键：当前路由 " + pageUrl + '来自于页面：' + this.$fromPage);

        //判断当前弹起键盘，返回键收键盘
        if (this.$isShowKeyboard) {
          cordova.$plugin.Keyboard.close();
          return;
        }

        // 关闭图片预览，TODO:关闭弹窗等
        if (this.$store.state.preview.previewId && this.$store.state.preview.previewId.setIsShowPreview) {
          this.$store.state.preview.previewId.setIsShowPreview(false);
          this.$store.dispatch('setPreviewId', '');
          return;
        }

        // 发现页面关闭键盘，用不到
        // if (this.$store.state.find.keyboardId) {
        //   this.$store.state.find.keyboardId.blur();
        //   this.$store.dispatch('setKeyboardId', '');
        //   return;
        // }

        //判断当前页面是否有弹窗，有则通知页面关闭弹窗
        if (this.$store.state.home.hasPopWin) {
          console.log("当前页面有弹出框，关闭弹出框!");
          this.$store.dispatch('setClosePopWinFlag', true);
          return;
        }

        // 判断当前页面是否有van-dialog类型的弹出框
        // 1.关闭操作只适用于在代码中调用Dialog.confirm创建的弹出框
        // 2.用v-model控制的弹出框可以使用this.$store.state.home.hasPopWin机制，控制关闭
        let el = document.querySelector(".van-dialog");
        if (el) {
          console.log("当前页面是否有Dialog弹出框："+el.style.display);
          console.log(el);
          console.log(el.firstChild.innerHTML);
          // console.log(el.message);
          if (el.style.display != 'none') {
            if (pageUrl == '/orderPayNew'||(pageUrl == '/setAddress'&&el.firstChild.innerHTML!="确定要删除地址吗？")||pageUrl == '/propertyPreOrderPay'||pageUrl == '/propertyPreOrderPayDetail'||pageUrl == '/propertyOrderPay'||pageUrl == '/propertyOrderPayDetail') {
              //点击物理返回键不关闭支付页弹窗，也不返回。不关闭收货地址页的返回二次确认

            }else{
              Dialog.close();
            }
            return;


          }
        }

        //判断当前发现页面是否有举报弹窗，有则通知页面关闭弹窗
        if (this.$store.state.find.hasPopWin) {
          console.log("发现页面有弹出框，关闭弹出框!");
          this.$store.dispatch('setFindClosePopWinFlag', true);
          return;
        }

       //发送物理返回键监听
        if (pageUrl == '/orderPayNew') {
          console.log("触发监听物理返回键");
          this.$store.dispatch('setEventBackButtonFlag', true);
          return;
        }
        //发送物理返回键监听
        if (pageUrl == '/paySuccess') {
          console.log("触发监听物理返回键");
          this.$store.dispatch('setPaysuccessEventBackButtonFlag', true);
          return;
        }
        //发送物理返回键监听
        if (pageUrl == '/orderDetails') {
          console.log("触发监听物理返回键");
          this.$store.dispatch('setOrderDetailEventBackButtonFlag', true);
          return;
          }
        //订单列表物理返回按键
        if(pageUrl=='/myOrderList'){
          this.$store.dispatch('setOrderListEventBackButtonFlag', true);
          return;
        }
        //商品详情页面物理返回按键
        if(pageUrl=='/goodsDetail'){
          this.$store.dispatch('setGoodsDetailEventBackButtonFlag', true);
          return;
        }
        //秒杀详情页面物理返回按键
         if(pageUrl=='/flashSaleGoods'){
          this.$store.dispatch('setFlashSaleEventBackButtonFlag', true);
          return;
        }
        //商品评价列表页面物理返回按键
        if(pageUrl=='/evaluateList'){
          this.$store.dispatch('setEvaluateListEventBackButtonFlag', true);
          return;
        }
        //商品评价列表页面物理返回按键
        if(pageUrl=='/myEvaluate'){
          this.$store.dispatch('setMyEvaluateEventBackButtonFlag', true);
          return;
        }

        if (pageUrl == '/setAddress') {
          this.$store.dispatch('setAddressEventBackButtonFlag', true);
          return;
        }

        if (pageUrl == '/productPage' || pageUrl == '/productPageThemeL'
          || pageUrl == '/findPage'
          || pageUrl == '/minePage'  || pageUrl == '/minePageThemeL') {
          //判断当前路由是否为各个导航栏的首页，是的话则返回主页
          console.log("==========> 跳转到首页");
          this.$router.push('/homePage');

        } else if (pageUrl == '/homePage' || pageUrl == '/homePageThemeL' || pageUrl == '/login' || pageUrl == '/bootPage') {
          //判断当前路由为首页、登录页、引导页，则退出APP
          console.log("==========> 退出APP");
          if (!this.backButtonPressed) {
            this.$toast("再次返回退出信我家");
            this.backButtonPressed = true;
            setTimeout(() => {
              console.log("超过 2s 恢复返回键按下状态。")
              this.backButtonPressed = false;
            }, 2000);
          } else {
            navigator.app.exitApp();
          }

        } else if ((pageUrl == '/shopPage' || pageUrl == '/shopPageThemeL')) {
          this.$store.dispatch('setShopPageEventBackButtonFlag', true);
          // this.$router.push('/productPage');
        } else if(pageUrl == '/propertyPreOrderPay'){
            this.$router.go(-2);

        } else if(pageUrl == '/propertyOrderList'){
          //点击返回键通知
          if(!this.$store.state.property.orderListPopupFlag){
            this.$store.dispatch('setOrderListPopupFlag', true);
            return;
          }
        }else if(pageUrl == '/qualityDetail'){
          //点击返回键通知
          if(!this.$store.state.quality.qualityDetalPopupFlag){
            this.$store.dispatch('setQualityDetalPopupFlag', true);
            return;
          }
        } else if (pageUrl === '/selectArea'){
          if (this.$fromPage === '/') {
            navigator.app.exitApp();
          }
          else {
            this.$router.back(-1);
          }
        }
        else if (pageUrl === '/roomApply'){
          Vue.prototype.$refreshHomePage = true;
          this.$router.push({ path: '/homePage' });
        }
        else if (pageUrl === '/userSetting') {
          if (this.$curTheme==0) {
            this.$router.replace("/minePage");
          } else {
            this.$router.replace("/minePageThemeL");
          }
        }
        else {
          console.log("==========> 返回前一页");
          if(pageUrl == '/shopPage'){
            Dialog.close();
          }
          this.$router.back(-1);
        }
      },
      //移除极光推送 别名
    deleteAlias() {
      this.$plugin.usePlugin("deleteAlias")(
        Number(this.random()), //sequence
        () => {
          //success callback
          console.log("极光推送: 删除别名成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：删除别名失败.");
        }
      );
    },
    //移除极光推送 标签
    clearTags() {
      this.$plugin.usePlugin("cleanTags")(
        Number(this.random()), //sequence
        () => {
          //success callback
          storage.setStorage("jpushTags", null, 60);
          console.log("极光推送：移除标签成功.");
        },
        () => {
          //errorCallback
          console.log("极光推送：移除标签失败.");
        }
      );
    },
    //生成随机数
    random: function() {
      var myDate = new Date(); //获取系统当前时间
      var year = myDate.getFullYear().toString();
      var month = myDate.getMonth().toString();
      var date = myDate.getDate().toString();
      var hours = myDate.getHours().toString();
      var minutes = myDate.getMinutes().toString();
      var seconds = myDate.getSeconds().toString();
      var randomNumber =
        year +
        month +
        date +
        hours +
        minutes +
        seconds +
        Math.floor(Math.random() * 1000).toString();
      return randomNumber;
    },
    checkNetPermission(){
      if (this.$devicePlatform != 'ios') {
        return;
      }
      var plugin = cordova.plugins.iOSPermission;
      plugin.checkNetPermission(
        res => {
          window.showNetDialog = res == 0;
          this.checkNetPermission()
        },
        err => {}
      );
    },
    showNoNetPrivacyAlert(){
      if (this.$devicePlatform != 'ios') {
        return;
      }
      console.log("进入插件了;;;")
      var plugin = cordova.plugins.iOSPermission;
      plugin.showNoNetPrivacyAlert()
    }
    }
  };
</script>

