<template>
  <div style="height:100%;display:flex;flex-direction:column;background-color: #F9F9F9" :class="{themeL:bindThemeL}">
    <!--<van-nav-bar id="NaviBar" title="中奖记录" left-arrow @click-left="onClickLeft" />-->
    <navigation-bar  @onLeftClick="onClickLeft" pageName="中奖记录" >
    </navigation-bar>
    <van-pull-refresh  style="flex:1;overflow:auto;" v-model="headerLoading" @refresh="onRefresh">
      <div :style="padingtopNum" v-if="showEmptyView && list.length == 0">
        <van-image
          style="width:40%;"
          fit="contain"
          :src="require('../../assets/images/home/prize/no_record.png')"
        />
        <div style="padding:30px">
          <div style="text-align:center;background:#FFF;color:#999999;font-size:12px" :style="{fontSize:defSize}">暂无中奖记录</div>
          <div style="text-align:center;background:#FFF;color:#999999;font-size:12px" :style="{fontSize:defSize}">攒攒人品，再接再厉</div>
        </div>
      </div>
      <van-list :style="{'padding-top': '5px', 'min-height':list.length == 0 ? '0px' : (listMinHeight+'px')}" v-model="footerLoading" :finished="finished" @load="onLoad">
        <div class="cellItem" v-for="(item,index) in list" :key="index" @click="pushToRules(item)">
          <img class="itemImg" :src="item.prizePicURL" />
          <div class="content">
            <div class="innerContent">
              <span class="title">{{item.prizeName}}</span>
              <span class="detail">{{item.promotionName}}</span>
            </div>
            <span class="date">{{item.timeDraw +'-'+item.timeCashLimit}}</span>
          </div>
          <div class="mark">
            <img
              class="markImg"
              :src="(item.isCashed == 1 || item.isEnd == 1 )? require('../../assets/images/home/prize/tag_honored.png') : require('../../assets/images/home/prize/tag_noaward.png') "
            />
            <span class="markText">{{item.isCashed == 0 ? "未兑奖" : "已兑奖"}}</span>
          </div>
          <div class="btnView" :class="{isUsed: item.isEnd==1 || item.isCashed == 1}">兑奖方式</div>
          <img
            class="expiremark"
            v-if="item.isEnd == 1"
            src="../../assets/images/home/prize/print_expired.png"
          />
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getRewardList } from "../../services/MineService";
import { Toast } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "PrizeRecord",
  components:{
    NavigationBar
  },
  data() {
    return {
      showEmptyView:false,
      statusBarH:this.$stbHeight + 44,
      listMinHeight: document.body.clientHeight - this.$stbHeight -44,
      list: [],
      footerLoading: false,
      headerLoading: false,
      finished: false,
      currentPage: 1,
      pageSize: 10,
      padingtopNum: {
        "padding-top": this.$stbHeight + 44 + "px",
        background: "#FFF",
        'height':document.body.clientHeight - this.$stbHeight -44 +"px",
        "text-align": "center"
      },
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true,
      defSize: this.$curTheme==0?"12px":"15px"
    };
  },

  methods: {
    onLoad() {
      if (this.headerLoading) return;
      this.currentPage = Math.ceil(this.list.length / this.pageSize) + 1;
      this.initRewardList();
    },

    onRefresh() {
      if (this.footerLoading) return;
      this.currentPage = 1;
      this.initRewardList();
    },

    initRewardList() {
      let param = {
        ownerId: this.$userInfo.OWNER_CODE,
        page: this.currentPage,
        rows: this.pageSize
      };
      console.log(this.currentPage);
      getRewardList(param)
        .then(data => {
          if (this.currentPage == 1) {
            this.list = [];
          }
          console.log(data.list);
          this.finished = data.list.length < this.pageSize ? true : false;
          this.headerLoading = false;
          this.footerLoading = false;
          this.list.push(...data.list);
          this.showEmptyView = true;
        })
        .catch(error => {
          // Toast(error);
          this.showEmptyView = true;
          this.footerLoading = false;
          this.headerLoading = false;
          this.finished = true;
        });
    },

    onClickLeft() {
      this.$router.back(-1);
    },
    pushToRules(item) {
      if (item.isCashed == 0 && item.isEnd == 0) {
        this.$router.push({
          name: "activityIntro",
          query: { promotionId: item.promotion_id }
        });
      }
    }
  },
  mounted() {
    this.initRewardList();
  }
};
</script>

<style scoped lang="scss">
  .themeL{
    .markText {
      color: #fff;
      position: absolute;
      font-size: 14px;
      height: 19px;
      width: 42px;
      left: 10px;
      line-height: 20px;
      white-space: nowrap;
    }
    .title {
      font-size: 18px;
      color: #3a3a3a;
      font-weight: bold;
      margin-top: 0;
    }
    .detail {
      font-size: 15px;
      color: #9a9a9a;
      letter-spacing: 0.09px;
      line-height: 22px;
    }
    .date {
      font-size: 13px;
      color: #9a9a9a;
      margin-bottom: 5px;
    }
  }
/deep/.van-nav-bar .van-icon {
  color: #333333;
}
.cellItem {
  position: relative;
  background-image: url("../../assets/images/home/prize/coupon_list_bg.png");
  background-size: 100%;
  background-position: center;
  height: 110px;
  margin: 10px;
  display: flex;
  align-items: center;
  padding-left: 25px;
  padding-right: 23px;
}
.content {
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.itemImg {
  max-width: 64px;
  max-height: 64px;
  /* width: 35px; */
  /* height: 70px; */
  margin-right: 10px;
}
.title {
  max-width: 150px;
  font-size: 16px;
  color: #3a3a3a;
  font-weight: bold;
  margin-top: 5px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.detail {
  max-width: 150px;
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: 0.09px;
  line-height: 22px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.innerContent {
  display: flex;
  flex-direction: column;
}
.date {
  font-size: 12px;
  color: #9a9a9a;
  margin-bottom: 5px;
}
.btnView {
  position: absolute;
  right: 20px;
  height: 23px;
  width: 72px;
  background: #ce9d6a;
  border: 1px solid #cf9b64;
  border-radius: 2px;
  color: #fff;
  font-size: 14px;
  /*text-align: center;*/
  /*line-height: 23px;*/
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.expiremark {
  width: 70px;
  height: 70px;
  position: absolute;
  top: 2px;
  right: 10px;
}
.mark {
  position: absolute;
  top: -1px;
  left: 7.5%;
  text-align: center;
  width: 55px;
  height: 20px;
}
.markImg {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 55px;
  height: 20px;
}
.markText {
  color: #fff;
  position: absolute;
  font-size: 10px;
  height: 19px;
  width: 42px;
  left: 10px;
  line-height: 20px;
}
.isUsed {
  background: #8f8f8f;
  border: 1px solid #8f8f8f;
}
</style>
