<template>
  <div class="setTheme">
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" >
    </navigation-bar>
    <div class="defaultBox" :class="{defaultBoxThemeL:bindThemeL}" @click="setTheme">
      <div class="defaultBtn" :class="{defaultBtnThemeL:bindThemeL}">标准模式</div>
      <van-switch v-model="normalChecked" active-color="#FB5768"  inactive-color="#D1D1D1" :size="normalSize" @click.stop="setTheme"/>
    </div>
    <van-divider :style="{ borderColor: '#e3e3e3', padding: '0 16px',margin:'0' }"/>
    <div class="defaultBox" :class="{defaultBoxThemeL:bindThemeL}" @click="setTheme">
      <div class="defaultBtn" :class="{defaultBtnThemeL:bindThemeL}">大字模式</div>
      <van-switch v-model="largeChecked" active-color="#FB5768"  inactive-color="#D1D1D1" :size="largeSize" @click.stop="setTheme"/>
    </div>
  </div>
</template>

<script>
  import Vue from "vue";
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  export default {
    name: "SetTheme",
    components:{
      NavigationBar
    },
    data() {
      return {
        title:'字号大小设置',
        curTheme:this.$curTheme,
        largeChecked: this.$curTheme=="0" ? false : true,
        normalChecked: this.$curTheme=="0" ? true : false,
        largeValue:"24px",
        normalValue:"18px",
        largeSize:this.$curTheme=="0" ? "18px":"24px",
        normalSize:this.$curTheme=="0" ? "18px":"24px",
        /*大字模式*/
        bindThemeL: this.$curTheme==0?false:true
      };
    },
    watch: {
      largeChecked(val) {
        //this.largeSize = val ? this.largeValue : this.normalValue;
        //this.normalSize = val ? this.normalValue : this.largeValue;
      }
    },
    methods: {
      onClickLeft() {
        this.$router.back(-1);
      },
      setTheme() {
        this.largeChecked = this.largeChecked ? false : true;
        this.normalChecked = this.normalChecked ? false : true;
        this.curTheme = this.normalChecked ? "0" : "1";
        localStorage.setItem("curTheme", this.curTheme)

        Vue.prototype.$curTheme = this.curTheme;

        if (this.curTheme == "0") {
          this.$toast("已设置为标准模式");
          this.bindThemeL = false;
        } else {
          this.$toast("已设置为大字模式");
          this.bindThemeL = true;
        }
        this.largeSize = this.curTheme=="0" ? "18px":"24px";
        this.normalSize = this.largeSize;
      },
    }
  }
</script>

<style scoped>
  .setTheme {
    height: 100%;
    background: #f9f9f9;
  }

  .setTheme .defaultBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background: #fff;
    height: 24px;
  }

  .setTheme .defaultBoxThemeL {
    height: 45px;
  }

  .setTheme .defaultBtn {
    font-family: PingFangSC-Medium;
    font-size: 15px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 22px;
  }

  .setTheme .defaultBtnThemeL {
    font-size: 18px;
    height: 45px;
    line-height: 45px;
  }

  .setTheme .ios-checkbox {
    height: 30px;
    width: 48px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: space-around;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .emulate-ios-button {
    display: block;
    width: 100%;
    height: 100%;
    background: #ccc;
    border-radius: 30px;
    cursor: pointer;
    position: relative;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
  }



  .emulate-ios-button:after {
    content: '';
    display: block;
    width: 26px;
    height: 26px;
    border-radius: 100%;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .1);
    position: absolute;
    left: 3px;
    top: 2px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
  }

  .emulate-ios-button:active:after {
    width: 1.1rem;
  }


  .setTheme .raw-checkbox {
    height: 2rem;
    width: 2rem;
    display: none;
  }

  .setTheme .raw-checkbox:checked+label {
    background: #FB5768;
  }

  .setTheme .raw-checkbox:checked+label:after {
    left: 20px;
  }

  .setTheme .raw-checkbox:checked+label:active:after {
    left: .5rem;
  }


  .setTheme .raw-checkbox:disabled+label {
    background: #d5d5d5;
    pointer-events: none;
  }

  .setTheme .raw-checkbox:disabled+label:after {
    background: #bcbdbc;
  }
</style>